//react
import {Link} from "react-router-dom";

//css
import styled from "styled-components"

//img
import IcoSoldOut from "../../../img/ico_sold_out.png"

//page
import appInfoController from '../../../server/appInfoController'

//숫자 3자리수 콤마 코드
function numberWithCommas(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

export default function MarketProduct({value}) {
    
    return (
        <Container>
            {
                value.prd_soldout != 0 ?
                <Product>
                    <Link to={`/shop/product/${value.prd_type}/${value.prd_id}`}>
                        <ProductImgWrap>
                            <ProductImg src={appInfoController.ImagePath + value.prd_img} alt="Product img"/>
                        </ProductImgWrap>
                        <ProductTextWrap>
                            <ProductNameWrap>
                                <ProductName>{value.prd_title}</ProductName>
                            </ProductNameWrap>
                            <>
                                <ProductText>{value.prd_detail1}</ProductText>
                                <Point>적립금 <PointText>{numberWithCommas(value.prd_earn_price ? value.prd_earn_price : "0")}</PointText>원</Point>
                                {
                                    value.prd_sale_rate > 0 ? 
                                    <DiscountRate><Discount>Discount</Discount>{value.prd_sale_rate}%</DiscountRate>
                                    :
                                    null
                                }
                            </>
                            <>
                                {
                                    value.prd_sale_rate > 0 ?
                                    <Price>{numberWithCommas(value.prd_price)}원</Price>
                                    :
                                    null
                                }
                                <TotalPrice><TotalPriceText>{numberWithCommas(Math.round(value.prd_price - value.prd_price*value.prd_sale_rate/100))}</TotalPriceText>원</TotalPrice>
                            </>
                        </ProductTextWrap>
                    </Link>
                </Product>
            :
                <Product>
                    <Link to={`/shop/product/${value.prd_type}/${value.prd_id}`}>
                        <ProductImgWrap>
                            <ProductImg src={value.prd_id} alt="Product img"/>
                            <SoldOutImgWrap>
                                <SoldOutImgText>품절</SoldOutImgText>
                            </SoldOutImgWrap>
                        </ProductImgWrap>
                        <ProductTextWrap>
                            <ProductNameWrap>
                                <ProductName style={{color: "#7D7D7D"}}>{value.prd_title}</ProductName>
                            </ProductNameWrap>
                            <SoldOutTextWrap>
                                <ProductText style={{color: "#7D7D7D"}}>{value.prd_detail1}</ProductText>
                                <SoldOutText><IconSoldOut src={IcoSoldOut}/> 품절</SoldOutText>
                            </SoldOutTextWrap>
                            <SoldOutTextWrap>
                                <SoldOutPoint style={{color: "#7D7D7D"}}>적립금 <PointText style={{color: "#7D7D7D"}}>{numberWithCommas(value.prd_earn_price ? value.prd_earn_price : "0")}</PointText>원</SoldOutPoint>
                                <TotalPrice style={{color: "#7D7D7D"}}><TotalPriceText style={{color: "#7D7D7D"}}>{numberWithCommas(Math.round(value.prd_price - value.prd_price*value.prd_sale_rate/100))}</TotalPriceText>원</TotalPrice>
                            </SoldOutTextWrap>
                        </ProductTextWrap>
                    </Link>
                </Product>
            }
     </Container>
  );
}

const Container = styled.div`
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    width: calc((100%/2) - calc(100vw*(10/414)));
    margin: calc(100vw*(12/414)) calc(100vw*(5/414));
    border: calc(100vw*(2/414)) solid #e4e4e4;
    border-radius: calc(100vw*(13/414));
    overflow: hidden;
`
const Product = styled.div`
    width: 100%;
`
const ProductImgWrap = styled.div`
    position: relative;
    width: 100%;
`
const ProductImg = styled.img`
    display: block;
    width: calc(100vw*(188/414)); height: calc(100vw*(188/414));
    object-fit: cover;
    margin: 0 auto;
`
const ProductTextWrap = styled.div`
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-end;
    width: calc(100vw*(180/414));
    margin: 0 auto;
    padding: calc(100vw*(8/414)) calc(100vw*(10/414)) calc(100vw*(15/414));
    border-top: calc(100vw*(1/414)) solid #e4e4e4;
`
const ProductNameWrap = styled.p`
    width: 100%;
`
const ProductName = styled.p`
    width: 100%;
    font-size: calc(100vw*(15/414));
    margin-bottom: calc(100vw*(3/414));
`
const ProductText = styled.p`
    width: 100%;
    font-size: calc(100vw*(12/414));
`
const Discount = styled.span`
    font-size: calc(100vw*(11/414));
    color: #e20000;
    font-weight: normal;
    margin-right: calc(100vw*(3/414));
`
const DiscountRate = styled.span`
    display: block;
    width: 100%;
    font-size: calc(100vw*(15/414));
    color: #e20000;
    font-weight: 900;
`
const Price = styled.span`
    display: block;
    font-size: calc(100vw*(13/414));
    color: #707070;
    text-align: right;
    padding-right: calc(100vw*(8/414));
`
const TotalPrice = styled.span`
    display: flex;
    align-items: center;
    font-size: calc(100vw*(15/414));
    margin-top: calc(100vw*(6/414));
`
const TotalPriceText = styled.span`
    font-size: calc(100vw*(20/414));
    font-weight: 900;
`
const Point = styled.p`
    font-size: calc(100vw*(11/414));
    color: #292929;
    font-weight: bold;
    margin-top: calc(100vw*(8/414));
`
const PointText = styled.span`
    color: #292929;
    font-weight: 900;
`
/* Sold out */
const SoldOutText = styled.p`
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    font-size: calc(100vw*(14/414));
    font-weight: bold;
    text-align: right;
    margin-top: calc(100vw*(5/414));
`
const IconSoldOut = styled.img`
    width: calc(100vw*(14/414));
    margin-right: calc(100vw*(5/414));
`
const SoldOutTextWrap = styled.div`
    width: 100%;
    display: flex;
    justify-content: space-between;
`
const SoldOutPoint = styled.p`
    font-size: calc(100vw*(11/414));
    color: #292929;
    font-weight: bold;
    margin-top: calc(100vw*(12/414));
`
const SoldOutImgWrap = styled.div`
    position: absolute; top: 0; left: 0;
    width: 100%; height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(1,1,1,0.5);
`
const SoldOutImgText = styled.p`
    font-size: calc(100vw*(22/414));
    color: #fff;
    font-weight: bold;
`