//react
import React ,{useState,useEffect} from 'react';
import {Link} from "react-router-dom";

//css
import styled from "styled-components"

//img
import mainLogo from "../../img/ico_main.svg"
import iconSearch from "../../img/ico_search.png"
import iconMypage from "../../img/ico_mypage.png"
import iconAlarm from "../../img/ico_alarm.png"


export default function MainTitleBar() {
    const [searchText ,setSearchText] = useState("");

    const searchProduct = () =>{ 
        if(checksearchTextdate()){
            alert("검색" + searchText);
        }
        else
            alert("검색어를 두글자 이상 입력해주세요.");
     }
    const checksearchTextdate = () =>{ return searchText.length > 2; };

    return (
        <Container>
            <MainTitleBarWrap>
                <TitleBar>
                    <Link to={"/"}>
                        <>
                            <LogoLink>
                                <img src={mainLogo} alt="main logo"/>
                            </LogoLink>
                        </>
                    </Link>
                    <SearchWrap>
                        <SearchInput type="search" value={searchText} onChange={(e)=>{setSearchText(e.target.value)}}/>
                        <SearchBtn onClick={searchProduct}></SearchBtn>
                    </SearchWrap>
                    <TitleIconWarp>
                        <Link to={"/mypage"}>
                            <>
                                <MypageImg>
                                    <img src={iconMypage} alt="mypage"/>
                                </MypageImg>
                            </>
                        </Link>
                        <Link to={"/alarm"}>
                            <>
                                <AlarmImg>
                                    <img src={iconAlarm} alt="alarm"/>
                                    <AlarmCount>99+</AlarmCount>
                                </AlarmImg>
                            </>
                        </Link>
                    </TitleIconWarp>
                </TitleBar>
            </MainTitleBarWrap>
        </Container>
    );
}

//css
const Container = styled.div`
    width: 100%; height: calc(100vw*(45/414));
`
const MainTitleBarWrap = styled.div`
    position: fixed; top: 0;
    width: 100%;
    background-color: #fff;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    z-index: 100;
`
const TitleBar = styled.div`
    display: flex; align-items: center; justify-content: center;
    width: 100%;
    padding: calc(100vw*(7/414)) 0;
`
const LogoLink = styled.a`
    display: block;
    width: calc(100vw*(30/414)); height: calc(100vw*(30/414));
    margin-right: calc(100vw*(12/414));
`
const SearchWrap = styled.div`
    position: relative;
    width: calc(100vw*(252/414)); height: calc(100vw*(31/414));
    margin-right: calc(100vw*(19/414));
`
const SearchInput = styled.input`
    width: 100%; height: calc(100vw*(31/414));
    border: calc(100vw*(1/414)) solid #000;
    border-radius: calc(100vw*(17/414));
    font-size: calc(100vw*(13/414));
    text-indent: calc(100vw*(15/414));
`
const SearchBtn = styled.button`
    position: absolute; top: calc(100vw*(8/414)); right: calc(100vw*(15/414));
    width: calc(100vw*(15/414)); height: calc(100vw*(15/414));
    background: url(${iconSearch}) no-repeat center/calc(100vw*(15/414));
`
const TitleIconWarp = styled.div`
    display: flex; align-items: center;
`
const MypageImg = styled.p`
    display: flex; align-items: center;
    width: calc(100vw*(22/414));
    margin-right: calc(100vw*(10/414));
`
const AlarmImg = styled.p`
    position: relative;
    display: flex; align-items: center;
    width: calc(100vw*(19/414));
`
const AlarmCount = styled.span`
    position: absolute; top: 0; left: calc(100vw*(12/414));
    display: block;
    padding: calc(100vw*(1/414)) calc(100vw*(3/414));
    background: #ED2939;
    color: #fff;
    border-radius: calc(100vw*(6/414));
    font-size: calc(100vw*(7/414));
`