//css
import styled from "styled-components"


const GuideInfoList = [
    {
        shopping_method: "신선/냉장/냉동",
        shopping_price1:"무료배송",
        shopping_price2:"-15,000원 이상 구매 시 무료배송",
        shopping_price3:"-해당 서비스 가능 지역에 한함",
        bundle_shipping:"가능",
        delivery_period:"-서비스 이용 지역별 상의(단, 주문시간별 도착 예정 시간은 지역 및 상황에 따라 변동될수있어, 정확한 정보는 결제 완료시 안내되는 문구를 꼭 확인 부탁드립니다.)",
        return_cost:"상품에 따라 다름",
        return_method:"주문 교환/환불/취소는 전화 문의 후 마켓에서 상태 설정",
    }
]

export default function ShopProductReview() {

    return (
        <Container>
            <ShopLine></ShopLine>

            <GuideWrap>
                <ReviewInnerWrap>
                    {
                        GuideInfoList.map((value)=>{
                            
                            return (
                                <div>
                                    <GuideboxWrap>
                                        <GuideTitleWrap>
                                            <GuideTitle>배송정보</GuideTitle>
                                        </GuideTitleWrap>
                                        <>
                                            <table>
                                                <tr>
                                                    <TableTitle>배송방법</TableTitle>
                                                    <TableTextWrap>
                                                        <TableText>{value.shopping_method}</TableText>
                                                    </TableTextWrap>
                                                </tr>
                                                <tr>
                                                    <TableTitle>배송비</TableTitle>
                                                    <TableTextWrapA>
                                                        <TableText>{value.shopping_price1}</TableText>
                                                        <TableText>{value.shopping_price2}</TableText>
                                                        <TableText>{value.shopping_price3}</TableText>
                                                    </TableTextWrapA>
                                                </tr>
                                                <tr>
                                                    <TableTitle>묶음배송 여부</TableTitle>
                                                    <TableTextWrap>
                                                        <TableText>{value.bundle_shipping}</TableText>
                                                    </TableTextWrap>
                                                </tr>
                                                <tr>
                                                    <TableTitle style={{borderBottom: "calc(100vw*(1/414)) solid #e4e4e4"}}>배송기간</TableTitle>
                                                    <TableTextWrap style={{borderBottom: "calc(100vw*(1/414)) solid #e4e4e4"}}>
                                                        <TableText>{value.delivery_period}</TableText>
                                                    </TableTextWrap>
                                                </tr>
                                            </table>
                                        </>
                                    </GuideboxWrap>
                                    <GuideboxWrap>
                                        <GuideTitleWrap>
                                            <GuideTitle>교환/반품 안내</GuideTitle>
                                        </GuideTitleWrap>
                                        <>
                                            <table>
                                                <tr>
                                                    <TableTitle>교환/반품 비용</TableTitle>
                                                    <TableTextWrap>
                                                        <TableText>{value.return_cost}</TableText>
                                                    </TableTextWrap>
                                                </tr>
                                                <tr>
                                                    <TableTitle style={{borderBottom: "calc(100vw*(1/414)) solid #e4e4e4"}}>교환/ 반품 방법</TableTitle>
                                                    <TableTextWrapA style={{borderBottom: "calc(100vw*(1/414)) solid #e4e4e4"}}>
                                                        <TableText>{value.return_method}</TableText>
                                                    </TableTextWrapA>
                                                </tr>
                                            </table>
                                        </>
                                    </GuideboxWrap>
                                </div>
                            )
                        })
                    }
                    <GuideboxWrap>
                        <GuideTitleWrap>
                            <GuideTitle>교환/반품 제한사항</GuideTitle>
                        </GuideTitleWrap>
                        <GuideDetailWrap>
                            <TableText>- 주문/제작 상품의 경우, 상품의 제작이 이미 진행된 경우</TableText>
                            <TableText>- 상품 포장을 개봉하여 사용 또는 설치 완료되어 상품의 가치가 훼
                            손된 경우 (단, 내용 확인을 위한 포장 개봉의 경우는 예외)</TableText>
                            <TableText>- 고객의 사용, 시간경과, 일부 소비에 의하여 상품의 가치가 현저히
                            감소한 경우</TableText>
                            <TableText>- 세트상품 일부 사용, 구성품을 분실하였거나 취급 부주의로 인한 
                            파손/고장/오염으로 재판매 불가한 경우</TableText>
                            <TableText>- 모니터 해상도의 차이로 인해 색상이나 이미지가 실제와 달라,
                            고객이 단순 변심으로 교환/반품을 무료로 요청하는 경우</TableText>
                            <TableText>- 제조사의 사정 (신모델 출시 등) 및 부품 가격 변동 등에 의해 무료
                            교환/반품으로 요청하는 경우</TableText>
                        </GuideDetailWrap>
                    </GuideboxWrap>
                </ReviewInnerWrap>
            </GuideWrap>
        </Container>
    );
}


const Container = styled.div`
    width: 100%;
`
const GuideWrap = styled.div`
    width: 100%;
    background: #fff;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0,0,0,0.16);
    padding-bottom: calc(100vw*(18/414));
`
const ShopLine = styled.div`
    width: 100%;
    height: calc(100vw*(14/414));
    background: #f7f7f7;
`
const ReviewInnerWrap = styled.div`
    width: 100%;
`
const GuideboxWrap = styled.div`
    width: 100%;
    padding: calc(100vw*(40/414)) calc(100vw*(28/414)) calc(100vw*(8/414));
`
const GuideTitleWrap = styled.div`
    margin-bottom: calc(100vw*(20/414));
`
const GuideTitle = styled.h3`
    font-size: calc(100vw*(13/414));
`
const TableTitle = styled.th`
    width: calc(100vw*(127/414));
    background: #f7f7f7;
    font-size: calc(100vw*(13/414));
    font-weight: 900;
    text-align: center;
    padding: calc(100vw*(12/414)) 0;
    vertical-align: middle;
    border-top: calc(100vw*(1/414)) solid #e4e4e4;
    border-left: calc(100vw*(1/414)) solid #e4e4e4;
`
const TableTextWrap = styled.td`
    width: calc(100vw*(230/414));
    padding: calc(100vw*(12/414)) 0;
    vertical-align: middle;
    border: calc(100vw*(1/414)) solid #e4e4e4;
    border-bottom: none;
    font-size: calc(100vw*(13/414));
    padding: calc(100vw*(12/414)) calc(100vw*(12/414)) calc(100vw*(12/414)) calc(100vw*(11/414));
`
const TableText = styled.p`
    font-size: calc(100vw*(13/414));
    line-height: 1.5;
`
const TableTextWrapA = styled(TableTextWrap)`
    padding: calc(100vw*(30/414)) calc(100vw*(10/414)) calc(100vw*(30/414)) calc(100vw*(11/414));

`
const GuideDetailWrap = styled.div`
    margin-bottom: calc(100vw*(35/414));
`
