//react
import {Link} from "react-router-dom";
import React ,{useState,useEffect} from 'react';

//css
import styled from "styled-components"

//img
import IconReadMore from "../../../img/ico_read_more.png"

//page
import serverController from '../../../server/serverController';

//숫자 3자리수 콤마 코드
function numberWithCommas(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

export default function ShopProductInquiry({prd_id,shopOption}) {
    const [qnaList,setQnaList] = useState([]);

    useEffect(()=>{
        serverController.connectFetchController(`qna/selectPrd?prd_id=${prd_id}`,"GET",null,function(res){
        if(res.result == 1) {
            setQnaList(res.list)
        }});
    },[]);

    if(shopOption == false)
        return null;
    return (
        <Container>
            <ShopLine></ShopLine>

            <Link to={`/product/inquiryList/${prd_id}`}>
                <ReviewTitleWrap>
                    <ReviewTitle>상품문의 ({numberWithCommas(qnaList.length)})</ReviewTitle>
                        <IcoReadMore src={IconReadMore} alt="상품평 모두보기"/>
                </ReviewTitleWrap>
            </Link>
        </Container>
    );
}


const Container = styled.div`
    width: 100%;
    background: #fff;
`
const ShopLine = styled.div`
    width: 100%;
    height: calc(100vw*(10/414));
    background: #f7f7f7;
`
const ReviewTitleWrap = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: calc(100vw*(30/414)) calc(100vw*(25/414));
`
const ReviewTitle = styled.p`
    font-size: calc(100vw*(18/414));
    font-weight: bold;
`
const IcoReadMore = styled.img`
    width: calc(100vw*(8/414));
    margin-left: calc(100vw*(9/414));
`