//react
import {Link} from "react-router-dom";
import {useState, useEffect} from 'react';

//css
import styled from "styled-components"

//page
import serverController from '../../../server/serverController';

export default function ShopPayInfo() {
    const [myInfo,setmyInfo] = useState([]);
    
    useEffect(()=>{
        serverController.connectFetchController(`user/mypage`,"GET",null,function(res){
        if(res.result == 1) {
            setmyInfo(res.list[0])
        }});
    },[]);

    return (
        <Container>
            <ShopPayInfoWrap>
                <ShopPayInfoTitleWrap>
                    <ShopPayInfoTitle>구매자 정보</ShopPayInfoTitle>
                </ShopPayInfoTitleWrap>
                <ShopPayInfoInner>
                    <ShopPayInfoBox>
                        <ShopPayInfoBoxTitle>이름</ShopPayInfoBoxTitle>
                        <ShopPayInfoBoxText>{myInfo.mem_username}</ShopPayInfoBoxText>
                    </ShopPayInfoBox>
                    <ShopPayInfoBox>
                        <ShopPayInfoBoxTitle>이메일</ShopPayInfoBoxTitle>
                        <ShopPayInfoBoxText>{myInfo.mem_email}</ShopPayInfoBoxText>
                    </ShopPayInfoBox>
                    <ShopPayInfoBox>
                        <ShopPayInfoBoxTitle>연락처</ShopPayInfoBoxTitle>
                        <PayInfoNumberWrap>
                            <PayInfoNumber type="number"/>
                            <PayInfoNumberBtn>확인</PayInfoNumberBtn>
                            <ErrorTextWrap>
                                <ErrorText>번호를 다시 한번 확인해주세요.</ErrorText>
                            </ErrorTextWrap>
                        </PayInfoNumberWrap>
                    </ShopPayInfoBox>
                </ShopPayInfoInner>
            </ShopPayInfoWrap>
        </Container>
    );
}

//css
const Container = styled.div` 
    position: relative;
    width: 100%;
    background: #fff;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    z-index: 10;
    margin-top: calc(100vw*(12/414));
`
const ShopPayInfoWrap = styled.div`
    width: 100%;
    padding-top: calc(100vw*(20/414));
    padding: calc(100vw*(24/414)) calc(100vw*(36/414)) calc(100vw*(22/414));
`
const ShopPayInfoTitleWrap = styled.div`
    width: 100%;
`
const ShopPayInfoTitle = styled.p`
    font-size: calc(100vw*(18/414));
    font-weight: bold;
    margin-bottom: calc(100vw*(5/414));
`
const ShopPayInfoInner = styled.div`
    width: 100%;
`
const ShopPayInfoBox = styled.div`
    display: flex;
    align-items: baseline;
    padding: calc(100vw*(12/414)) 0;
`
const ShopPayInfoBoxTitle = styled.p`
    width: calc(100vw*(68/414));
    font-size: calc(100vw*(13/414));
    font-weight: bold;
`
const ShopPayInfoBoxText = styled.p`
    width: calc(100% - calc(100vw*(68/414)));
    font-size: calc(100vw*(13/414));
    font-weight: bold;
`
const PayInfoNumberWrap = styled.div`
    width: calc(100% - calc(100vw*(68/414)));
    display: flex;
    align-items: center;
    flex-wrap: wrap;
`
const PayInfoNumber = styled.input`
    width: calc(100vw*(142/414)); height: calc(100vw*(27/414));
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    border-radius: calc(100vw*(3/414));
    border: calc(100vw*(1/414)) solid #979797;
    text-indent: calc(100vw*(8/414));
`
const PayInfoNumberBtn = styled.button`
    height: calc(100vw*(27/414));
    padding: 0 calc(100vw*(14/414));
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    color: #979797;
    border-radius: calc(100vw*(3/414));
    border: calc(100vw*(1/414)) solid #979797;
    margin-left: calc(100vw*(7/414));
`

const ErrorTextWrap = styled.div`
    min-height: calc(100vw*(17/414));
    padding-top: calc(100vw*(5/414));
`
const ErrorText = styled.p`
    font-size: calc(100vw*(9/414));
    font-weight: bold;
    color: #e30000;
`