//react
import React ,{useState,useEffect} from 'react';

//css
import styled from "styled-components"

//img
import IconReadMore from "../../../img/ico_read_more.png"

//page
import serverController from '../../../server/serverController';

//숫자 3자리수 콤마 코드
function numberWithCommas(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

export default function ShopProductReview({prd_id,rev_id}) {
    const [prdImg,setPrdImg] = useState([]);

    useEffect(()=>{
        serverController.connectFetchController(`products/detail?prd_id=${prd_id}`,"GET",null,function(res){
        if(res.result == 1) {
            setPrdImg(res.list[0].prd_img);
        }});
    },[]);

    return (
        <Container>
            <ShopLine></ShopLine>

            <PrdImgBox></PrdImgBox>
        </Container>
    );
}


const Container = styled.div`
    width: 100%;
`
const ShopLine = styled.div`
    width: 100%;
    height: calc(100vw*(10/414));
    background: #f7f7f7;
`
const PrdImgBox = styled.div`
    width: 100%;
    background: #fff;
    padding: calc(100vw*(100/414)) 0;
`