//react
import React ,{useState,useEffect} from 'react';

import MainTitleBar from '../../../component/main/MainTitleBar';
import ShopProductInfo from '../../../component/shop/product/ShopProductInfo';
import ShopProductImg from '../../../component/shop/product/ShopProductImg';
import ShopProductReview from '../../../component/shop/product/ShopProductReview';
import ShopProductInquiry from '../../../component/shop/product/ShopProductInquiry';
import ShopProductGuide from '../../../component/shop/product/ShopProductGuide';
import MainRecentPurchases from '../../../component/main/MainRecentPurchases';
import ShopProductButton from '../../../component/shop/product/ShopProductButton';
import FooterPadding from '../../../component/common/FooterPadding';
import FooterMenu from '../../../component/common/FooterMenu';
import CartModal from '../../../component/common/modal/CartModal';
import PurchaseModal from '../../../component/shop/product/PurchaseModal';
import CouponModal from '../../../component/shop/product/CouponModal';

export default function ShopProductPage({match}) {
  const [couponModal,setCouponModal] = useState(false);
  const [purchaseModal,setPurchaseModal] = useState(false);
  const [cartModal,setCartModal] = useState(false);
  const [shopOption,setShopOption] = useState(false);
  const [optionList,setOptionList] = useState([]);
  const [selectList,setSelectList] = useState({});
  const [product,setProduct] = useState({prd_additional5:"",prd_additional4:""});
  const [optionText,setOptionText] = useState("");

  const openPurchaseModal = (onOff) =>{ setPurchaseModal(onOff);}
  const openCouponModal = (onOff) =>{ setCouponModal(onOff);}

  useEffect(()=>{
    if(product.prd_id && optionList.length == 0){
      const data =  {
          "1617853119962": {
              "prd_id": product.prd_id,
              "prd_img": product.prd_img,
              "prd_price": product.prd_price,
              "prd_sale_rate": product.prd_sale_rate,
              "prd_title": product.prd_title,
              "count": 1,
              "options": {
                  "optionList": {},
                  "optId": [],
                  "product": {
                      "prd_id": product.prd_id,
                      "prd_sale_rate": product.prd_sale_rate,
                      "prd_price":product.prd_price,
                  },
                  "isOptional": false,
                  "isSetOptional": false
              }
          }
      }
      setSelectList(data);
    }
    else
      setSelectList({});
  },[optionList]);

  return ( 
    <>
      <MainTitleBar/>
      <ShopProductInfo
        prd_id={match.params.prd_id} 
        prd_type={match.params.prd_type} 
        openCouponModal={openCouponModal} 
        shopOption={shopOption}
        setShopOption={setShopOption}
        optionList={optionList}
        setOptionList={setOptionList}
        selectList={selectList}
        setSelectList={setSelectList}
        product={product}
        setProduct={setProduct}
        setOptionText={setOptionText}
        setCartModal={setCartModal}
      />
      <ShopProductImg prd_id={match.params.prd_id}/>
      <ShopProductReview prd_id={match.params.prd_id} rev_id={match.params.rev_id} shopOption={shopOption}/>
      <ShopProductInquiry prd_id={match.params.prd_id} shopOption={shopOption}/>
      <ShopProductGuide prd_id={match.params.prd_id}/>
      <MainRecentPurchases rank={true} prd_id={match.params.prd_id}/>
      <ShopProductButton openPurchaseModal={openPurchaseModal}/>
      <FooterPadding option={{backgroundColor:"#f7f7f7"}}/>
      <FooterMenu/>
      <PurchaseModal
        selectList={selectList}
        setSelectList={setSelectList}
        product={product}
        purchaseModal={purchaseModal}
        openPurchaseModal={openPurchaseModal}
        prd_id={match.params.prd_id}
        prd_type={match.params.prd_type}
        shopOption={shopOption}
      />
      <CartModal cartModal={cartModal} setCartModal={setCartModal} setPurchaseModal={setPurchaseModal}/>
      <CouponModal couponModal={couponModal} openCouponModal={openCouponModal}/>
    </>
  );
}
