import {useState} from "react";

//css
import styled from "styled-components"

//page
import TermListLayout from "../../shop/pay/TermListLayout"
const TermList = [
    {
        term_title: "서비스 이용 약관 동의",
        term_text: "서비스 이용 약관 동의내용이 들어가는 공간입니다.",
    },
    {
        term_title: "개인정보 이용 약관 동의",
        term_text: "서비스 이용 약관 동의내용이 들어가는 공간입니다.",
    },
    {
        term_title: "개인정보 이용 약관 동의",
        term_text: "서비스 이용 약관 동의내용이 들어가는 공간입니다.",
    },
    {
        term_title: "TFC페이 이용 약관 동의",
        term_text: "서비스 이용 약관 동의내용이 들어가는 공간입니다.",
    }
]

export default function ShopTermModal({openTermModal,TermModal}) {

    if(TermModal == false)
        return null;

    return (
        <Container>
            <TermModalWrap>
                <TermModalTitleWrap>
                    <TermModalTitle>구매조건 확인 및 결제대행 서비스 약관 동의</TermModalTitle>
                </TermModalTitleWrap>

                {
                    TermList.map((value) => {
                        return(
                            <TermListLayout value={value}/>
                        )
                    })
                }
                <TermModalChkBtnWrap>
                    <TermModalChkBtn onClick={()=>{openTermModal(false)}}>확인</TermModalChkBtn>
                </TermModalChkBtnWrap>
            </TermModalWrap>
        </Container>
    );
}


const Container = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed; bottom: 0;
    width: 100%; height: 100vh;
    z-index: 500;
    background: rgba(1,1,1,0.4);
`
const TermModalWrap = styled.div`
    width: calc(100vw*(389/414)); max-height: calc(100vh - calc(100vw*(50/414)));
    background: #fff;
    padding: calc(100vw*(25/414)) calc(100vw*(35/414)) calc(100vw*(35/414));
    border-radius: 15px;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    overflow-y: scroll;
`
const TermModalTitleWrap = styled.div`
    padding-bottom: calc(100vw*(30/414));
`
const TermModalTitle = styled.p`
    font-size: calc(100vw*(17/414));
    font-weight: bold;
    text-align: center;
`
const TermModalChkBtnWrap = styled.div`
    text-align: center;
    padding-top: calc(100vw*(35/414));
`
const TermModalChkBtn = styled.button`
    font-size: calc(100vw*(17/414));
    font-weight: bold;
`