//react
import {Link} from "react-router-dom";
import {useState, useEffect} from 'react';

//css
import styled from "styled-components"

//img
import productImg from "../../../img/product_img.png"

//page
import OrderDtailProduct from "../../common/product/OrderDtailProduct";

const OrderProductItemList = [
    {
        prd_id: 0,
        tax_payment_date:"2021.02.01",
        txn_state: 1,
        path :"/shop/product",
        src: productImg,
        prd_title:"[농심]농심신라면",
        prd_count:"5",
        prd_detail:"멀티 5입",
        prd_details:"1봉 5개입",
        point:"120",
        prd_sale_rate:"10",
        txn_payment_amount:"3400",
        prd_qty:"1"
    },
    {
        prd_id: 1,
        tax_payment_date:"2021.01.01",
        txn_state: 4,
        path :"/shop/product",
        src: productImg,
        prd_title:"[농심]농심신라면",
        prd_count:"5",
        prd_detail:"멀티 5입",
        prd_details:"1봉 5개입",
        point:"120",
        prd_sale_rate:"20",
        txn_payment_amount:"3400",
        prd_qty:"1"
    }
]
const orderText = {
    1 : { title : "상품 준비중" },
    2 : { title : "배송중" },
    3 : { title : "배송완료" },
    4 : { title : "배송완료" },
    5 : { title : "배송완료" }
}
const btnlist = (txn_state) => {
    switch(txn_state){
        case 1 : 
                return (
                    <OrderBtnWrap>
                        <OrderBtnBasic>배송지변경</OrderBtnBasic>
                        <OrderBtnBasic>주문취소</OrderBtnBasic>
                    </OrderBtnWrap>
                );
        case 2 : 
                return (
                    <OrderBtnWrap>
                        <OrderBtnRed>배송조회</OrderBtnRed>
                    </OrderBtnWrap>
                );
        case 3 : 
                return (
                    <OrderBtnWrap>
                        <OrderBtnBasic>교환신청</OrderBtnBasic>
                        <OrderBtnBasic>반품신청</OrderBtnBasic>
                        <OrderBtnRed>
                            <Link to={"/review/write"}>리뷰적기</Link>
                        </OrderBtnRed>
                    </OrderBtnWrap>
                );
        case 4 : 
                return (
                    <OrderBtnWrap>
                        <OrderBtnRed>
                            <Link to={"/review/write"}>리뷰적기</Link>
                        </OrderBtnRed>
                        <OrderBtnBasic>구매확정</OrderBtnBasic>
                        <OrderBtnText>00일이 지난후에는 교환 및 반품이 불가능 합니다.</OrderBtnText>
                    </OrderBtnWrap>
                );
        case 5 : 
                return (
                    <OrderBtnWrap>
                        <OrderBtnRed>재구매</OrderBtnRed>
                    </OrderBtnWrap>
                );
        default : return null;
    }
}

export default function ProductDetail({value}) {

    return (
        <Container>
            <ProductDetailWrap>
                <ProductList>
                    <ProductListTitleWrap>
                        <ProductListTitle>
                            {
                                orderText[OrderProductItemList[0].txn_state].title
                            }
                        </ProductListTitle>
                        <ProductListSubTitle>1.2(목) 도착 예정</ProductListSubTitle>
                    </ProductListTitleWrap>
                    <ProductWrap>
                        {
                            OrderProductItemList.map((value,index)=>{ return (<OrderDtailProduct rank={true} value={value}/>); })
                        }
                        {
                            btnlist(OrderProductItemList[0].txn_state)
                        }
                    </ProductWrap>
                </ProductList>
            </ProductDetailWrap>
        </Container>
    );
}

//css
const Container = styled.div` 
    width: 100%;
    background: #fff;
`
const ProductDetailWrap = styled.div`
    width: 100%;
    padding: calc(100vw*(25/414)) calc(100vw*(15/414)) 0;
`
const ProductList = styled.div`
    width: 100%;
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(12/414));
    padding: calc(100vw*(20/414)) calc(100vw*(15/414));
`
const ProductListTitleWrap = styled.div`
    display: flex;
    align-items: center;
    width: 100%;
    padding: 0 calc(100vw*(20/414));
`
const ProductListTitle = styled.p`
    font-size: calc(100vw*(18/414));
    font-weight: bold;
`
const ProductListSubTitle = styled.span`
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    color: #979797;
    margin-left: calc(100vw*(5/414));
`
const ProductWrap = styled.div`
    width: 100%;
    margin-top: calc(100vw*(10/414));
`
const OrderBtnWrap = styled.div`
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    padding-right: calc(100vw*(10/414));
`
const OrderBtnBasic = styled.button`
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    padding: calc(100vw*(5/414)) calc(100vw*(24/414));
    background: #f7f7f7;
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(4/414));
    margin-left: calc(100vw*(10/414));
    color: #707070;
`
const OrderBtnRed = styled(OrderBtnBasic)`
    padding: calc(100vw*(5/414)) calc(100vw*(18/414));
    background: #ec2939;
    border-color: #bc343f;
    color: #fff;
`
const OrderBtnText = styled.p`
    width: 100%;
    font-size: calc(100vw*(10/414));
    text-align: center;
    color: #707070;
    padding-top: calc(100vw*(6/414));
`