//react
import {Link} from "react-router-dom";

//css
import styled from "styled-components"


export default function ServiceCenter() {

    return(
        <Container>
            <ServiceCenterWrap>
                <ServiceCenterListWrap>
                    <ServiceCenterList>
                        <Link to={"/privacyPolicy"}>개인정보처리방침</Link>
                    </ServiceCenterList>
                    <ServiceCenterList>
                        <Link to={"/term"}>이용약관</Link>
                    </ServiceCenterList>
                    <ServiceCenterList>전화문의 1588-1588</ServiceCenterList>
                </ServiceCenterListWrap>

            </ServiceCenterWrap>
        </Container>

    );
}

const Container = styled.div`
    width: 100%;
`
const ServiceCenterWrap = styled.div`
    width: 100%; min-height: calc(100vh - calc(100vw*(150/414)));
    background: #fff;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    z-index: 10;
    margin-top: calc(100vw*(12/414));
    padding-bottom: calc(100vw*(100/414));
`
const ServiceCenterListWrap = styled.ul`
    padding: calc(100vw*(24/414)) calc(100vw*(50/414));
    display: flex;
    flex-wrap: wrap;
    align-items: center;
`
const ServiceCenterList = styled.li`
    width:100%;
    padding: calc(100vw*(15/414)) 0;
    margin: calc(100vw*(10/414)) 0;
    font-size: calc(100vw*(15/414));
    font-weight: bold;
`