//react
import {Link} from "react-router-dom";
import React ,{useState,useEffect} from 'react';

//css
import styled from "styled-components"

//img
import IconstarOn from "../../../img/ico_star_on.svg"
import IconstarOff from "../../../img/ico_star_off.svg"
import IcoSoldOut from "../../../img/ico_sold_out.png"

//page
import appInfoController from '../../../server/appInfoController'
import serverController from '../../../server/serverController';

//숫자 3자리수 콤마 코드
function numberWithCommas(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

const getStarImageList = (count) =>{
    let imageList = [];
    for(var i =0;i < 5;i++){
        imageList.push(
            <Stars>
             <img src={i < count ? IconstarOn : IconstarOff} alt=""/>
            </Stars>
        )
    }
    return imageList;
}
  

export default function Product({value,rank}) {

    return (
            <Container>
            {
                value.prd_soldout != 0 ?

                <ProductListWrap>
                    {
                        rank ? 
                        <ProductNumberWrap>
                            <ProductNumber>{value.number ? value.number : "0"}위</ProductNumber>
                        </ProductNumberWrap>
                        :
                        null
                    }
                
                    <ProductImgWrap>
                        <ProductImg src={appInfoController.ImagePath + value.prd_img} alt="Product img"/>
                    </ProductImgWrap>
                    <ProductTextWrap>
                        <ProductTextInner>
                            <ProductName>{value.prd_title}</ProductName>
                            <ProductText>{value.prd_detail1}</ProductText>
                            <Point>적립금 <PointText>{numberWithCommas(value.prd_earn_price ? value.prd_earn_price : "0")}</PointText>원</Point>
                            {
                                value.prd_sale_rate > 0 ? 
                                <DiscountRate><Discount>Discount</Discount>{value.prd_sale_rate}%</DiscountRate>
                                :
                                null
                            }
                            {
                                value.prd_type == 2 ?
                                <StarsWrap>
                                    {
                                        getStarImageList(Math.floor(value.rev_score))
                                    }
                                    <StarText>({numberWithCommas(value.rev_score ? value.rev_score : "0")})</StarText>
                                </StarsWrap>
                                :
                                null
                            }
                        </ProductTextInner>
                        <ProductTextInner>
                            {
                                value.prd_sale_rate > 0 ?
                                <Price>{numberWithCommas(value.prd_price)}원</Price>
                                :
                                null
                            }
                            <TotalPrice><TotalPriceText>{numberWithCommas(Math.round(value.prd_price - value.prd_price*value.prd_sale_rate/100))}</TotalPriceText>원</TotalPrice>
                        </ProductTextInner>
                    </ProductTextWrap>
                </ProductListWrap>
            :
                <ProductListWrap>
                    {
                        rank ? 
                        <ProductNumberWrap>
                            <ProductNumber>{value.number ? value.number : "0"}</ProductNumber>
                        </ProductNumberWrap>
                        :
                        null
                    }
                
                    <ProductImgWrap>
                        <ProductImg src={appInfoController.ImagePath + value.prd_img} alt="Product img"/>
                        <SoldOutImgWrap>
                            <SoldOutImgText>품절</SoldOutImgText>
                        </SoldOutImgWrap>
                    </ProductImgWrap>
                    <ProductTextWrap>
                        <ProductTextInner>
                            <SoldOutProductName>{value.prd_title}</SoldOutProductName>
                            <SoldOutProductText>{value.prd_detail1}</SoldOutProductText>
                            <SoldOutPoint>적립금 <SoldOutPointText>{numberWithCommas(value.prd_earn_price ? value.prd_earn_price : "0")}</SoldOutPointText>원</SoldOutPoint>
                            {
                                value.prd_sale_rate > 0 ? 
                                <SoldOutDiscountRate><SoldOutDiscount>Discount</SoldOutDiscount>{value.prd_sale_rate}%</SoldOutDiscountRate>
                                :
                                null
                            }
                            <StarsWrap>
                                {
                                    getStarImageList(value.rev_score ? value.rev_score : "0")
                                }
                                <StarText>({numberWithCommas(value.rev_score ? value.rev_score : "0")})</StarText>
                            </StarsWrap>
                        </ProductTextInner>
                        <ProductTextInner>
                            
                        <SoldOutText><IconSoldOut src={IcoSoldOut}/> 품절</SoldOutText>
                        {
                            value.prd_sale_rate > 0 ?
                            <Price>{numberWithCommas(value.prd_price)}원</Price>
                            :
                            null
                        }
                        <SoldOutTotalPrice><SoldOutTotalPriceText>{numberWithCommas(Math.round(value.prd_price - value.prd_price*value.prd_sale_rate/100))}</SoldOutTotalPriceText>원</SoldOutTotalPrice>
                        </ProductTextInner>
                    </ProductTextWrap>
                </ProductListWrap>
            }
            </Container>
  );
}

const Container = styled.div`
    width: 100%;
`
const ProductListWrap = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    padding: calc(100vw*(22/414));
    border-bottom: calc(100vw*(1/414)) solid #f7f7f7;
`
const ProductNumberWrap = styled.div`
    padding-right: calc(100vw*(15/414));
`
const ProductNumber = styled.div`
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    white-space: nowrap;
`
const ProductImgWrap = styled.div`
    position: relative;
    width: calc(100vw*(120/414)); height: calc(100vw*(120/414));
`
const ProductImg = styled.img`
    display: block;
    width: 100%; height: 100%;
    object-fit: cover;
`
const ProductTextWrap = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    width: calc(100vw*(260/414));
    padding-left: calc(100vw*(20/414));
`
const ProductTextInner = styled.div`
    display: block;
`
const ProductName = styled.p`
    width: 100%;
    font-size: calc(100vw*(15/414));
    margin-bottom: calc(100vw*(3/414));
`
const ProductText = styled.p`
    width: 100%;
    font-size: calc(100vw*(12/414));
`
const Point = styled.p`
    font-size: calc(100vw*(11/414));
    color: #292929;
    font-weight: bold;
    margin-top: calc(100vw*(10/414));
`
const PointText = styled.span`
    color: #292929;
    font-weight: 900;
`
const Discount = styled.span`
    font-size: calc(100vw*(11/414));
    color: #e20000;
    font-weight: normal;
    margin-right: calc(100vw*(3/414));
`
const DiscountRate = styled.span`
    display: block;
    width: 100%;
    font-size: calc(100vw*(15/414));
    color: #e20000;
    font-weight: 900;
`
const Price = styled.span`
    display: block;
    font-size: calc(100vw*(13/414));
    color: #707070;
    text-align: right;
    text-decoration-line: line-through;
`
const TotalPrice = styled.span`
    display: flex;
    align-items: center;
    font-size: calc(100vw*(15/414));
    margin-top: calc(100vw*(6/414));
`
const TotalPriceText = styled.span`
    font-size: calc(100vw*(20/414));
    font-weight: 900;
`
const StarsWrap = styled.div`
    display: flex;
    align-items: center;
`
const Stars = styled.span`
    display: block;
    width: calc(100vw*(10.6/414));
    margin: 0 calc(100vw*(0.8/414));

`
const StarText = styled.span`
    font-size: calc(100vw*(10/414));
    font-weight: bold;
    color: #707070;
    margin-left: calc(100vw*(3/414));
`

/* Sold out */
const SoldOutText = styled.p`
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    text-align: right;
    margin-bottom: calc(100vw*(5/414));
`
const IconSoldOut = styled.img`
    width: calc(100vw*(15/414));
    margin-right: calc(100vw*(5/414));
`
const SoldOutImgWrap = styled.div`
    position: absolute; top: 0; left: 0;
    width: 100%; height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(1,1,1,0.5);
`
const SoldOutImgText = styled.p`
    font-size: calc(100vw*(22/414));
    color: #fff;
    font-weight: bold;
`

const SoldOutProductName = styled(ProductName)`
    color: #7d7d7d;
`
const SoldOutProductText = styled(ProductText)`
    color: #7d7d7d;
`
const SoldOutPoint = styled(Point)`
    color: #7d7d7d;
`
const SoldOutPointText = styled(PointText)`
    color: #7d7d7d;
`
const SoldOutDiscount = styled(Discount)`
    color: #ee7d7d;
`
const SoldOutDiscountRate = styled(DiscountRate)`
    color: #ee7d7d;
`
const SoldOutTotalPrice = styled(TotalPrice)`
    color: #7d7d7d;
`
const SoldOutTotalPriceText = styled(TotalPriceText)`
    color: #7d7d7d;
`