//react
import {Link} from "react-router-dom";

//css
import styled from "styled-components"


export default function SignUpStep1() {

    return (
        <Container>
            <SignUpWrap>
                <Link to={"/signUpStep2"}>
                    <SignUpTypeWrap>
                        <SignUpTypeText>통합회원 회원가입</SignUpTypeText>
                    </SignUpTypeWrap>
                </Link>
                <Link to={"/signUpStep2"}>
                    <SignUpTypeWrap>
                        <SignUpTypeText>쇼핑몰 회원가입</SignUpTypeText>
                    </SignUpTypeWrap>
                </Link>
                <Link to={"/signUpStep2"}>
                    <SignUpTypeWrap>
                        <SignUpTypeText>사업자 회원가입</SignUpTypeText>
                    </SignUpTypeWrap>
                </Link>
            </SignUpWrap>
        </Container>
  );
}

const Container = styled.div`
    position: relative;
    width: 100%; height: calc(100vh - calc(100vw*(65/414)));
    z-index: 100;
    background: #fff;
`
const SignUpWrap = styled.div`
    width: 100%;
    padding: calc(100vw*(36/414));
`
const SignUpTypeWrap = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%; height: calc(100vw*(210/414));
    border: calc(100vw*(1/414)) solid #707070;
    border-radius: calc(100vw*(8/414));
    margin-bottom: calc(100vw*(16/414));
`
const SignUpTypeText = styled.p`
    font-size: calc(100vw*(28/414));
    font-weight: bold;
    text-align: center;
`