//react
import { Link,useHistory } from "react-router-dom";

//css
import styled from "styled-components"

//img
import IconBackBtn from "../../../img/ico_arrow_back.png"


export default function MaypageSetTitle() {
    
const history = useHistory();
const goBack = () =>{
    history.goBack();
}
  return (
        <Container>
            <MypageMainTitleWrap>
                <BackBtnWrap onClick={goBack}>
                    <BackBtn></BackBtn>
                </BackBtnWrap>
                <div>
                    <MypageMainTitleText>내정보관리</MypageMainTitleText>
                </div>
                <SetBtnWrap>
                    <Link to={"/mypageSet/edit"}>
                        <SetBtn>수정</SetBtn>
                    </Link>
                </SetBtnWrap>
            </MypageMainTitleWrap>
        </Container>
  );
}

//css
const Container = styled.div` 
    width: 100%;
`
const MypageMainTitleWrap = styled.div` 
    width: 100%; height: calc(100vw*(65/414));
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #fff;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
`
const SetBtnWrap = styled.div`
    position: absolute; right: calc(100vw*(24/414));
`
const SetBtn = styled.button`
    padding: calc(100vw*(5/414)) calc(100vw*(14/414));
    background: #f7f7f7;
    border: calc(100vw*(1/414)) solid #969696;
    border-radius: calc(100vw*(6/414));
    font-size: calc(100vw*(15/414));
    font-weight: bold;
`
const MypageMainTitleText = styled.h2`
    width: 100%;
    font-size: calc(100vw*(20/414));
    font-weight: 900;
`
const BackBtnWrap = styled.div`
    position: absolute; left: calc(100vw*(15/414));
    width: calc(100vw*(45/414)); height: calc(100vw*(45/414));
`
const BackBtn = styled.button`
    width: calc(100vw*(45/414)); height: calc(100vw*(45/414));
    background: url(${IconBackBtn}) no-repeat center/calc(100vw*(10/414));
`