//react
import React ,{useState,useEffect} from 'react';

import MainTitleBar from '../../component/main/MainTitleBar';
import MainTitleMenuBar from '../../component/main/MainTitleMenuBar';
import MainBanner from '../../component/main/MainBanner';
import MarketDeal from '../../component/main/MarketDeal';
import MainRecentPurchases from '../../component/main/MainRecentPurchases';
import MainProduct from '../../component/main/MainProduct';
import FooterPadding from '../../component/common/FooterPadding';
import FooterMenu from '../../component/common/FooterMenu';


//main page
export default function MainPage() {
  return (
    <>
      <MainTitleBar/>
      <MainTitleMenuBar/>
      <MainBanner/>
      <MarketDeal/>
      <MainRecentPurchases rank={true}/>
      <MainProduct/>
      <FooterPadding option={{backgroundColor:"#f7f7f7"}}/>
      <FooterMenu />
    </>
  );
}
