import {useState} from "react";

//css
import styled from "styled-components"

//img
import IcoArrowOn from "../../img/ico_arrow_on.png"
import IcoArrowOff from "../../img/ico_arrow_off.png"

//page
import MarketProduct from './MarketProduct';

export default function MarketProductList({openFilterModal, tabBtnD}) {
  const [show, setShow] = useState(true);


  if(tabBtnD == false)
  return null;

  return (
    <Container>
        <ProductWrap>
            <MainTitleWrap>
                <MainTitleBox onClick={()=>{setShow(e => !e)}}>
                    <MainTitle>오뚜기 기획</MainTitle>
                    <MainTitleDate>2020.11.27(금)
                        {
                            show ?
                            <IcoArrowMore src={IcoArrowOn} alt="more"/>
                            :
                            <IcoArrowMore src={IcoArrowOff} alt="more"/>
                        }
                    </MainTitleDate>
                </MainTitleBox>
            </MainTitleWrap>
            <SubTitleWrap>
                <SubTitle>오늘의 마켓딜 !</SubTitle>
            </SubTitleWrap>

            {/* Product */}
            {
                show ?
                <MarketProduct openFilterModal={openFilterModal}/>
                :
                null
            }
        </ProductWrap>
    </Container>
);
}

/* main title */
const Container = styled.div` 
    width:100%;
`
const ProductWrap = styled.div` 
    width:100%; min-height: calc(100vh - calc(100vw*(180/414)));
    background: #fff;
    margin-bottom: calc(100vw*(20/414));
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
`
const MainTitleWrap = styled.div`
    width: 100%;
`
const MainTitleBox = styled.a`
    display: flex;
    justify-content: space-between;
    align-items: center;
    width:100%;
    padding: calc(100vw*(24/414)) calc(100vw*(26/414)) calc(100vw*(18/414));
`
const MainTitle = styled.span`
    font-size: calc(100vw*(15/414));
    color: #ED2939;
    font-weight: 900;
`
const MainTitleDate = styled.span`
    font-size: calc(100vw*(13/414));
    font-weight: bold;
`
const IcoArrowMore = styled.img`
    width: calc(100vw*(12/414));
    margin-left: calc(100vw*(28/414));
`
const SubTitleWrap = styled.div`
    background: #ff6752;
`
const SubTitle = styled.p`
    width: 100%;
    font-size: calc(100vw*(15/414));
    font-weight: 900;
    padding: calc(100vw*(13/414)) calc(100vw*(26/414));
    color: #fff;
`
