import { Swiper, SwiperSlide } from 'swiper/react';

//css
import 'swiper/swiper-bundle.css';
import styled from "styled-components"

//img
import MarketSlideImg1 from "../../img/market_slide_img1.png"
import MarketSlideImg2 from "../../img/market_slide_img2.png"
import MarketSlideImg3 from "../../img/market_slide_img3.png"


export default function MarketDeal({option}) {
    
  return (
        <Container style={option}>
            <MainTitleWrap>
                <MainTitle>오늘의 마켓 딜</MainTitle>
                <MainText>전국 곳곳에 할인상품을 모았습니다!</MainText>
            </MainTitleWrap>

            {/* market slide */}
            <MarketBannerWrap className="MarketBannerWrap">
                <Swiper
                    spaceBetween={5}
                    slidesPerView={2.2}
                >
                    <SwiperSlide>
                        <Slide>
                            <MarketSlideImgA src={MarketSlideImg1} alt="Market slide"/>
                            <MarketSlideTextboxA>
                                <MarketSlideTextA>오늘의 마켓딜<br/>UP TO 70% OFF</MarketSlideTextA>
                            </MarketSlideTextboxA>
                        </Slide>
                    </SwiperSlide>
                    <SwiperSlide>
                        <SlideA>
                            <MarketSlideImgB src={MarketSlideImg2} alt="Market slide"/>
                            <MarketSlideTextboxB>
                                <MarketSlideTextB>제철음식재료<br/>마켓 딜!</MarketSlideTextB>
                            </MarketSlideTextboxB>
                        </SlideA>
                    </SwiperSlide>
                    <SwiperSlide>
                        <SlideB>
                            <MarketSlideImgC src={MarketSlideImg3} alt="Market slide"/>
                            <MarketSlideTextboxC>
                                <MarketSlideTextC>수입포도<br/>50% 세일 기간</MarketSlideTextC>
                            </MarketSlideTextboxC>
                        </SlideB>
                    </SwiperSlide>
                </Swiper>
            </MarketBannerWrap>
        </Container>
  );
}

/* main title */
const Container = styled.div` 
    width:100%;
    padding-bottom: calc(100vw*(36/414));
`
const MainTitleWrap = styled.div` 
    display: flex; flex-wrap: wrap; justify-content: space-between;
    width:100%;
    padding-top: calc(100vw*(25/414));
    padding-left: calc(100vw*(22/414));
    margin-bottom: calc(100vw*(10/414));
`
const MainTitle = styled.h2`
    width: 100%;
    font-size: calc(100vw*(15/414));
    line-height: 1.2;
    font-weight: 900;
`
const MainText = styled.p`
    width: 100%;
    font-size: calc(100vw*(12/414));
    font-weight: normal;
`

/* market slide */
const MarketBannerWrap = styled.div` 
    width: 100%;
`
const Slide = styled.div`
    position: relative;
    height: calc(100vw*(122/414));
    border-radius: calc(100vw*(16/414));
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    overflow: hidden;
`
const SlideA = styled(Slide)``
const SlideB =styled(Slide)``

const MarketSlideImgA = styled.img`
    position: absolute; top: 0; left: 0;
    width: 100%; height: 100%; object-fit: cover;
`
const MarketSlideImgB = styled(MarketSlideImgA)``
const MarketSlideImgC =styled(MarketSlideImgA)``

const MarketSlideTextboxA = styled.div`
    position: absolute; top: 0; left: 0;
    width: 100%; height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
`
const MarketSlideTextboxB = styled(MarketSlideTextboxA)``
const MarketSlideTextboxC =styled(MarketSlideTextboxA)``

const MarketSlideTextA = styled.p`
    display: block;
    font-size: calc(100vw*(13/414));
    text-align: center;
    color: #fff;
    line-height: 1.1;
`
const MarketSlideTextB = styled(MarketSlideTextA)``
const MarketSlideTextC =styled(MarketSlideTextA)``
