//react
import { Link,useHistory } from "react-router-dom";

//css
import styled from "styled-components"


export default function Complete() {
    
const history = useHistory();
const goBack = () =>{
    history.go(-3);
}

    return (
        <Container>
            <ShopLine></ShopLine>
            <CompleteWrap>
                <CompleteTitleWrap>
                    <CompleteTitle>문의가 성공적으로 작성되었습니다.<br/>빠른시일내에 답변드릴수 있도록 하겠습니다.</CompleteTitle>
                    <CompleteBtn onClick={goBack}>돌아가기</CompleteBtn>
                </CompleteTitleWrap>
            </CompleteWrap>
        </Container>
  );
}

const Container = styled.div`
    width: 100%; height: calc(100vh - calc(100vw*(164/414)));
`
const ShopLine = styled.div`
    width: 100%;
    height: calc(100vw*(14/414));
    background: #f7f7f7;
`
const CompleteWrap = styled.div`
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 100%; height: 100%;
    background: #fff;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0,0,0,0.16);
    padding: calc(100vw*(46/414)) calc(100vw*(28/414));
`
const CompleteTitleWrap = styled.div`
    width: 100%;
`
const CompleteTitle = styled.p`
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    text-align: center;
    line-height: 1.3;
    margin-bottom: calc(100vw*(60/414));
`
const CompleteBtn = styled.button`
    width: 100%;
    background: #ed2939;
    border: calc(100vw*(3/414)) solid #bc343f;
    color: #fff;
    border-radius: calc(100vw*(6/414));
    padding: calc(100vw*(18/414)) 0;
    font-size: calc(100vw*(18/414));
    font-weight: 900;
`