//react
import {Link} from "react-router-dom";

//css
import styled from "styled-components"


export default function SignUpComplete() {

    return (
        <Container>
                <MainBackWrap>
                    <MainBackTextWrap>
                        <MainBackText>비밀번호 변경이 완료되었습니다.</MainBackText>
                        <MainBackText>다시 로그인해주세요.</MainBackText>
                    </MainBackTextWrap>
                    <Link to={"/login"}>
                        <MainBackBtn>로그인</MainBackBtn>
                    </Link>
                </MainBackWrap>

        </Container>
  );
}

const Container = styled.div`
    width: 100%; height: 100vh;
    background: #fff;
`
const MainBackWrap = styled.div`
    text-align: center;
    padding-top: calc(100vw*(140/414));
    padding-bottom: calc(100vw*(80/414));
`
const MainBackBtn = styled.button`
    width: calc(100vw*(342/414));
    background: #fff;
    color: #4b4b4b;
    border: calc(100vw*(2/414)) solid #efefef;
    border-radius: calc(100vw*(13/414));
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.13);
    padding: calc(100vw*(19/414)) 0;
    font-size: calc(100vw*(19/414));
    font-weight: 900;
`
const MainBackTextWrap = styled.div`
    width: 100%;
    padding-bottom: calc(100vw*(45/414));
`
const MainBackText = styled.p`
    font-size: calc(100vw*(21/414));
    font-weight: bold;
    color: #ed2939;
    padding: calc(100vw*(2/414)) 0;
`