//react
import React ,{useState,useEffect} from 'react';
import {Link} from "react-router-dom";

//css
import styled from "styled-components"

//img
import productImg from "../../img/product_img.png"

//page
import serverController from '../../server/serverController';
import Product from "../common/product/Product";


export default function BestProduct() {
    const [isMarket,setIsMarket] = useState(true);
    const [itemList,setItemList] = useState([]);
    const [shopList,setShopList] = useState([]);
    
    useEffect(()=>{
        serverController.connectFetchController(`products/hit?prd_type=1`,"GET",null,function(res){
        if(res.result == 1) {
            setItemList(res.info)
        }});
        serverController.connectFetchController(`products/hit?prd_type=2`,"GET",null,function(res){
        if(res.result == 1) {
            setShopList(res.info)
        }});
    },[]);
    

  return (
        <Container>
            <BestProductWrap>
                <BestProductTitleWrap>
                    <BestProductTitleBox>
                        <BestProductTitleListA>
                            <BestProductTitle isMarket={isMarket} onClick={()=>{setIsMarket(true)}}>마켓상품 보기</BestProductTitle>
                        </BestProductTitleListA>
                        <BestProductTitleListB>
                            <MallProductTitle isMarket={isMarket} onClick={()=>{setIsMarket(false)}}>쇼핑몰상품 보기</MallProductTitle>
                        </BestProductTitleListB>
                    </BestProductTitleBox>
                </BestProductTitleWrap>
                {/* Product */}
                <ProductBox>
                {
                    isMarket ?
                        itemList.length > 0 ?
                        itemList.map((value,index)=>{
                            return (
                                <Link to={`/shop/product/${value.prd_type}/${value.prd_id}`}>
                                    <Product key={index + "marketList"} rank={true} value={value}/>
                                </Link>
                            )
                        })
                        :
                        <BestNoText>마켓 상품이 없습니다.</BestNoText>
                    :
                        shopList.length > 0 ?
                        shopList.map((value,index)=>{
                            return (
                                <Link to={`/shop/product/${value.prd_type}/${value.prd_id}`}>
                                    <Product key={index + "marketList"} rank={true} value={value}/>
                                </Link>
                            )
                        })
                        :
                        <BestNoText>쇼핑몰 상품이 없습니다.</BestNoText>
                }
                </ProductBox>
            </BestProductWrap>
        </Container>
  );
}

/* main title */
const Container = styled.div` 
    width:100%;
`
const BestProductWrap = styled.div` 
    width:100%;
    background: #fff;
    margin-bottom: calc(100vw*(24/414));
    padding-bottom: calc(100vw*(30/414));
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
`
const BestProductTitleWrap = styled.div` 
    padding: calc(100vw*(15/414)) 0 calc(100vw*(10/414));
`
const BestProductTitleBox = styled.ul`
    display: flex;
    align-items: center;
    justify-content: center;
`
const BestProductTitleListA = styled.li`
    margin: 0 calc(100vw*(22/414));
`
const BestProductTitleListB = styled(BestProductTitleListA)``


const ProductTitle = styled.a`
    position: relative;
    display: block;
    padding: calc(100vw*(10/414));
    font-weight: bold;
    font-size: calc(100vw*(15/414));
`
const BestProductTitle = styled(ProductTitle)`

    ${({isMarket})=>{
        return isMarket ? 
        `&::after {
            content: '';
            position: absolute; left: 0; bottom: 0;
            display: block;
            width: 100%; height: calc(100vw*(3/414));
            background: #ED2939;
            border-radius: calc(100vw*(3/414));
        }`
        :
        ``
    }}
`
const MallProductTitle = styled(ProductTitle)`
    ${({isMarket})=>{
        return !isMarket ? 
        `&::after {
            content: '';
            position: absolute; left: 0; bottom: 0;
            display: block;
            width: 100%; height: calc(100vw*(3/414));
            background: #ED2939;
            border-radius: calc(100vw*(3/414));
        }`
        :
        ``
    }}
`

/* Product */
const ProductBox = styled.div`
    min-height: calc(100vh - calc(100vw*(180/414)));
    margin-bottom: calc(100vw*(24/414));
`
const BestNoText = styled.p`
    text-align: center;
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    color: #979697;
    padding: calc(100vw*(80/414)) 0;
`