//css
import styled from "styled-components"

//img


export default function ShopProductInquiry({openPurchaseModal}) {

    return (
        <Container>
            <ShopBtnWrap>
                <CartBtn onClick={()=>{openPurchaseModal(true)}}>장바구니</CartBtn>
                <PurchaseBtn onClick={()=>{openPurchaseModal(true)}}>바로구매</PurchaseBtn>
            </ShopBtnWrap>
        </Container>
    );
}


const Container = styled.div`
    width: 100%; height: calc(100vw*(70/414));
`
const ShopBtnWrap = styled.div`
    width: 100%;
    position: fixed; bottom: calc(100vw*(95/414));
    display: flex;
    padding: calc(100vw*(6/414)) calc(100vw*(14/414)) calc(100vw*(15/414));
    z-index: 500;
`
const ShopBtn = styled.button`
    width: 100%;
    padding: calc(100vw*(11/414)) 0;
    background: #b4b4b4;
    border: calc(100vw*(3/414)) solid #a5a5a5;
    border-radius: calc(100vw*(6/414));
    color: #fff;
    font-size: calc(100vw*(18/414));
    font-weight: 900;
    margin: 0 calc(100vw*(4/414));
`
const CartBtn = styled(ShopBtn)``
const PurchaseBtn = styled(ShopBtn)`
    background: #ed2939;
    border-color: #bc343f;

`