//react
import React ,{useState,useEffect} from 'react';

import MaypageSetEditTitle from '../../../component/mypage/set/MaypageSetEditTitle';
import MaypageSetEditList from '../../../component/mypage/set/MaypageSetEditList';
import FooterPadding from '../../../component/common/FooterPadding';
import FooterMenu from '../../../component/common/FooterMenu';
import Popup from '../../../component/common/modal/PopupPostCode';

//MypageSetEdit page
export default function MypageSetEditPage() {
  const [adressShow,setAdressShow] = useState(false);
  const [adrZipcode,setAdrZipcode] = useState("");
  const [adrAddress,setAdrAddress] = useState("");
  const [editBtn,setEditBtn] = useState(false);

  const EditBtnOn = (onOff) =>{setEditBtn(onOff);}

  return (
    <>
        <MaypageSetEditTitle EditBtnOn={EditBtnOn}/>
        <MaypageSetEditList
          editBtn={editBtn}
          setAdressShow={setAdressShow}
          adrZipcode={adrZipcode}
          setAdrZipcode={setAdrZipcode}
          adrAddress={adrAddress}
          setAdrAddress={setAdrAddress}
        />
        <FooterPadding option={{backgroundColor:"#fff"}}/>
        <FooterMenu/>
        {
          adressShow ?
          <Popup setAdressShow={setAdressShow} setAdrZipcode={setAdrZipcode} setAdrAddress={setAdrAddress}/>
          :
          null
        }
      
    </>
  );
}
