//react
import React,{useState,useEffect}  from 'react';

//css
import styled from "styled-components"

//img
import IconSelect from "../../../img/ico_shop_select.png"

export default function DetailSetOptionSelectBox({zIndex,title,list ,clickSelect}) {

    //const [title,setTitle ] = useState("");
    const [show,setShow ] = useState(false);
    const [selectTitle, setSelectTitle] = useState(title);
    const [listHistory, setListHistory] = useState(list);
    // if(list == undefined || list.length == 0 || reset == true){
    //   return <View></View>
    // }

    useEffect(() => {
      if(!list || list[0].opt_relate_id == 0){
        return;
      }
      
      if(listHistory != null){
        if(JSON.stringify(listHistory) != JSON.stringify(list)){
          setSelectTitle(title);
        }
      }
        
      setListHistory(list);   
      }, [list])

    const openDropDownModal = () =>{
      setShow(e => !e);
    }

    const modalSelect = (value) =>{
      setShow(false);
      if(clickSelect(value) == false)
        return;
      setSelectTitle(value.label);
    }

    return (
      <OptionWrap>
        <Optionbox onClick={openDropDownModal}>
            <p>{selectTitle}</p>
            {
                // show ? 
                // <img  src={require('../../../assets/img/label_point/dropUpPoint.png')} resizeMode={"stretch"}/>
                // :
                // <img  src={require('../../../assets/img/label_point/dropDownPoint.png')} resizeMode={"stretch"}/>
            }
        </Optionbox>
        {
            show && list ?
            <SelectBoxWrap>
                {
                    list.map((value)=>{
                    return (
                        <SelectBox onClick={()=>{modalSelect(value)}}>
                            <p>{value.label}</p>
                        </SelectBox>
                    )
                    })
                }
            </SelectBoxWrap>
            :
            null
        }
    </OptionWrap>
    );
}
          

const OptionWrap = styled.div`
    width: 100%;
`
const Optionbox = styled.div`
    width: calc(100vw*(380/414));
    margin: calc(100vw*(8/414)) auto 0;
    padding: calc(100vw*(16/414)) calc(100vw*(24/414));
    border: calc(100vw*(1/414)) solid #d1d1d1;
    border-radius: calc(100vw*(8/414));
    font-size: calc(100vw*(14/414));
    color: #4b4b4b;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background: url(${IconSelect}) no-repeat 93% 50%/calc(100vw*(14/414));
`
const SelectBoxWrap = styled.div`
    position: absolute;
    left: 50%;
    width: calc(100vw*(380/414));
    margin-top: calc(100vw*(-1/414));
    margin-left: calc(100vw*(-190/414));
    border: calc(100vw*(1/414)) solid #d1d1d1;
    border-radius: calc(100vw*(8/414));
    background: #fff;
    padding: calc(100vw*(6/414)) 0;
`
const SelectBox = styled.div`
    width: 100%;
    padding: calc(100vw*(10/414)) calc(100vw*(24/414));
    font-size: calc(100vw*(14/414));
    color: #4b4b4b;
`