//react
import React ,{useState,useEffect} from 'react';

//css
import styled from "styled-components"

//img
import productImg from "../../img/product_img.png"

//page
import serverController from '../../server/serverController';
import MarketProductList from "../common/product/MarketProduct";
import ProductTitleDetail from '../common/product/ProductTitleDetail';


export default function MarketBest({openFilterModal}) {
    const [itemList,setItemList] = useState([]);
    useEffect(()=>{
        serverController.connectFetchController(`products/info?prd_type=1`,"GET",null,function(res){
        if(res.result == 1) {
            setItemList(res.items)
        }});
    },[]);

  return (
    <Container>
        <ProductWrap>
            <MainTitleWrap>
                <MainTitleBox>
                    <MainTitle>자주 구매한 상품</MainTitle>
                </MainTitleBox>
                <SubTitleBox>
                    <SubTitle>우리마트의 이런 제품들은 어떠세요?</SubTitle>
                </SubTitleBox>
            </MainTitleWrap>

            {/* Product */}
            <ProductBox>
                <ProductTitleWrap>
                    <ProductTitleDetail openFilterModal={openFilterModal}/>
                </ProductTitleWrap>
                <ProductLine></ProductLine>

                <ProductList>
                    {
                       itemList.map((value,index)=>{ return (<MarketProductList value={value}/>)})
                    }
                </ProductList>
            </ProductBox>

        </ProductWrap>
    </Container>
);
}

/* main title */
const Container = styled.div` 
    width:100%;
`
const ProductWrap = styled.div` 
    width:100%;
    background: #fff;
    padding-bottom: calc(100vw*(5/414));
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
`
const MainTitleWrap = styled.div`
    width: 100%;
`
const MainTitleBox = styled.p`
    display: flex;
    justify-content: space-between;
    align-items: center;
    width:100%;
    padding: calc(100vw*(24/414)) calc(100vw*(26/414)) calc(100vw*(12/414));
`
const MainTitle = styled.span`
    font-size: calc(100vw*(15/414));
    color: #ED2939;
    font-weight: 900;
`
const SubTitleBox = styled.a`
    width:100%;
    padding: 0 calc(100vw*(26/414));
`
const SubTitle = styled.p`
    font-size: calc(100vw*(13/414));
    font-weight: bold;
`
const IcoArrowMore = styled.img`
    width: calc(100vw*(12/414));
    margin-left: calc(100vw*(28/414));
`

/* Product */
const ProductBox = styled.div`
    width: 100%;
`
const ProductTitleWrap = styled.div`
    width: 100%;
    padding: calc(100vw*(35/414)) calc(100vw*(26/414)) calc(100vw*(5/414));
`

const ProductLine = styled.div`
    width: calc(100vw*(392/414));
    height: calc(100vw*(1/414));
    background: #d1d1d1;
    margin: 0 auto;
`
/* Product List */
const ProductList = styled.div`
    display: flex;
    flex-wrap: wrap;
    width: 100%; min-height: calc(100vh - calc(100vw*(240/414)));
    padding: calc(100vw*(10/414)) calc(100vw*(5/414)) calc(100vw*(20/414));
`