//css
import styled from "styled-components"

//img
import IcoSoldOut from "../../../img/ico_sold_out.png"

//page
import appInfoController from '../../../server/appInfoController'

//숫자 3자리수 콤마 코드
function numberWithCommas(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}


export default function PurchaseProduct({value}) {
    return (
        <Container>
        {
            value.prd_soldout != 0 ?
            <ProductWrap>
                <PurchaseSlideImgWrap>
                    <PurchaseSlideImg src={appInfoController.ImagePath + value.prd_img} alt="Purchase slide"/>
                </PurchaseSlideImgWrap>
                <PurchaseSlideTextbox>
                    <PurchaseName>{value.prd_title}</PurchaseName>
                    <PurchaseText>{value.prd_detail1}</PurchaseText>
                    <>
                        <Point>적립금 <PointText>{numberWithCommas(value.prd_earn_price ? value.prd_earn_price : "0")}</PointText>원</Point>
                        {
                            value.prd_sale_rate > 0 ? 
                            <DiscountRate><Discount>Discount</Discount>{value.prd_sale_rate}%</DiscountRate>
                            :
                            null
                        }
                    </>
                    <>
                        {
                            value.prd_sale_rate > 0 ?
                            <Price>{numberWithCommas(value.prd_price)}원</Price>
                            :
                            null
                        }
                        <TotalPrice><TotalPriceText>{numberWithCommas(Math.round(value.prd_price - value.prd_price*value.prd_sale_rate/100))}</TotalPriceText>원</TotalPrice>
                    </>
                </PurchaseSlideTextbox>
            </ProductWrap>
            :
            <ProductWrap>
                <PurchaseSlideImgWrap>
                    <PurchaseSlideImg src={value.src} alt="Purchase slide"/>
                    <SoldOutImgWrap>
                        <SoldOutImgText>품절</SoldOutImgText>
                    </SoldOutImgWrap>
                </PurchaseSlideImgWrap>
                <PurchaseSlideTextbox>
                    <PurchaseName>{value.prd_title}</PurchaseName>
                    <PurchaseText>{value.prd_detail1}</PurchaseText>
                    <>
                        <Point>적립금 <PointText>{numberWithCommas(value.prd_earn_price)}</PointText>원</Point>
                    </>
                    <>
                        <SoldOutText><IconSoldOut src={IcoSoldOut}/> 품절</SoldOutText>
                        <TotalPrice><TotalPriceText>{numberWithCommas(Math.round(value.prd_price - value.prd_price*value.prd_sale_rate/100))}</TotalPriceText>원</TotalPrice>
                    </>
                </PurchaseSlideTextbox>
            </ProductWrap>
            }
        </Container>
  );
}

const Container = styled.div`
    width: 100%;
`
const ProductWrap = styled.div`
    width: 100%;
`
const PurchaseSlideImgWrap = styled.div`
    position: relative;
    width: 100%;
    height: calc(100vw*(176/414));
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
`
const PurchaseSlideImg = styled.img`
    display: block;
    width: 100%; height: 100%;
    object-fit: cover;
`
const PurchaseSlideTextbox = styled.div`
    width: 100%; height: calc(100vw*(85/414));
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: calc(100vw*(7/414)) calc(100vw*(12/414)) calc(100vw*(8/414));
    border-top: calc(100vw*(1/414)) solid #e4e4e4;
`
const PurchaseName = styled.p`
    width: 100%;
    font-size: calc(100vw*(13/414));
    margin-bottom: calc(100vw*(3/414));
`
const PurchaseText = styled.p`
    width: 100%;
    font-size: calc(100vw*(11/414));
`
const Price = styled.p`
    font-size: calc(100vw*(12/414));
    color: #707070;
    text-decoration: line-through;
    text-align: right;
`
const Point = styled.p`
    width: 50%;
    font-size: calc(100vw*(11/414));
    color: #292929;
    font-weight: bold;
    margin-bottom: calc(100vw*(2/414));
`
const PointText = styled.span`
    color: #292929;
    font-weight: 900;
`
const Discount = styled.span`
    width: 50%;
    font-size: calc(100vw*(9/414));
    color: #e20000;
    margin-right: calc(100vw*(3/414));
`
const DiscountRate = styled.p`
    font-size: calc(100vw*(13/414));
    color: #e20000;
    font-weight: 900;
`
const TotalPrice = styled.p`
    display: flex;
    align-items: center;
    font-size: calc(100vw*(14/414));
    text-align: right;
`
const TotalPriceText = styled.span`
    font-size: calc(100vw*(20/414));
    font-weight: 900;
    letter-spacing: calc(100vw*(-1/414));
    margin-top: calc(100vw*(3/414));
`

/* Sold out */
const SoldOutText = styled.p`
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    text-align: right;
    margin-bottom: calc(100vw*(3/414));
    margin-top: calc(100vw*(-3/414));
`
const IconSoldOut = styled.img`
    width: calc(100vw*(15/414));
    margin-right: calc(100vw*(5/414));
`
const SoldOutImgWrap = styled.div`
    position: absolute; top: 0; left: 0;
    width: 100%; height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(1,1,1,0.5);
`
const SoldOutImgText = styled.p`
    font-size: calc(100vw*(22/414));
    color: #fff;
    font-weight: bold;
`
