//react
import {Link} from "react-router-dom";

//css
import styled from "styled-components"

//img
import IconSetBtn from "../../img/ico_setting.png"


export default function MypageMainTitle() {
    
  return (
        <Container>
            <MypageMainTitleWrap>
                <div>
                    <MypageMainTitleText>USERNAME</MypageMainTitleText>
                </div>
                <SetBtnWrap>
                    <Link to={"/mypageSet"}>
                        <SetBtn></SetBtn>
                    </Link>
                </SetBtnWrap>
            </MypageMainTitleWrap>
        </Container>
  );
}

//css
const Container = styled.div` 
    width: 100%;
`
const MypageMainTitleWrap = styled.div` 
    width: 100%; height: calc(100vw*(65/414));
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #fff;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
`
const SetBtnWrap = styled.div`
    position: absolute; right: calc(100vw*(15/414));
    width: calc(100vw*(45/414)); height: calc(100vw*(45/414));
`
const SetBtn = styled.button`
    width: calc(100vw*(45/414)); height: calc(100vw*(45/414));
    background: url(${IconSetBtn}) no-repeat center/calc(100vw*(24/414));
`
const MypageMainTitleText = styled.h2`
    width: 100%;
    font-size: calc(100vw*(20/414));
    font-weight: 900;
`