import { createAction, handleActions } from 'redux-actions';
import produce from 'immer';

// 액션 타입을 정의해줍니다.
const UPDATE_USERID = 'uesrInfoAction/updatePrdIdAction';
const UPDATE_ADRID = 'uesrInfoAction/updateAdrdIdAction';

// 액션 생성 함수를 만듭니다.
export const updateUserIdData = createAction(UPDATE_USERID);
export const updateAdrIdData = createAction(UPDATE_ADRID);

// 모듈의 초기 상태를 정의합니다.
const initialState = {
    userId:{
        mem_id:"",
        mem_name:"", 
        mem_password:"",
        mem_username:"",
        mem_email:"",
        mem_phone:"",
    },
    ardId:{
        adr_id:"",
        adr_zipcode:"", 
        adr_name:"",
        adr_address1:"",
        adr_address2:"",
        adr_phone:"",
    },
}

// immer 를 사용하여 값을 수정하는 리듀서입니다.
export default handleActions({
  [UPDATE_USERID]: (state, action) => {
    return produce(state, draft => {
      draft.userId = action.payload.userId ? action.payload.userId : draft.userId;
    });
  },
  [UPDATE_ADRID]: (state, action) => {
    return produce(state, draft => {
      draft.ardId = action.payload.ardId ? action.payload.ardId : draft.ardId;
    });
  },
}, initialState);