//react
import {Link} from "react-router-dom";
import {useState, useEffect} from 'react';

//css
import styled from "styled-components"


export default function ShopPayCoupon() {

    return (
        <Container>
            <ShopPayPointWrap>
                <ShopPayInfoTitleWrap>
                    <ShopPayInfoTitle>쿠폰</ShopPayInfoTitle>
                </ShopPayInfoTitleWrap>
                <ShopPayInfoInner>
                    <ShopPaySelectBox>
                        <CouponList name="" id="">
                            <option value="">쿠폰1</option>
                            <option value="">쿠폰2</option>
                            <option value="">쿠폰3</option>
                        </CouponList>
                    </ShopPaySelectBox>
                    <ShopPayInfoBox>
                        <ShopPayPointBtnWrap>
                            <ShopPayInfoBoxTitle>상품가격</ShopPayInfoBoxTitle>
                        </ShopPayPointBtnWrap>
                        <ShopPayPointBtnWrap>
                                <ShopPayInfoBoxText>68,000원</ShopPayInfoBoxText>
                        </ShopPayPointBtnWrap>
                    </ShopPayInfoBox>
                    <ShopPayInfoBox>
                        <ShopPayPointBtnWrap>
                            <ShopPayInfoBoxTitle>쿠폰할인</ShopPayInfoBoxTitle>
                        </ShopPayPointBtnWrap>
                        <ShopPayPointBtnWrap>
                                <ShopPayInfoBoxText>-1,000원</ShopPayInfoBoxText>
                        </ShopPayPointBtnWrap>
                    </ShopPayInfoBox>
                </ShopPayInfoInner>
            </ShopPayPointWrap>
        </Container>
    );
}

//css
const Container = styled.div` 
    position: relative;
    width: 100%;
    background: #fff;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    z-index: 10;
    margin-top: calc(100vw*(10/414));
`
const ShopPayPointWrap = styled.div`
    width: 100%;
    padding-top: calc(100vw*(20/414));
    padding: calc(100vw*(24/414)) calc(100vw*(36/414)) calc(100vw*(28/414));
`
const ShopPayInfoTitleWrap = styled.div`
    width: 100%;
`
const ShopPayInfoTitle = styled.p`
    font-size: calc(100vw*(18/414));
    font-weight: bold;
    margin-bottom: calc(100vw*(5/414));
`
const ShopPayInfoInner = styled.div`
    position: relative;
    width: 100%;
`
const ShopPaySelectBox = styled.div`
    display: flex;
    align-items: baseline;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: calc(100vw*(12/414)) 0;
`
const ShopPayInfoBox = styled(ShopPaySelectBox)`
    padding: calc(100vw*(8/414)) 0;
`
const ShopPayInfoBoxTitle = styled.p`
    font-size: calc(100vw*(13/414));
    font-weight: bold;
`
const ShopPayInfoBoxText = styled.p`
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    word-break: keep-all;
    text-align: right;
`
const ShopPayPointBtnWrap = styled.div`
    display: flex;
    align-items: center;
`
const RemainingPointWrap = styled.div`
    width: 100%;
`
const RemainingPointText = styled.p`
    width: 100%;
    text-align: right;
    padding: calc(100vw*(5/414)) 0;
    font-size: calc(100vw*(13/414));
    font-weight: bold;
`
const CouponList = styled.select`
    width: 100%; height: calc(100vw*(32/414));
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(3/414));
    margin-right: calc(100vw*(6/414));
    text-indent: calc(100vw*(5/414));
`


