//react
import {Link} from "react-router-dom";
import {useState, useEffect} from 'react';

//css
import styled from "styled-components"


const deleteOptionList = [
    {id:0, title:"상품 불만족", active:false},
    {id:0, title:"사생활 기록 삭제 우려", active:false},
    {id:0, title:"개인정보 누출 우려", active:false},
    {id:0, title:"이용할 기능 부족", active:false},
    {id:0, title:"서비스 불만족", active:false},
    {id:0, title:"이벤트 등의 목적으로 한시사용", active:false},
    {id:0, title:"기타", active:false}
]

export default function WithdrawalForm() {

    const [next , setNext] = useState(false);
    const [password, setPassword] = useState("");
    const [repassword, setRepassword] = useState("");
    const [deleteList, setDeleteList] = useState(deleteOptionList);

    const passwordTextChange = (e) => { setPassword(e.target.value); }
    const repasswordTextChange = (e) => { setRepassword(e.target.value); }
    const checkpasswordVaildate = () =>{ return (password == repassword); }

    useEffect(()=>{
        if( checkpasswordVaildate())
            setNext(true);
        else
            setNext(false);
    },[password,repassword])

    
    const getErrorMessage = () =>{
        if(repassword.length == 0)
            return "";
        else if(checkpasswordVaildate() == false)
            return "비밀번호가 일치하지 않습니다.";
    }

    const deleteOptionChange = (e,index) =>{
        var list = JSON.parse(JSON.stringify(deleteList));
        list[index].active = e.target.checked;
        setDeleteList(list);
        // setDeleteList(e.target.checked);
    }
    

    return (
            <Container>
                <WithdrawalWrap>
                    <WithdrawalInfoWrap>
                        <IDInputWrap>
                            <IDText>아이디</IDText>
                            <IDInput type="text"/>
                        </IDInputWrap>
                        <WithdrawalPWWrap>
                            <PWInputWrap>
                                <PWText>비밀번호</PWText>
                                <PWInput type="password" name="" id="" value={password} onChange={passwordTextChange}/>
                            </PWInputWrap>
                            <PWInputWrap>
                                <PWText>비밀번호<br/>확인</PWText>
                                <PWInput type="password" name="" id="" value={repassword} onChange={repasswordTextChange}/>
                            </PWInputWrap>
                            <ErrorMessageWrap>
                                <ErrorMessage>{getErrorMessage()}</ErrorMessage>
                            </ErrorMessageWrap>
                        </WithdrawalPWWrap>
                    </WithdrawalInfoWrap>
                    <div>
                        <Line></Line>
                        <WithdrawalTitleWrap>
                                <WithdrawalTitle>탈퇴사유</WithdrawalTitle>
                                <WithdrawalText>탈퇴사유를 선택해주세요. (복수선택가능)</WithdrawalText>
                            </WithdrawalTitleWrap>
                            <WithdrawalChkList>
                            {
                                deleteList.map((value,index)=>{
                                    return (
                                        <WithdrawalChkWrap>
                                            <ChkBox type="checkbox" name="" id={index + "_delete_option"} checked={value.active} onChange={(e)=>{deleteOptionChange(e,index)}} required/>
                                            <ChkBoxLabel htmlFor={index + "_delete_option"}></ChkBoxLabel>
                                            <ChkBoxText>{value.title}</ChkBoxText>
                                        </WithdrawalChkWrap>
                                    )
                                })
                            }
                        </WithdrawalChkList>
                    </div>
                </WithdrawalWrap>
                <WithdrawalBtnWrap>
                    <Link to={"/withdrawalResult"}>
                        <WithdrawalBtn signUpBtnOn={next}>완료</WithdrawalBtn>
                    </Link>
                </WithdrawalBtnWrap>
            </Container>
    );
}

//css
const Container = styled.div` 
    position: relative;
    width: 100%;
    background: #fff;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    z-index: 10;
    margin-top: calc(100vw*(12/414));
    padding-bottom: calc(100vw*(100/414));
`
const WithdrawalWrap = styled.div`
    width: 100%; min-height: calc(100vh - calc(100vw*(250/414)));
    padding-top: calc(100vw*(20/414));
    padding: calc(100vw*(25/414)) calc(100vw*(11/414)) 0;
`
const WithdrawalInfoWrap = styled.div`
    width: 100%;
    padding: 0 calc(100vw*(22/414));
`
const IDInputWrap = styled.div`
    display: flex;
    align-items: center;
    width: 100%;
    margin: calc(100vw*(20/414)) 0;
`
const PWInputWrap = styled(IDInputWrap)`
    margin: 0;
`
const IDText = styled.p`
    width: calc(100% - calc(100vw*(274/414)));
    font-size: calc(100vw*(15/414));
    font-weight: bold;
`
const PWText = styled(IDText)`
`
const IDInput = styled.input`
    width: calc(100vw*(274/414)); height: calc(100vw*(32/414));
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(3/414));
    margin: calc(100vw*(3/414)) 0;
    text-indent: calc(100vw*(10/414));
    font-size: calc(100vw*(15/414));
    color: #4b4b4b;
    font-weight: bold;
`
const PWInput = styled(IDInput)`
`
const WithdrawalPWWrap = styled.div`
    width: 100%;
    margin: calc(100vw*(20/414)) 0;
`
const WithdrawalBtnWrap = styled.div`
    width: 100%;
    text-align: center;
    padding: 0 calc(100vw*(16/414));
`
const WithdrawalBtn = styled.button`
    width: 100%;
    font-size: calc(100vw*(20/414));
    font-weight: 900;
    color: #4b4b4b;
    padding: calc(100vw*(16/414)) 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.13);
    border: calc(100vw*(2/414)) solid #efefef;
    border-radius: calc(100vw*(13/414));
`
const ErrorMessageWrap = styled.div`
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: calc(100vw*(20/414));
`
const ErrorMessage = styled.p`
    font-size: calc(100vw*(10/414));
    font-weight: bold;
    color: #f0505d;
    text-align: right;
`

/* checkbox */
const Line = styled.div` 
    width: 100%; height: calc(100vw*(1/414));
    background: #e4e4e4;
    margin-top: calc(100vw*(15/414));
`
const WithdrawalTitleWrap = styled.div`
    display: flex;
    align-items: center;
    padding: calc(100vw*(25/414)) 0 calc(100vw*(12/414)) calc(100vw*(18/414));
` 
const WithdrawalTitle = styled.p` 
    font-size: calc(100vw*(18/414));
    font-weight: bold;
`
const WithdrawalText = styled.span` 
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    color: #6b6b6b;
    margin-left: calc(100vw*(8/414));
`

const WithdrawalChkList = styled.div`
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding: calc(100vw*(10/414)) calc(100vw*(20/414));
`
const WithdrawalChkWrap = styled.div`
    position: relative;
    display: flex;
    align-items: baseline;
    width: 50%; max-height: calc(100vw*(36/414));
    padding: calc(100vw*(10/414)) 0;
`
const ChkBox = styled.input`
    &:checked + label::after {
        content: '';
        position: absolute; top: calc(100vw*(14/414)); left: calc(100vw*(5/414));
        width: calc(100vw*(8/414)); height: calc(100vw*(8/414));
        background: #ed3342;
        border-radius: calc(100vw*(2/414));
    }
`
const ChkBoxLabel = styled.label`
    &&::before {
        content: '';
        position: absolute; top: calc(100vw*(9/414)); left: 0;
        width: calc(100vw*(16/414)); height: calc(100vw*(16/414));
        text-align: center;
        background: #fafafa;
        border: calc(100vw*(1/414)) solid #ed3342;
        border-radius: calc(100vw*(4/414));
    }
`
const ChkBoxText = styled.span`
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    color: #707070;
    padding-left: calc(100vw*(35/414));
`