//react
import {Link} from "react-router-dom";
import React ,{useState,useEffect} from 'react';

//css
import styled from "styled-components"

export default function DateFilter({setDateTypeBtn, updateBtnon}) {
    const [filterColor,setfilterColor] = useState(0);

    return(
        <Container>
            <DateFilterWrap>
                <DateFilterInner>
                    <DateFilterList>
                        <DateFilterText onClick={() => setfilterColor(1)} active={filterColor == 1}>일주일</DateFilterText>
                        <DateFilterText onClick={() => setfilterColor(2)} active={filterColor == 2}>1개월</DateFilterText>
                        <DateFilterText onClick={() => setfilterColor(3)} active={filterColor == 3}>3개월</DateFilterText>
                        <DateFilterText onClick={() => setfilterColor(4)} active={filterColor == 4}>6개월</DateFilterText>
                    </DateFilterList>
                    <DateFilterBtn onClick={() => updateBtnon(filterColor)}>조회</DateFilterBtn>
                </DateFilterInner>
            </DateFilterWrap>
        </Container>

    );
}

const Container = styled.div`
    width: 100%;
`
const DateFilterWrap = styled.div`
    width: 100%;
    padding-top: calc(100vw*(20/414));
`
const DateFilterInner = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding: calc(100vw*(16/414)) calc(100vw*(20/414));
    background: #f7f7f7;
`
const DateFilterList = styled.ul`
    display: flex;
    align-items: center;
`
const DateFilterText = styled.li`
    position: relative;
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    padding: calc(100vw*(8/414)) calc(100vw*(15/414));
    text-align: center;
    &&::after {
        content: "";
        position: absolute; right: 0;
        display: inline-block;
        width: calc(100vw*(1/414)); height: calc(100vw*(16/414));
        background: #e4e4e4;
    }
    &:last-child::after {display: none;}
    
    ${({active})=>{
        return active ? 
        `
            color: #ED2939;
        `
        :
        ``
    }}
`
const DateFilterBtn = styled.button`
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    padding: calc(100vw*(8/414)) calc(100vw*(20/414));
    background: #fff;
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(6/414));
`