//react
import {Link , useHistory} from "react-router-dom";
import {useState} from "react";

//css
import styled from "styled-components"


export default function CartModal({cartModal ,setCartModal, setPurchaseModal}) {

    if(cartModal == false)
        return null;

    return(
        <Container>
            <ModalWrap>
                <ModalInner>
                    <ModalText>이미 선택된 옵션 입니다.<br/>장바구니로 이동하시겠습니까?</ModalText>
                    <ModalBtnWrap>
                        <ModalBtn onClick={()=>{setCartModal(false)}}>취소</ModalBtn>
                        <ModalLinkBtn onClick={()=>{setCartModal(false); setPurchaseModal(true);}}>이동</ModalLinkBtn>
                    </ModalBtnWrap>
                </ModalInner>
            </ModalWrap>
        </Container>
    );
}

/* cart title */
const Container = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed; bottom: 0;
    width: 100%; height: 100vh;
    z-index: 1500;
    background: rgba(1,1,1,0.4);
`
const ModalWrap = styled.div`
    width: calc(100vw*(389/414));
    background: #fff;
    border-radius: 15px;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    padding: 0 calc(100vw*(8/414));
`
const ModalInner = styled.div`
    padding: calc(100vw*(5/414)) 0;
    text-align: center;
`
const ModalText = styled.p`
    font-size: calc(100vw*(15/414));
    font-weight: 900;
    color: #4b4b4b;
    padding: calc(100vw*(30/414)) 0;
    line-height: 1.5;
`
const ModalBtnWrap = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 calc(100vw*(16/414));
    margin-bottom: calc(100vw*(10/414));
`
const ModalBtn = styled.button`
    width: 50%;
    font-size: calc(100vw*(15/414));
    font-weight: 900;
    padding: calc(100vw*(10/414)) 0;
    margin: calc(100vw*(15/414)) calc(100vw*(15/414));
    line-height: 1.5;
    background: #b4b4b4;
    color: #fff;
    border: calc(100vw*(3/414)) solid #a5a5a5;
    border-radius: calc(100vw*(6/414));
`
const ModalLinkBtn = styled(ModalBtn)`
    background: #ed2939;
    border-color: #bc343f;
`