//react
import {Link} from "react-router-dom";
import {useState, useEffect} from 'react';

//css
import styled from "styled-components"

export default function AddressLayout({value,adrTypeClick}) {
    
    return (
        <Container>
            <ShopPayAddressList activeStyle={value.adr_type == 1}>
                <ShopPayInfoWrap>
                    <ShopPayInfoInner>
                        <ShopPayInfoBox>
                            <ShopPayInfoBoxTitle>이름</ShopPayInfoBoxTitle>
                            <ShopPayInfoBoxText>{value.adr_name}</ShopPayInfoBoxText>
                        </ShopPayInfoBox>
                        <ShopPayInfoBox>
                            <ShopPayInfoBoxTitle>주소</ShopPayInfoBoxTitle>
                            <ShopPayInfoBoxText>{value.adr_address1} {value.adr_address2}</ShopPayInfoBoxText>
                        </ShopPayInfoBox>
                        <ShopPayInfoBox>
                            <ShopPayInfoBoxTitle>연락처</ShopPayInfoBoxTitle>
                            <ShopPayInfoBoxText>{value.adr_phone}</ShopPayInfoBoxText>
                        </ShopPayInfoBox>
                        <ShopPayInfoBox>
                            <ShopPayInfoBoxText>{value.adr_memo}</ShopPayInfoBoxText>
                        </ShopPayInfoBox>
                    </ShopPayInfoInner>
                </ShopPayInfoWrap>
                <AddressBtnWrap>
                    <AddressBtn>
                        <Link to={`/shop/pay/editAddress/${value.adr_id}`}>수정</Link>
                    </AddressBtn>
                    <AddressBtnRed onClick={ () => adrTypeClick(value.adr_id)}>선택</AddressBtnRed>
                </AddressBtnWrap>
            </ShopPayAddressList>
        </Container>
    );
}

//css
const Container = styled.div` 
    width: 100%;
    background: #f7f7f7;
`
const ShopPayAddressList = styled.div`
    width: 100%;
    background: #fff;
    border-radius: calc(100vw*(10/414));
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    margin-top: calc(100vw*(8/414));
    ${({activeStyle})=>{
        return activeStyle ?
        `
            border: calc(100vw*(2/414)) solid #ed2939;
        `
        :
        ``
    }}
`
const ShopPayInfoWrap = styled.div`
    width: 100%;
    padding-top: calc(100vw*(20/414));
    padding: calc(100vw*(22/414)) calc(100vw*(45/414)) calc(100vw*(14/414));
`
const ShopPayInfoInner = styled.div`
    position: relative;
    width: 100%;
`
const ShopPayInfoBox = styled.div`
    display: flex;
    align-items: baseline;
    padding: calc(100vw*(6/414)) 0;
`
const ShopPayInfoBoxTitle = styled.p`
    width: calc(100vw*(68/414));
    font-size: calc(100vw*(13/414));
    font-weight: bold;
`
const ShopPayInfoBoxText = styled.p`
    width: calc(100% - calc(100vw*(68/414)));
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    word-break: keep-all;
`
const AddressBtnWrap = styled.div`
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 0 calc(100vw*(25/414)) calc(100vw*(15/414));
`
const AddressBtn = styled.button`
    width: calc(100vw*(76/414));
    padding: calc(100vw*(7/414)) 0;
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    color: #707070;
    background: #f7f7f7;
    border-radius: calc(100vw*(7/414));
    border: calc(100vw*(2/414)) solid #979797;
    margin: 0 calc(100vw*(4/414));
`
const AddressBtnRed = styled(AddressBtn)`
    background: #ed2939;
    border-color: #bc343f;
    color: #fff;
`