//react
import {Link} from "react-router-dom";

//css
import styled from "styled-components"

//img
import IconSetBtn from "../../img/ico_setting.png"
import IconMore from "../../img/ico_read_more.png"


export default function MypageMainPoint({rank}) {
    
  return (
        <Container>
            <MypageMainPointWrap>
                <PointMoreWrap>
                    {
                        rank ?
                        <Link to={"/point"}>
                            <PointMoreText>적립금 내역 바로가기 <IcoMore src={IconMore} alt="point more page"/></PointMoreText>
                        </Link>
                        :
                        null
                    }
                </PointMoreWrap>
                <MyPointWrap>
                    <MyPointLine>
                        <MyPointText>보유 적립금 : <MyPointTextNumber>500</MyPointTextNumber>ponts</MyPointText>
                    </MyPointLine>
                </MyPointWrap>
                <LastPointWrap>
                    <LastPointText>소멸예정 적립금 : <LastPointNumber>0</LastPointNumber><LastPointNumberText>points</LastPointNumberText></LastPointText>
                </LastPointWrap>
            </MypageMainPointWrap>
        </Container>
  );
}

//css
const Container = styled.div` 
    width: 100%;
    padding-top: calc(100vw*(12/414));
`
const MypageMainPointWrap = styled.div` 
    width: 100%;
    background: #fff;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    border: calc(100vw*(3/414)) solid #f7f7f7;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    padding: calc(100vw*(12/414)) 0;
`
const PointMoreWrap = styled.div` 
    width: 100%; height: calc(100vw*(13/414));
    display: flex;
    align-items: center;
    justify-content: flex-end;
`
const PointMoreText = styled.p` 
    font-size: calc(100vw*(12/414));
    font-weight: bold;
    margin-right: calc(100vw*(22/414));
`
const IcoMore = styled.img` 
    width: calc(100vw*(5/414));
    margin-left: calc(100vw*(3/414));
`
const MyPointWrap = styled.div` 
    width: 100%;
`
const MyPointLine = styled.div` 
    width: 100%;
    background: #ed2939;
    padding: calc(100vw*(6/414)) 0;
    margin: calc(100vw*(10/414)) 0;
`
const MyPointText = styled.p` 
    width: 100%;
    font-size: calc(100vw*(20/414));
    text-align: center;
    font-weight: 900;
    color: #fff;
    border-top: 1px dashed #fff;
    border-bottom: 1px dashed #fff;
    padding: calc(100vw*(12/414)) 0;
`
const MyPointTextNumber = styled.span`
    font-size: calc(100vw*(20/414));
    font-weight: 900;
    color: #fff;
    background: #ed2939;
    padding: calc(100vw*(6/414)) 0;
    margin-left: calc(100vw*(20/414));
    margin-right: calc(100vw*(5/414));
`
const LastPointWrap = styled.div` 
    width: 100%;
`
const LastPointText = styled.p` 
    width: 100%;
    font-size: calc(100vw*(13/414));
    text-align: center;
    font-weight: bold;
    color: #4b4b4b;
`
const LastPointNumber = styled.span` 
    font-size: calc(100vw*(13/414));
    text-align: center;
    font-weight: 900;
    color: #4b4b4b;
    margin-left: calc(100vw*(10/414));
`
const LastPointNumberText = styled.span` 
    font-size: calc(100vw*(13/414));
    text-align: center;
    font-weight: 900;
    color: #4b4b4b;
    margin-left: calc(100vw*(3/414));
`