const ip = "http://127.0.0.1:8089/";

let jwtToken = ``;

function getCookie(cname) {
  var name = cname + "=";
  var decodedCookie = decodeURIComponent(document.cookie);
  var ca = decodedCookie.split(';');
  for(var i = 0; i <ca.length; i++) {
    var c = ca[i];
    while (c.charAt(0) == ' ') {
      c = c.substring(1);
    }
    if (c.indexOf(name) == 0) {
      return c.substring(name.length, c.length);
    }
  }
  return "";
}

const updateToken = () => {
  jwtToken = getCookie("jwtT");
}
updateToken();

const api = {
    connectFetchController : async (path,method,body,callBack,errorCallBack) =>{

     return fetch(`${ip}${path}`, {
        credentials:'include',
        method: method,
        headers: {
          'Authorization' : `Bearer ${jwtToken}`,
          'Connection' : 'keep-alive',
      },
        body:body?body:null,
      }).then(function(res) {
        return res.json();
      }).then(function(data) {
        if(callBack)
          callBack(data);
        return data;
      }).catch(function(e){
        console.log(e);
        if(errorCallBack)
          errorCallBack(e);
      });
    },
    loginFetchController : async (path,method,body,callBack,errorCallBack) =>{

      return fetch(`${ip}${path}`, {
         credentials:'include',
         method: method,
         headers: {
           'Content-Type': 'application/json',
           'Authorization' : `Bearer ${jwtToken}`,
           'Accept-Encoding' : 'gzip, deflate, br',
           'Connection' : 'keep-alive',
       },
         body:body?body:null,
       }).then(function(res) {
         return res.json();
       }).then(function(data) {
         if(callBack)
           callBack(data);
         return data;
       }).catch(function(e){
         console.log(e);
         if(errorCallBack)
           errorCallBack(e);
       });
     },
    setToken : (token) => {
      var d = new Date();
      d.setTime(d.getTime() + (1000 * 24 * 60 * 60 * 1000));
      var expires = "expires="+d.toUTCString();
      document.cookie = "jwtT" + "=" + token + ";" + expires + ";path=/";
      updateToken();
    }
}


export default api;