//react
import {useState, useEffect} from 'react';
import { Link,useHistory } from "react-router-dom";

//css
import styled from "styled-components"

//page
import serverController from '../../../server/serverController';

export default function ShopPayEditAddress({setAdressShow, adrZipcode, setAdrZipcode, adrAddress, setAdrAddress, adr_id}) {
    const [myAdrInfo,setMyAdrInfo] = useState([]);
    const [next,setNext] = useState(false);
    
    const [adrName,setAdrName] = useState("");
    const [adrAddressDetail,setAdrAddressDetail] = useState("");
    const [adrPhone,setAdrPhone] = useState("");
    const [adrMemo,setAdrMemo] = useState("");

    const adrNameChange = (e) =>{ setAdrName(e.target.value); }
    const adrAddressDetailChange = (e) =>{ setAdrAddressDetail(e.target.value); }
    const adrPhoneChange = (e) =>{ setAdrPhone(e.target.value); }
    const adrMemoChange = (e) =>{ setAdrMemo(e.target.value); }

    const checkNameVaildate = () =>{ return adrName.length != 0; };
    const checkZipcodeVaildate = () =>{ return adrZipcode.length != 0; }
    const checkAddress1Vaildate = () =>{ return adrAddress.length != 0 }
    const checkAddress2Vaildate = () =>{ return adrAddressDetail.length != 0 }
    const checkPhoneVaildate = () =>{ return adrPhone.length != 0 }
    const history = useHistory();

    const openPostCode = () => {
        setAdressShow(true);
    }
    const closePostCode = () => {
        setAdressShow(false);
    }

    useEffect(()=>{
        serverController.connectFetchController(`user/target_address?adr_id=${adr_id}`,"GET",null,function(res){
        if(res.result == 1) {
            setMyAdrInfo(res.info);
            setAdrName(res.info.adr_name);
            setAdrZipcode(res.info.adr_zipcode);
            setAdrAddress(res.info.adr_address1);
            setAdrAddressDetail(res.info.adr_address2);
            setAdrPhone(res.info.adr_phone);
            setAdrMemo(res.info.adr_memo);
        }});
    },[]);

    useEffect(()=>{
        if(checkNameVaildate() && checkZipcodeVaildate() && checkAddress1Vaildate() && checkAddress2Vaildate() && checkPhoneVaildate())
            setNext(true);
        else
            setNext(false);
    },[adrName,adrZipcode,adrAddress,adrAddressDetail,adrPhone])

    const adrBtnClick = () =>{
        if(next == true){
            const formData = new FormData();
            formData.append('adr_id',adr_id);
            formData.append('adr_name',adrName);
            formData.append('adr_zipcode',adrZipcode);
            formData.append('adr_address1',adrAddress);
            formData.append('adr_address2',adrAddressDetail);
            formData.append('adr_phone',adrPhone);
            formData.append('adr_memo',adrMemo);
            serverController.connectFetchController('user/update_address',"PUT",formData,function(res){
                if(res.result == 1) {
                    return history.push("/shop/pay/address");
                } else {
                    alert("배송지 수정을 실패했습니다.");
                }
            });
        }
    }
    const deleteBtnClick = () =>{
        serverController.connectFetchController(`user/delete_address?adr_id=${adr_id}`,"DELETE",null,function(res){
            if(res.result == 1) {
                return history.push("/shop/pay/address");
            } else {
                alert("배송지 삭제를 실패했습니다.");
            }
        });
    }
    
  return (
        <Container>
            <AddressWrap>
                <AddressListWrap>
                    <SetList>
                        <SetListTitle>이름 :</SetListTitle>
                        <SetListText type="text" placeholder={myAdrInfo.adr_name} value={adrName} onChange={adrNameChange}/>
                    </SetList>
                    <SetList>
                        <SetListTitle>주소 :</SetListTitle>
                        <SetListAddressWrap>
                            <AddresssearchWrap>
                                <AddresssearchInput type="text" placeholder={myAdrInfo.adr_zipcode} value={adrZipcode} readonly/>
                                <AddresssearchBtn onClick={openPostCode}>주소검색</AddresssearchBtn>
                            </AddresssearchWrap>
                            <SetListAddress type="text" placeholder={myAdrInfo.adr_address1} value={adrAddress} readonly/>
                            <SetListAddress type="text" placeholder={myAdrInfo.adr_address2} value={adrAddressDetail} onChange={adrAddressDetailChange}/>
                        </SetListAddressWrap>
                    </SetList>
                    <SetList>
                        <SetListTitle>연락처 :</SetListTitle>
                        <SetListText type="number" placeholder={myAdrInfo.adr_phone} value={adrPhone} onChange={adrPhoneChange}/>
                    </SetList>
                    <SetList>
                        <SetListTitle>배송요청사항 :</SetListTitle>
                        <SetListTextarea placeholder={myAdrInfo.adr_memo} value={adrMemo} onChange={adrMemoChange}></SetListTextarea>
                    </SetList>
                    <AddressBtnWrap>
                        <AddressBtn onClick={deleteBtnClick}>삭제</AddressBtn>
                        <AddressBtn adrBtnOn={next} onClick={adrBtnClick}>수정</AddressBtn>
                    </AddressBtnWrap>
                </AddressListWrap>
            </AddressWrap>
        </Container>
  );
}

//css
const Container = styled.div` 
    width: 100%;
    padding: calc(100vw*(25/414)) calc(100vw*(15/414));
`
const AddressWrap = styled.div` 
    width: 100%;
`
const AddressListWrap = styled.div` 
    width: 100%;
    padding: calc(100vw*(50/414)) calc(100vw*(30/414)) calc(100vw*(80/414));
    background: #fff;
    border: calc(100vw*(2/414)) solid #efefef;
    border-radius: calc(100vw*(17/414));
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
`
const SetList = styled.div`
    margin: calc(100vw*(20/414)) 0;
`
const SetListTitle = styled.p`
    width: 100%;
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    margin-bottom: calc(100vw*(8/414));
`
const SetListText = styled.input`
    width: 100%; height: calc(100vw*(40/414));
    font-size: calc(100vw*(14/414));
    font-weight: bold;
    text-align: left;
    text-indent: calc(100vw*(15/414));
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(3/414));
    color: #4b4b4b;
`
const SetListTextarea = styled.textarea`
    width: 100%; height: calc(100vw*(115/414));
    font-size: calc(100vw*(14/414));
    font-weight: bold;
    text-align: left;
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(3/414));
    color: #4b4b4b;
    padding: calc(100vw*(9/414)) calc(100vw*(18/414));
`
const SetListAddressWrap = styled.div`
    width: 100%;
`
const SetListAddress = styled.input`
    width: 100%; height: calc(100vw*(40/414));
    font-size: calc(100vw*(14/414));
    font-weight: bold;
    text-align: left;
    text-indent: calc(100vw*(15/414));
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(3/414));
    color: #4b4b4b;
    margin: calc(100vw*(3/414)) 0;
`
const AddresssearchWrap = styled.div`
    display: flex;
    align-items: center;
`
const AddresssearchInput = styled.input`
    width: calc(100% - calc(100vw*(100/414))); height: calc(100vw*(40/414));
    font-size: calc(100vw*(14/414));
    font-weight: bold;
    text-align: left;
    text-indent: calc(100vw*(15/414));
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(3/414));
    color: #4b4b4b;
    margin: calc(100vw*(3/414)) 0;
`
const AddresssearchBtn = styled.button`
    width: calc(100vw*(100/414)); height: calc(100vw*(40/414));
    color: #fff;
    background: #646464;
    border: calc(100vw*(2/414)) solid #979797;
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    margin-left: calc(100vw*(10/414));
    border-radius: calc(100vw*(3/414));
`
const AddressBtnWrap = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: calc(100vw*(40/414));
`
const AddressBtn = styled.button`
    width: calc(100vw*(130/414)); height: calc(100vw*(48/414));
    color: #fff;
    background: #646464;
    border: calc(100vw*(2/414)) solid #979797;
    border-radius: calc(100vw*(3/414));
    font-size: calc(100vw*(18/414));
    font-weight: bold;
    margin: 0 calc(100vw*(6/414));
    ${({adrBtnOn})=>{
        return adrBtnOn ? 
        `
            background: #ed2939;
            border-color: #bc343f;
        `
        :
        ``
    }}
`