//react
import {Link} from "react-router-dom";
import {useState, useEffect} from 'react';

//css
import styled from "styled-components"


export default function PasswordEdit() {

    const [next , setNext] = useState(false);
    const [password, setPassword] = useState("");
    const [repassword, setRepassword] = useState("");

    const passwordTextChange = (e) => { setPassword(e.target.value); }
    const repasswordTextChange = (e) => { setRepassword(e.target.value); }
    const checkpasswordVaildate = () =>{ return (password == repassword); }

    useEffect(()=>{
        if( checkpasswordVaildate())
            setNext(true);
        else
            setNext(false);
    },[password,repassword])

    const getErrorMessage = () =>{
        if(repassword.length == 0)
            return "";
        else if(checkpasswordVaildate() == false)
            return "비밀번호가 일치하지 않습니다.";
    }
    
  return (
        <Container>
            <PasswordEditWrap>
                <PasswordEditList>
                    <PasswordEditText type="password" placeholder={"현재 비밀번호를 입력하세요."}/>
                    <PasswordEditText type="password" placeholder={"새 비밀번호를 입력하세요."} value={password} onChange={passwordTextChange}/>
                    <PasswordEditText type="password" placeholder={"새 비밀번호를 입력하세요."} value={repassword} onChange={repasswordTextChange}/>
                    <ErrorMessage>{getErrorMessage()}</ErrorMessage>
                </PasswordEditList>
            </PasswordEditWrap>
            <PasswordBtnWrap>
                <Link to={"/passwordResult"}>
                    <PasswordBtn signUpBtnOn={next}>비밀번호 변경</PasswordBtn>
                </Link>
            </PasswordBtnWrap>
        </Container>
  );
}

//css
const Container = styled.div` 
    position: relative;
    width: 100%;
    z-index: 100;
    background: #fff;
`
const PasswordEditWrap = styled.div` 
    width: 100%; min-height: calc(100vh - calc(100vw*(240/414)));
`
const PasswordEditList = styled.div` 
    padding: calc(100vw*(50/414)) calc(100vw*(36/414));
`
const PasswordEditText = styled.input`
    width: 100%; height: calc(100vw*(42/414));
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    color: #4b4b4b;
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(3/414));
    margin: calc(100vw*(8/414)) 0;
    text-indent: calc(100vw*(20/414));
`
const PasswordBtnWrap = styled.div`
    width: 100%;
    text-align: center;
    padding: 0 calc(100vw*(16/414));
`
const PasswordBtn = styled.button`
    width: 100%;
    font-size: calc(100vw*(20/414));
    font-weight: 900;
    color: #4b4b4b;
    padding: calc(100vw*(16/414)) 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.13);
    border: calc(100vw*(2/414)) solid #efefef;
    border-radius: calc(100vw*(13/414));
`
const ErrorMessage = styled.p`
    font-size: calc(100vw*(10/414));
    font-weight: bold;
    color: #f0505d;
    text-align: right;
`