//react
import {Link} from "react-router-dom";

//css
import styled from "styled-components"

//img
import IconstarOn from "../../../img/ico_star_on.svg"
import IconstarOff from "../../../img/ico_star_off.svg"

//숫자 3자리수 콤마 코드
function numberWithCommas(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

const getStarImageList = (count) =>{
    let imageList = [];
    for(var i =0;i < 5;i++){
        imageList.push(
            <Stars>
             <img src={i < count ? IconstarOn : IconstarOff} alt=""/>
            </Stars>
        )
    }
    return imageList;
}

export default function MyReview({value,index}) {
    return (
        <Container>
            <Link to={`/reviewDetail/${value.rev_id}`}>
                <ReviewTitleWrap>
                    <ReviewTitleInner>
                        <ReviewTitle>{value.prd_title}</ReviewTitle>
                        <ReviewText>{value.prd_detail1}</ReviewText>
                        <ReviewDetail>{value.prd_detail2}</ReviewDetail>
                    </ReviewTitleInner>
                    <ReviewTitleInner>
                        <ReviewTitleDate>{value.rev_date}</ReviewTitleDate>
                    </ReviewTitleInner>
                </ReviewTitleWrap>
            </Link>
            {
                value.rev_image ?
                    value.rev_image.length > 0 ?
                    <Link to={`/reviewImgDetail/${value.rev_id}`}>
                        <CustomerImgWrap>
                            <CustomerImage src={value.rev_image} alt="Customer images"/>
                        </CustomerImgWrap>
                    </Link>
                    :
                    null
                :null
            }
            <Link to={`/reviewDetail/${value.rev_id}`}>
                <ReviewBox>
                    <ReviewBoxTitleWrap>
                        <ReviewBoxTitle>{value.mem_username}</ReviewBoxTitle>
                        <StarsWrap>
                            {
                                getStarImageList(value.rev_score)
                            }
                        </StarsWrap>
                    </ReviewBoxTitleWrap>
                    <ReviewTextWrap>
                        <ReviewTextDetail>{value.rev_content}</ReviewTextDetail>
                    </ReviewTextWrap>
                </ReviewBox>
            </Link>
            <ReviewLine></ReviewLine>
        </Container>
  );
}

const Container = styled.div`
    width: 100%;
`
const ReviewTitleWrap = styled.div`
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding-top: calc(100vw*(17/414));
`
const ReviewTitleInner = styled.div`
    padding: 0 calc(100vw*(7/414));
`
const ReviewTitle = styled.p`
    font-size: calc(100vw*(19/414));
    margin-bottom: calc(100vw*(2/414));
`
const ReviewText = styled.p`
    font-size: calc(100vw*(15/414));
`
const ReviewDetail = styled.p`
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    color: #707070;
    margin-top: calc(100vw*(8/414));
`
const ReviewTitleDate = styled.p`
    font-size: calc(100vw*(13/414));
    color: #969696;
    text-align: right;
`
const ReviewPrice = styled.p`
    font-size: calc(100vw*(15/414));
    font-weight: 900;
    margin-top: calc(100vw*(4/414));
    text-align: right;
`
const CustomerImgWrap = styled.div`
    display: flex;
    white-space: nowrap;
    overflow-y: visible;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: none;
    ::-webkit-scrollbar {
        display:none;
    }
    padding-top: calc(100vw*(18/414));
`
const CustomerImage = styled.img`
    width: calc(100vw*(112/414));
    margin: 0 calc(100vw*(1.5/414));
    &:first-child{
        margin-left: calc(100vw*(7/414));
    }
`
const ReviewBox = styled.div`
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: calc(100vw*(12/414)) calc(100vw*(7/414)) calc(100vw*(20/414));
`
const ReviewBoxTitleWrap = styled.div`
    display: flex;
    align-items: center;
`
const ReviewBoxTitle = styled.p`
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    white-space: nowrap;
    margin-right: calc(100vw*(12/414));
`
const ReviewTextWrap = styled.div`
    width: 100%;
    margin-top: calc(100vw*(4/414));
`
const ReviewTextDetail = styled.p`
    font-size: calc(100vw*(13/414));
    line-height: 1.5;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    white-space: normal;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;

`
const StarsWrap = styled.div`
    display: flex;
`
const Stars = styled.span`
    display: block;
    width: calc(100vw*(15/414));
    margin: 0 calc(100vw*(1/414));
`
const ReviewLine = styled.div`
    width: 100%; height: calc(100vw*(1/414));
    background: #f7f7f7;
`
