//react
import {Link,useHistory} from "react-router-dom";
import {useState, useEffect} from 'react';
import { UserAction } from '../../../store/actionCreators'; // 담기

//css
import styled from "styled-components"

//page
import serverController from '../../../server/serverController';
import AddressLayout from '../../shop/pay/AddressLayout';

export default function ShopPayAddress() {
    const [myAddressList,setMyAddressList] = useState([]);
    const history = useHistory();

    useEffect(()=>{
        serverController.connectFetchController(`user/address_list`,"GET",null,function(res){
        if(res.result == 1) {
            setMyAddressList(res.list);
            console.log(res.list);
        }});
    },[]);

    /*
    useEffect(()=>{
        if(myAddressList){
            let idlist = [];
            myAddressList.map((value,index) => {
                idlist.push(value.adr_id);
            })
            setAdrId(idlist);
        }
    },[myAddressList]);
    */
    
    const adrTypeClick = (id) => {
        serverController.connectFetchController(`user/update_address_type?adr_id=${id}`,"PUT",null,function(res){
        if(res.result == 1) {
            //redux
            serverController.connectFetchController(`user/mypage`,"GET",null,function(res){
                if(res.result == 1) {
                    let userData = {
                        mem_id:res.list[0].mem_id,
                        mem_name:res.list[0].mem_name,
                        mem_username:res.list[0].mem_username,
                        mem_email:res.list[0].mem_email,
                        mem_phone:res.list[0].mem_phone,
                    }
                    let adrData = {
                        adr_id:res.list[0].adr_id,
                        adr_zipcode:res.list[0].adr_zipcode,
                        adr_name:res.list[0].adr_name,
                        adr_address1:res.list[0].adr_address1,
                        adr_address2:res.list[0].adr_address2,
                        adr_phone:res.list[0].adr_phone,
                    }

                    UserAction.updateUserIdData( { userId : userData } ) // 담기
                    UserAction.updateAdrIdData( { ardId : adrData } ) // 담기
            }});

            return history.push("/shop/pay");
        }});
    }

    return (
        <Container>
            <ShopPayAddressWrap>
                {
                    myAddressList.map((value,index) => {
                        return(
                            <AddressLayout value={value} adrTypeClick={adrTypeClick}/>
                        )
                    })
                }
            </ShopPayAddressWrap>

            <AddAddressWrap>
                <AddAddressBtn>
                    <Link to={"/shop/pay/addAddress"}>+ 배송지 추가하기</Link>
                </AddAddressBtn>
            </AddAddressWrap>
        </Container>
    );
}

//css
const Container = styled.div` 
    width: 100%;
    background: #f7f7f7;
`
const ShopPayAddressWrap = styled.div` 
    width: 100%; min-height: calc(100vh - calc(100vw*(157/414)));
    padding: calc(100vw*(18/414)) calc(100vw*(16/414));
`
const AddAddressWrap = styled.div`
    width: 100%;
    padding: 0 calc(100vw*(16/414));
    margin-bottom: calc(100vw*(16/414));
`
const AddAddressBtn = styled.button`
    width: 100%; height: calc(100vw*(60/414));
    font-size: calc(100vw*(20/414));
    font-weight: bold;
    color: #4b4b4b;
    background: #fff;
    border-radius: calc(100vw*(13/414));
    border: calc(100vw*(2/414)) solid #efefef;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.13);
`