//react
import {Link} from "react-router-dom";
import React ,{useState,useEffect} from 'react';

//css
import styled from "styled-components"

//img
import IconMore from "../../../../img/ico_arrow_more.png"

//page
import serverController from '../../../../server/serverController';

export default function InquiryWrite({prd_id}) {
    const [inquiryList,setInquiryList] = useState([]);

    useEffect(()=>{
        serverController.connectFetchController(`qna/selectPrd?prd_id=${prd_id}`,"GET",null,function(res){
        if(res.result == 1) {
            setInquiryList(res.list)
        }});
    },[]);
    
    return (
        <Container>
            <ShopLine></ShopLine>
            <InquiryListWrap>
                    {
                        inquiryList.length > 0 ?
                        inquiryList.map((value)=>{
                            return (
                                <Link to={`/product/inquiryView/${value.qna_id}`}>
                                    <InquiryWrap>
                                        <InquiryTitleWrap>
                                            <InquiryTitle>{value.prd_title}</InquiryTitle>
                                            <Inquirydate>{value.prd_date}</Inquirydate>
                                        </InquiryTitleWrap>
                                        <InquiryTextWrap>
                                            <InquiryQTitle>Q</InquiryQTitle>
                                            <InquiryQText>{value.qna_name}</InquiryQText>
                                        </InquiryTextWrap>
                                        <InquiryLine></InquiryLine>
                                        <InquiryTextWrap>
                                            <InquiryATitle>A</InquiryATitle>
                                            <InquiryAText>{value.qna_answer}</InquiryAText>
                                        </InquiryTextWrap>
                                    </InquiryWrap>
                                </Link>
                            )
                        })
                        :
                        <QnaNoneWrap>
                            <QnaNoneText>문의 내용이 없습니다.</QnaNoneText>
                        </QnaNoneWrap>
                    }
            </InquiryListWrap>
            <InquiryBtnWrap>
                <Link to={`/product/inquiryWrite/${prd_id}`}>
                    <InquiryBtn>문의하기</InquiryBtn>
                </Link>
            </InquiryBtnWrap>

        </Container>
  );
}

const Container = styled.div`
    width: 100%; height: 100%;
`
const ShopLine = styled.div`
    width: 100%;
    height: calc(100vw*(14/414));
    background: #f7f7f7;
`
const InquiryListWrap = styled.div`
    width: 100%; min-height: calc(100vh - calc(100vw*(275/414)));
    padding: calc(100vw*(32/414)) calc(100vw*(15/414));
    background: #fff;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0,0,0,0.16);
`
const InquiryWrap = styled.div`
    width: 100%;
    background: #fff url(${IconMore}) no-repeat center right calc(100vw*(16/414))/calc(100vw*(9/414));
    padding: calc(100vw*(16/414)) calc(100vw*(46/414)) calc(100vw*(16/414)) calc(100vw*(21/414));
    border: calc(100vw*(1/414)) solid #dddddd;
    border-radius: calc(100vw*(11/414));
    margin-bottom: calc(100vw*(15/414));
`
const InquiryTitleWrap = styled.div`
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: calc(100vw*(9/414));
`
const InquiryTitle = styled.p`
    font-size: calc(100vw*(12/414));
    color: #969696;
`
const Inquirydate = styled.span`
    font-size: calc(100vw*(12/414));
    color: #969696;
`
const InquiryTextWrap = styled.div`
    display: flex;
`
const InquiryQTitle = styled.p`
    font-size: calc(100vw*(13/414));
    font-weight: 900;
    margin-right: calc(100vw*(8/414));
`
const InquiryQText = styled.p`
    font-size: calc(100vw*(13/414));
    font-weight: 900;
`
const InquiryATitle = styled.p`
    font-size: calc(100vw*(13/414));
    font-weight: 900;
    margin-right: calc(100vw*(8/414));
`
const InquiryAText = styled.p`
    font-size: calc(100vw*(13/414));
    line-height: 1.4;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    white-space: normal;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
`
const InquiryLine = styled.div`
    width: calc(100vw*(315/414)); height: calc(100vw*(1/414));
    background: #f4f4f4;
    margin: calc(100vw*(9/414)) 0 calc(100vw*(8/414));
`
const InquiryBtnWrap = styled.div`
    text-align: center;
    background: #fff;
    padding: calc(100vw*(20/414)) calc(100vw*(16/414)) calc(100vw*(16/414));
`
const InquiryBtn = styled.button`
    width: 100%;
    border: calc(100vw*(1/414)) solid #efefef;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.13);
    border-radius: calc(100vw*(13/414));
    padding: calc(100vw*(20/414)) 0;
    font-size: calc(100vw*(20/414));
    font-weight: 900;
    color: #4b4b4b;
`
const QnaNoneWrap = styled.div`
    width: 100%;
`
const QnaNoneText = styled.p`
    text-align: center;
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    color: #979697;
    padding: calc(100vw*(28/414)) 0;
`