//css
import styled from "styled-components"


//숫자 3자리수 콤마 코드
function numberWithCommas(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}


export default function OrderDtailProduct({value}) {

    return (
        <Container>
            <CartListWrap>
                <CartList>
                    <CartListImg>
                        <img src={value.src} alt="product image"/>
                    </CartListImg>
                    <CartListText>
                        <ProductTitle>
                            <ProductName>{value.prd_title} 외 {value.prd_count}건</ProductName>
                            <ProductText>{value.prd_detail1}</ProductText>
                            <ProductTextDetail>{value.prd_detail2}</ProductTextDetail>
                            <TotalPrice>{numberWithCommas(value.txn_payment_amount)}원 <ProductQty>{value.prd_qty}개</ProductQty></TotalPrice>
                        </ProductTitle>
                    </CartListText>
                </CartList>
            </CartListWrap>
        </Container>
        
  );
}
const Container = styled.div`
    position: relative;
    width: 100%;
    border-top: calc(100vw*(1/414)) solid #f7f7f7;
    &:first-child {
        border-top: none;
    }
`
const CartListWrap = styled.div`
    width: 100%;
`
const CartList = styled.div`
    display: flex;
    align-items: center;
    width: 100%;
    padding: calc(100vw*(18/414)) calc(100vw*(10/414));
`
const CartListImg = styled.div`
    position: relative;
    width: calc(100vw*(82/414));
`
const CartListText = styled.div`
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: calc(100vw*(255/414));
    padding-left: calc(100vw*(15/414));
`
const ProductTitle = styled.div`
    width: 100%
`
const ProductName = styled.p`
    width: 100%;
    font-size: calc(100vw*(16/414));
`
const ProductText = styled.p`
    width: 100%;
    font-size: calc(100vw*(13/414));
`
const ProductTextDetail = styled.p`
    width: 100%;
    font-size: calc(100vw*(13/414));
    color: #707070;
    margin-top: calc(100vw*(5/414));
`
const TotalPrice = styled.span`
    display: flex;
    align-items: center;
    font-size: calc(100vw*(13/414));
    margin-top: calc(100vw*(2/414));
    color: #707070;
`
const ProductQty = styled.span`
    font-size: calc(100vw*(13/414));
    color: #707070;
    margin-left: calc(100vw*(8/414));
`