import SubHeader from '../../../../component/common/SubHeader';
import ReviewDetail from '../../../../component/shop/product/review/ReviewDetail';
import FooterPadding from '../../../../component/common/FooterPadding';
import FooterMenu from '../../../../component/common/FooterMenu';


//MypageSetEdit page
export default function ReviewDetailPage({match}) {

  return (
    <>
        <SubHeader rank={true} title={"구매후기"}/>
        <ReviewDetail rev_id={match.params.rev_id}/>
        <FooterPadding option={{backgroundColor:"#fff"}}/>
        <FooterMenu />
    </>
  );
}
