//css
import styled from "styled-components"


export default function Term() {

    return(
        <Container>
            <TermWrap>
                <TermText>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Laudantium aliquid, et dolor voluptas vitae quasi aut. Ex excepturi officiis officia, dolores optio eaque unde, quos delectus at molestias consectetur? Ipsam!</TermText>
            </TermWrap>
        </Container>

    );
}

const Container = styled.div`
    width: 100%;
`
const TermWrap = styled.div`
    width: 100%; min-height: calc(100vh - calc(100vw*(150/414)));
    background: #fff;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    z-index: 10;
    margin-top: calc(100vw*(12/414));
    padding-bottom: calc(100vw*(100/414));
`
const TermText = styled.p`
    padding: calc(100vw*(30/414)) calc(100vw*(15/414));
    font-size: calc(100vw*(14/414));
    color: #333;
    line-height: 1.2;
`