//react
import {Link} from "react-router-dom";
import React ,{useState,useEffect} from 'react';

//css
import styled from "styled-components"

//img
import IconReadMore from "../../../img/ico_read_more.png"

//page
import serverController from '../../../server/serverController';
import ReviewLayout from "../../common/board/Review";

//숫자 3자리수 콤마 코드
function numberWithCommas(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

export default function ShopProductReview({prd_id,rev_id,shopOption}) {
    const [reviewList,setReviewList] = useState([]);

    useEffect(()=>{
        serverController.connectFetchController(`review/selectPrd?type=2&prd_id=${prd_id}`,"GET",null,function(res){
        if(res.result == 1) {
            setReviewList(res.list)
        }});
    },[]);


    if(shopOption == false)
        return null;
    return (
        <Container>
            <ShopLine></ShopLine>

            <Link to={`/product/review/${prd_id}`}>
                <ReviewTitleWrap>
                    <ReviewTitle>상품평 ({numberWithCommas(reviewList.length)})</ReviewTitle>
                    <ReviewText>상품평 모두보기<IcoReadMore src={IconReadMore} alt="상품평 모두보기"/></ReviewText>
                </ReviewTitleWrap>
            </Link>
            <ReviewLine></ReviewLine>
            <ReviewInnerWrap>
                <Link to={`/reviewDetail/${rev_id}`}>
                    {
                        reviewList.map((value,index)=>{
                            return (<ReviewLayout rev_id={value.rev_id} value={value} key={value.rev_id}/>)
                        })
                    }
                </Link>
            </ReviewInnerWrap>
            <ReviewMoreBtnWrap>
                <Link to={`/product/review/${prd_id}`}>
                    <ReviewMoreBtn>이 상품의 상품평 모두보기</ReviewMoreBtn>
                </Link>
            </ReviewMoreBtnWrap>
        </Container>
    );
}


const Container = styled.div`
    width: 100%;
    background: #fff;
`
const ShopLine = styled.div`
    width: 100%;
    height: calc(100vw*(10/414));
    background: #f7f7f7;
`
const ReviewTitleWrap = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: calc(100vw*(30/414)) calc(100vw*(25/414));
`
const ReviewTitle = styled.p`
    font-size: calc(100vw*(18/414));
    font-weight: bold;
`
const ReviewText = styled.p`
    font-size: calc(100vw*(12/414));
    font-weight: bold;
`
const IcoReadMore = styled.img`
    width: calc(100vw*(8/414));
    margin-left: calc(100vw*(9/414));
`
const ReviewLine = styled.div`
    width: calc(100vw*(392/414));
    height: calc(100vw*(1/414));
    background: #d1d1d1;
    margin: 0 auto;
`
/* review */
const ReviewInnerWrap = styled.div`
width: 100%;
`
const ReviewMoreBtnWrap = styled.div`
    padding: calc(100vw*(20/414)) calc(100vw*(18/414)) calc(100vw*(45/414));
`
const ReviewMoreBtn = styled.button`
    width: 100%;
    font-size: calc(100vw*(13/414));
    font-weight: 900;
    color: #ff6752;
    border: calc(100vw*(2/414)) solid #ff6752;
    padding: calc(100vw*(13/414)) 0;
    letter-spacing: calc(100vw*(-0.3/414));
`
