//react
import { Link, useHistory } from "react-router-dom";
import React ,{useState,useEffect} from 'react';
import { UserAction } from '../../../store/actionCreators'; // 담기

//css
import styled from "styled-components"

//page
import serverController from '../../../server/serverController';

export default function MaypageSetEditList({editBtn, setAdressShow, adrZipcode, setAdrZipcode, adrAddress, setAdrAddress}) {
    const [myInfo,setMyInfo] = useState([]);
    const [memId,setMemId] = useState([]);
    const [adrId,setadrId] = useState([]);
    
    const [userEmail,setUserEmail] = useState([]);
    const [userPhone,setUserPhone] = useState([]);
    const [adrName,setAdrName] = useState([]);
    const [adrAddressDetail,setAdrAddressDetail] = useState([]);
    const [adrPhone,setAdrPhone] = useState([]);

    const history = useHistory();
    const UserEmailChange = (e) =>{ setUserEmail(e.target.value); }
    const UserPhoneChange = (e) =>{ setUserPhone(e.target.value); }
    const adrNameChange = (e) =>{ setAdrName(e.target.value); }
    const adrAddressDetailChange = (e) =>{ setAdrAddressDetail(e.target.value); }
    const adrPhoneChange = (e) =>{ setAdrPhone(e.target.value); }
    
    const openPostCode = () => {
        setAdressShow(true);
    }
    const closePostCode = () => {
        setAdressShow(false);
    }

    useEffect(()=>{
        serverController.connectFetchController(`user/mypage`,"GET",null,function(res){
        if(res.result == 1) {
            setMyInfo(res.list[0]);
            setMemId(res.list[0].mem_id);
            setadrId(res.list[0].adr_id);

            setUserEmail(res.list[0].mem_email);
            setUserPhone(res.list[0].mem_phone);
            setAdrName(res.list[0].adr_name);
            setAdrZipcode(res.list[0].adr_zipcode);
            setAdrAddress(res.list[0].adr_address1);
            setAdrAddressDetail(res.list[0].adr_address2);
            setAdrPhone(res.list[0].adr_phone);
        }});
    },[]);

    useEffect(()=>{
        if(editBtn == true){
            //redux
            serverController.connectFetchController(`user/mypage`,"GET",null,function(res){
                if(res.result == 1) {
                    let userData = {
                        mem_id:res.list[0].mem_id,
                        mem_name:res.list[0].mem_name,
                        mem_username:res.list[0].mem_username,
                        mem_email:res.list[0].mem_email,
                        mem_phone:res.list[0].mem_phone,
                    }
                    let adrData = {
                        adr_id:res.list[0].adr_id,
                        adr_zipcode:res.list[0].adr_zipcode,
                        adr_name:res.list[0].adr_name,
                        adr_address1:res.list[0].adr_address1,
                        adr_address2:res.list[0].adr_address2,
                        adr_phone:res.list[0].adr_phone,
                    }
    
                    UserAction.updateUserIdData( { userId : userData } ) // 담기
                    UserAction.updateAdrIdData( { ardId : adrData } ) // 담기
            }});
            
            myinfoEditClick();
        }
    },[editBtn]);

    const myinfoEditClick = () =>{
        if(true){
            const formData = new FormData();
            formData.append('mem_id',memId);
            formData.append('mem_email',userEmail);
            formData.append('mem_phone',userPhone);
            formData.append('adr_id',adrId);
            formData.append('adr_name',adrName);
            formData.append('adr_zipcode',adrZipcode);
            formData.append('adr_address1',adrAddress); 
            formData.append('adr_address2',adrAddressDetail); 
            formData.append('adr_phone',adrPhone);
            serverController.connectFetchController('user/update',"PUT",formData,function(res){
            if(res.result == 1) {
                //redux
                serverController.connectFetchController(`user/mypage`,"GET",null,function(res){
                    if(res.result == 1) {
                        let userData = {
                            mem_id:res.list[0].mem_id,
                            mem_name:res.list[0].mem_name,
                            mem_username:res.list[0].mem_username,
                            mem_email:res.list[0].mem_email,
                            mem_phone:res.list[0].mem_phone,
                        }
                        let adrData = {
                            adr_id:res.list[0].adr_id,
                            adr_zipcode:res.list[0].adr_zipcode,
                            adr_name:res.list[0].adr_name,
                            adr_address1:res.list[0].adr_address1,
                            adr_address2:res.list[0].adr_address2,
                            adr_phone:res.list[0].adr_phone,
                        }

                        UserAction.updateUserIdData( { userId : userData } ) // 담기
                        UserAction.updateAdrIdData( { ardId : adrData } ) // 담기
                }});
                return history.push("/mypageSet");
            } else {
                alert("정보수정을 실패했습니다.");
            }
        });
        }
    }

  return (
        <Container>
            <MaypageSetListWrap>
                <SetListWrap>
                    <SetList>
                        <SetListTitle>이름</SetListTitle>
                        <SetIdText>{myInfo.mem_username}</SetIdText>
                    </SetList>
                    <SetList>
                        <SetListTitle>아이디</SetListTitle>
                        <SetIdText>{myInfo.mem_name}</SetIdText>
                    </SetList>
                    <SetList>
                        <SetListTitle>비밀번호</SetListTitle>
                        <SetPasswordWarp>
                            <SetPasswordText>****</SetPasswordText>
                            <Link to={"/password/edit"}>
                                <SetPasswordBtn>비밀번호 변경</SetPasswordBtn>
                            </Link>
                        </SetPasswordWarp>
                    </SetList>
                    <SetList>
                        <SetListTitle>이메일</SetListTitle>
                        <SetListText type="email" placeholder={myInfo.mem_email} value={userEmail} onChange={UserEmailChange}/>
                    </SetList>
                    <SetList>
                        <SetListTitle>연락처</SetListTitle>
                        <SetListText type="text" placeholder={myInfo.mem_phone} value={userPhone} onChange={UserPhoneChange}/>
                    </SetList>
                </SetListWrap>
                <MenuListLine></MenuListLine>
                <SetListWrap>
                    <SetList>
                        <SetListTitle>수령인</SetListTitle>
                        <SetListText type="text" placeholder={myInfo.adr_name} value={adrName} onChange={adrNameChange}/>
                    </SetList>
                    <SetList>
                        <SetListTitle>주소</SetListTitle>
                        <SetListAddressWrap>
                            <AddresssearchWrap>
                                <AddresssearchInput type="text" placeholder={myInfo.adr_zipcode} value={adrZipcode} readonly/>
                                <AddresssearchBtn onClick={openPostCode}>주소검색</AddresssearchBtn>
                            </AddresssearchWrap>
                            <SetListAddress type="text" placeholder={myInfo.adr_address1} value={adrAddress} readonly/>
                            <SetListAddress type="text" placeholder={myInfo.adr_address2} value={adrAddressDetail} onChange={adrAddressDetailChange}/>
                        </SetListAddressWrap>
                    </SetList>
                    <SetList>
                        <SetListTitle>연락처</SetListTitle>
                        <SetListText type="text" placeholder={myInfo.adr_phone} value={adrPhone} onChange={adrPhoneChange}/>
                    </SetList>
                </SetListWrap>
                <MenuListLine></MenuListLine>
                <SetAlarmWrap>
                    <SetList>
                        <SetListTitle>알림설정</SetListTitle>
                        <Switch>
                            <SwitchInput type="checkbox"/>
                            <SwitchSlider></SwitchSlider>
                        </Switch>
                    </SetList>
                </SetAlarmWrap>
            </MaypageSetListWrap>
        </Container>
  );
}

//css
const Container = styled.div` 
    position: relative;
    width: 100%;
    z-index: 100;
    background: #fff;
`
const MaypageSetListWrap = styled.div` 
    width: 100%;
`
const SetListWrap = styled.div` 
    width: 100%;
    padding: calc(100vw*(20/414)) calc(100vw*(38/414));
    background: #fff;
`
const SetAlarmWrap = styled.div` 
    width: 100%;
    padding: calc(100vw*(20/414)) calc(100vw*(38/414));
    background: #fff;
`
const SetList = styled.div`
    width: 100%;
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    margin: calc(100vw*(20/414)) 0;
`
const SetListTitle = styled.p`
    width: calc(100vw*(100/414));
    font-size: calc(100vw*(15/414));
    font-weight: bold;
`
const SetIdText = styled.p`
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    padding: calc(100vw*(8/414)) 0;
    text-align: right;
`
const SetListText = styled.input`
    width: calc(100% - calc(100vw*(100/414))); height: calc(100vw*(32/414));
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    text-align: left;
    text-indent: calc(100vw*(15/414));
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(3/414));
    color: #4b4b4b;
`
const MenuListLine = styled.div`
    position: relative;
    width: 100%;
    z-index: 100;
    width: 100%; height: calc(100vw*(5/414));
    background: #f7f7f7;
`
const Switch = styled.label`
    position: relative;
    display: inline-block;
    width: calc(100vw*(57/414)); height: calc(100vw*(27/414));
`
const SwitchInput = styled.input`
    opacity: 0;
    width: 0; height: 0;
    &:checked + span {
        background-color: #fff;
        border: calc(100vw*(2/414)) solid #ed2939;
    }
    &:checked + span::before {
        content: "켬";
        color: #ed2939;
        background-color: #ed2939;
        border-color: #ff8690;
        text-indent: calc(100vw*(-25/414));
        transform: translateX(calc(100vw*(30/414)));
    }
`
const SwitchSlider = styled.span`
    position: absolute; top: 0; left: 0; right: 0; bottom: 0;
    background-color: #fff;
    border: calc(100vw*(2/414)) solid #000;
    border-radius: calc(100vw*(34/414));
    transition: .4s;
    &&::before {
        content: "끔";
        color: #000;
        font-size: calc(100vw*(15/414)); font-weight: bold;
        text-indent: calc(100vw*(24/414));
        position: absolute; left: calc(100vw*(2/414)); bottom: calc(100vw*(2/414));
        width: calc(100vw*(19/414)); height: calc(100vw*(19/414));
        background-color: #000; 
        -webkit-transition: .4s;
        transition: .4s;
        border: calc(100vw*(2/414)) solid #6c6c6c;
        border-radius: 50%;
        box-sizing: border-box;
        box-shadow: 0 0 calc(100vw*(3/414)) 0 rgba(255, 255, 255, 0.39);
    }
`
const SetPasswordWarp = styled.div`
    width: calc(100% - calc(100vw*(100/414)));
    display: flex;
    align-items: center;
    justify-content: space-between;
`
const SetPasswordText = styled.p`
    padding-left: calc(100vw*(15/414));
    font-size: calc(100vw*(15/414));
    color: #4b4b4b;
`
const SetPasswordBtn = styled.button`
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    padding: calc(100vw*(7/414)) calc(100vw*(26/414));
    background: #f7f7f7;
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(3/414));
`
const SetListAddressWrap = styled.div`
    width: calc(100% - calc(100vw*(100/414)));
`
const SetListAddress = styled.input`
    width: 100%; height: calc(100vw*(32/414));
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    text-align: left;
    text-indent: calc(100vw*(15/414));
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(3/414));
    color: #4b4b4b;
    margin: calc(100vw*(3/414)) 0;
`
const AddresssearchWrap = styled.div`
    display: flex;
    align-items: center;
`
const AddresssearchInput = styled.input`
    width: calc(100% - calc(100vw*(105/414))); height: calc(100vw*(32/414));
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    text-align: left;
    text-indent: calc(100vw*(15/414));
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(3/414));
    color: #4b4b4b;
    margin: calc(100vw*(3/414)) 0;
`
const AddresssearchBtn = styled.button`
    width: calc(100vw*(95/414)); height: calc(100vw*(32/414));
    color: #fff;
    background: #646464;
    border: calc(100vw*(2/414)) solid #979797;
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    margin-left: calc(100vw*(10/414));
    border-radius: calc(100vw*(3/414));
`