//react
import React ,{useState,useEffect} from 'react';

//css
import styled from "styled-components"

//img
import IconDownload from "../../../img/ico_download.png"
import IconSelect from "../../../img/ico_shop_select.png"

//page
import serverController from '../../../server/serverController';
import ShopProduct from "../../common/product/ShopProduct";
import DetailSetOption from './DetailSetOption';

export default function ShopProductInfo({openCouponModal,prd_id, prd_type, setSelectList, selectList,product,setProduct,optionList,setOptionList,shopOption,setShopOption,setOptionText,setCartModal}) {
    
    useEffect(()=>{
        serverController.connectFetchController(`current/insert?prd_id=${prd_id}`,"POST",null,function(res){
            if(res.result == 1) {
                console.log("최근본목록 추가완료");
            }
        });
        
        serverController.connectFetchController(`products/detail?prd_id=${prd_id}`,"GET",null,function(res){
            if(res.result == 1) {
                console.log(res);
                
                setProduct(res.info)
                setOptionList(res.optionList)

                if(res.info.prd_type == 2)
                    setShopOption(true);
                else
                    setShopOption(false);
            }
        }); 
    },[]);

    function getOptionTextList(option){
        var optionText="";
        var value = option;
        for(let i in value){
            console.log(value + "???????????????????");
            for(let key in value[i].options.optionList){
                optionText += "[옵션: ";
                let optionlist = value[i].options.optionList[key];     
                optionText += (value[i].options.isSetOptional==true)? value[i].prd_title+":" : "";
                for(let z =0;z < optionlist.length;z++){
                    var opt = optionlist[z];
                    optionText += opt.opt_name + ( z + 1 == optionlist.length ?"" : "/");
                }
                optionText+="]";
            }
        }
        return optionText;
    }


    //단순 selectList 에 대한 돈계산.
    function calculatePrice(list){
        var totalPrice = 0;
        var count = 0;
        
        for(var key in list){
        var price = 0;
        var optionList = list[key].options.optionList;
        count++;
        for(var calKey in optionList){
          for(var i =0;i<	optionList[calKey].length ;i++){
            price += optionList[calKey][i].opt_price;
          }
        }
    
        if(list[key].options.isOptional == false){
          price += Math.round((list[key].prd_price) - ((list[key].prd_price * list[key].prd_sale_rate) / 100));
        }
          
        price *= list[key].count;
        totalPrice += price;
        }
        
        //배송비 추후 따로 받아와야함
        if(count > 0)
          totalPrice += 0;//shipPrice;
    
        return totalPrice;
    }

    //장바구니에 담아진 최종 리스트에 대한 돈계산.
    function getPrice(option){
	    let price = 0;
	    let optionPrice = 0;
	    let salePrice = 0;
	
	      let options = option.data;
	      for(let j = 0; j<options.length;j++){
	      
		    var p = 0;
		    var op = 0;
			var sp = 0;
	        let optionList = options[j].options.optionList;
	
	        for(let calKey in optionList){
	          for(let z =0;z<	optionList[calKey].length ;z++){
	            op += optionList[calKey][z].opt_price;
	          }
	        }
	
	        if(options[j].options.isOptional == false){
	          p += Math.round((options[j].prd_price) - ((options[j].prd_price * options[j].prd_sale_rate) / 100));
	          sp += Math.round(((options[j].prd_price * options[j].prd_sale_rate) / 100));
	        }
	
	        price += (p * options[j].count);
	        optionPrice += (op * options[j].count);
	        salePrice += (sp * options[j].count);
	      }
    
		return { price : price , optionPrice : optionPrice , salePrice : salePrice };
 	}

    const checkSetProductValidation = (opt) =>{
        let options = opt.options;
    
        for(let key in selectList){
            if(JSON.stringify(selectList[key].options) == JSON.stringify(options)){
                setCartModal(true);
                return;
            }
        }
    
        var selectOption = JSON.parse(JSON.stringify(selectList));
        var date_id = new Date().getTime().toString();
        selectOption[date_id] = opt;
      
        getOptionTextList(selectOption);
        setSelectList(selectOption);
        
        setOptionText(getOptionTextList(selectOption))
        console.log(selectOption);
    }
    
    
    return (
        <Container>
            <ProductInfoWrap>
                <ShopProduct value={product} prd_id={prd_id} shopOption={shopOption}/>
                <CouponOptionWrap>
                {/* <CouponWrap onClick={()=>{openCouponModal(true)}}>
                        <CouponText>최대 10,000원 할인쿠폰. 기간한정</CouponText>
                        <CouponText>쿠폰 받기 <IconCoupon src={IconDownload} alt=""/></CouponText>
                    </CouponWrap> */}
                    {
                        shopOption ?
                        <DetailSetOption  
                            product={product}
                            productInfo={[product]}
                            checkSetProductValidation={checkSetProductValidation} 
                            optionList={optionList} 
                        />
                        :
                        null
                    }
                </CouponOptionWrap>
            </ProductInfoWrap>
        </Container>
    );
}


const Container = styled.div`
    width: 100%;
`
const ProductInfoWrap = styled.div`
    width: 100%;
    background: #fff;
`
const CouponOptionWrap = styled.div`
    width: 100%;
    padding-bottom: calc(100vw*(35/414));
`
const CouponWrap = styled.div`
    width: calc(100vw*(380/414));
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: calc(100vw*(2/414)) dashed #ff6752;
    margin: 0 auto calc(100vw*(18/414));
    padding: calc(100vw*(12/414)) calc(100vw*(24/414));
`
const CouponText = styled.p`
    font-size: calc(100vw*(13/414));
    color: #ff6752;
    font-weight: 900;
`
const IconCoupon = styled.img`
    width: calc(100vw*(16/414));
    margin-left: calc(100vw*(8/414));
`