//react
import React ,{useState} from 'react';

import MainTitleBar from '../../component/main/MainTitleBar';
import MainTitleMenuBar from '../../component/main/MainTitleMenuBar';
import MarketInfo from '../../component/market/info/MarketInfo';
import MarketProductListB from '../../component/market/MarketProductListB';
import MarketProductListC from '../../component/market/MarketProductListC';
import MarketProductListD from '../../component/market/MarketProductListD';
import MarketProductListE from '../../component/market/MarketProductListE';
import MarketTabBar from '../../component/market/MarketTabBar';
import MainRecentPurchases from '../../component/main/MainRecentPurchases';
import FooterPadding from '../../component/common/FooterPadding';
import FooterMenu from '../../component/common/FooterMenu';
import ProductFilter from '../../component/common/product/ProductFilter';


//Market page
export default function MarketPage() {
  const [filterModal,setFilterModal] = useState(false);
  const [tabBtnA, setTabBtnA] = useState(true);
  const [tabBtnB, setTabBtnB] = useState(false);
  const [tabBtnC, setTabBtnC] = useState(false);
  const [tabBtnD, setTabBtnD] = useState(false);
  const [tabBtnE, setTabBtnE] = useState(false);

  const openFilterModal = (onOff) =>{ setFilterModal(onOff);}

  return (
    <>
      <MainTitleBar/>
      <MainTitleMenuBar/>
      <MarketTabBar setTabBtnA={setTabBtnA} setTabBtnB={setTabBtnB} setTabBtnC={setTabBtnC} setTabBtnD={setTabBtnD} setTabBtnE={setTabBtnE}/>
      <MarketInfo tabBtnA={tabBtnA}/>
      <MarketProductListB openFilterModal={openFilterModal} tabBtnB={tabBtnB}/>
      <MarketProductListC openFilterModal={openFilterModal} tabBtnC={tabBtnC}/>
      <MarketProductListD openFilterModal={openFilterModal} tabBtnD={tabBtnD}/>
      <MarketProductListE openFilterModal={openFilterModal} tabBtnE={tabBtnE}/>
      <MainRecentPurchases rank={false}/>
      <FooterPadding option={{backgroundColor:"#f7f7f7"}}/>
      <FooterMenu/>
      <ProductFilter filterModal={filterModal} openFilterModal={openFilterModal}/>
    </>
  );
}
