import SubHeader from '../../../../component/common/SubHeader';
import InquiryList from '../../../../component/shop/product/inquiry/InquiryList';
import FooterPadding from '../../../../component/common/FooterPadding';
import FooterMenu from '../../../../component/common/FooterMenu';


export default function InquiryListPage({match}) {

  return (
    <>
      <SubHeader rank={true} title={"상품문의"}/>
      <InquiryList prd_id={match.params.prd_id}/>
      <FooterPadding option={{backgroundColor:"#fff"}}/>
      <FooterMenu/>
    </>
  );
}
