//react
import React ,{useState,useEffect} from 'react';
import {Link} from "react-router-dom";

//css
import styled from "styled-components"

//img
import IcoFilter from "../../img/ico_filter.png"
import MarketIntoMore from "../../img/ico_market_arrow.png"

//page
import serverController from '../../server/serverController';
import MarketProduct from "../common/product/MarketProduct";


//숫자 3자리수 콤마 코드
function numberWithCommas(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

export default function MarketProductLayout({openFilterModal}) {

    const [itemList,setItemList] = useState([]);
    const [count,setCount] = useState(0);

    useEffect(()=>{
        serverController.connectFetchController(`products/info?prd_type=1`,"GET",null,function(res){
        if(res.result == 1) {
            setItemList(res.items)
            setCount(res.items.length)
        }});
    },[]);

  return (
    <Container>
        <ProductBox>
        <ProductTitleWrap>
            <ProductTitle>야채 세일상품</ProductTitle>
            <CategoryFilterWrap>
                <FilterText><FilterNumberText>{numberWithCommas(count)}</FilterNumberText> 개의 상품</FilterText>
                <IconFilterText onClick={()=>{openFilterModal(true)}}><IconFilter src={IcoFilter} alt="Filter button"/> 필터</IconFilterText>
            </CategoryFilterWrap>
        </ProductTitleWrap>
        <ProductLine></ProductLine>

        <ProductList>
            {
                itemList.map((value,index)=>{
                    return (
                        <MarketProduct value={value}/>
                    )
                })
            }
        </ProductList>
        </ProductBox>

        {/* <Link to={"market/info"}>
            <MarketInto>
                <MarketIntoBtnText>오늘의 마트클럽 구로점 마켓소개</MarketIntoBtnText>
            </MarketInto>
        </Link> */}
    </Container>
);
}

/* main title */
const Container = styled.div` 
    width:100%;
`
/* Product */
const ProductBox = styled.div`
`
const ProductTitleWrap = styled.div`
    width: 100%;
    padding: calc(100vw*(5/414)) calc(100vw*(26/414));
`
const ProductTitle = styled.h2`
    font-size: calc(100vw*(19/414));
    font-weight: bold;
    text-align: center;
    padding: calc(100vw*(30/414)) 0;
`
const ProductLine = styled.div`
    width: calc(100vw*(392/414));
    height: calc(100vw*(1/414));
    background: #d1d1d1;
    margin: 0 auto;
`
const CategoryFilterWrap = styled.div` 
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    width: 100%;
`
const FilterText = styled.p`
    font-size: calc(100vw*(14/414));
    line-height: 1.2;
    font-weight: bold;
`
const FilterNumberText = styled.span`
    font-size: calc(100vw*(15/414));
    font-weight: 900;
`
const IconFilterText = styled.p`
    display: flex;
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    padding: calc(100vw*(7/414)) calc(100vw*(16/414));
    background: #f7f7f7;
    border-radius: calc(100vw*(8/414));
    border: calc(100vw*(1/414)) solid #979797;
    color: #707070;
`
const IconFilter = styled.img`
    width: calc(100vw*(11/414));
    margin-right: calc(100vw*(4/414));
`

/* Product List */
const ProductList = styled.div`
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    padding: calc(100vw*(10/414)) calc(100vw*(5/414)) 0;
`
const MarketInto = styled.div`
    width: 100%;
    background: #ed2939 url(${MarketIntoMore}) no-repeat right calc(100vw*(30/414)) center/calc(100vw*(115/414));
    padding: calc(100vw*(30/414)) 0;
    margin: calc(100vw*(40/414)) 0 calc(100vw*(50/414));
`
const MarketIntoBtnText = styled.p`
    width: 100%;
    font-size: calc(100vw*(15/414));
    color: #fff;
    font-weight: 900;
    text-align: left;
    margin-right: calc(100vw*(5/414));
    padding-left: calc(100vw*(44/414));
`