//react
import React ,{useState,useEffect} from 'react';
import {Link} from "react-router-dom";

//css
import styled from "styled-components"


const cateArrayData = [
    {
        title:"홈",
        path :"/"
    },
    {
        title:"베스트",
        path :"/best"
    },
    {
        title:"마켓",
        path :"/market"
    },
    {
        title:"쇼핑몰",
        path :"/mall"
    },
    {
        title:"렌탈",
        path :"/rental"
    },
    {
        title:"방송&공구",
        path :"/tool"
    }
];


export default function MainTitleMenuBar() {
    
    const [mainMenuOn,mainMenuOff] = useState(true);
  return (
        <Container>
            <MainTitleMenuWarp>
                {
                    cateArrayData.map((value)=>{
                        var pathName = window.location.pathname;
                        return (
                            <MainTitleMenubox>
                                <Link to={value.path}>
                                    <MainTitleMenu mainMenuOn={"/" + pathName.split('/')[1] == value.path}>{value.title}</MainTitleMenu>
                                </Link>
                            </MainTitleMenubox>
                        )
                    })
                }
            </MainTitleMenuWarp>
        </Container>
  );
}

//css
const Container = styled.div` 
    width:100%;
`
const MainTitleMenuWarp = styled.ul` 
    width:100%;
    display: flex;
    justify-content: center;
    white-space: nowrap;
    overflow-y: visible;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: none;
    ::-webkit-scrollbar {
        display:none;
    }   
`
const MainTitleMenubox = styled.li` 
    margin: calc(100vw*(10/414)) calc(100vw*(12/414));
`
const MainTitleMenu = styled.a`
    font-size: calc(100vw*(15/414));
    font-weight: 600;
    color: #4B4B4B;
    padding: calc(100vw*(3/414)) calc(100vw*(4/414));

    ${({mainMenuOn})=>{
        return mainMenuOn ? 
        `
            color: #ED2939;
        `
        :
        ``
    }}
`