//react
import React ,{useState,useEffect} from 'react';

//css
import styled from "styled-components"


export default function CartSelect({itemList,checkProduct,handleAllCheck,clickDelete}) {
    return (
        <Container>
            <Checks>
                <ChkBox checked={itemList.length === checkProduct.length? true : false} onChange={(e)=>{handleAllCheck(e.target.checked)}} type="checkbox" name="chk" id="AllChk"/>
                <ChkBoxLabel htmlFor="AllChk"></ChkBoxLabel>
                <CheckboxText htmlFor="AllChk" >전체선택</CheckboxText>
            </Checks>
            <>
                <DeleteBtn onClick={clickDelete}>삭제</DeleteBtn>
            </>
        </Container>
    );
}

/* main title */
const Container = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: calc(100vw*(10/414)) calc(100vw*(24/414));
    margin-top: calc(100vw*(-10/414));
`
const Checks = styled.div`
    position: relative;
`
const ChkBox = styled.input`
    position: absolute;
    width: calc(100vw*(1/414)); height: calc(100vw*(1/414));
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip:rect(0,0,0,0);
    border: 0;
    &:checked + label::after {
        content: '';
        position: absolute; top: calc(100vw*(-3/414)); left: calc(100vw*(5/414));
        width: calc(100vw*(8/414)); height: calc(100vw*(8/414));
        background: #ed3342;
        border-radius: calc(100vw*(2/414));
    }
`
const ChkBoxLabel = styled.label`
    display: inline-block;
    position: relative;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    text-indent: -9999px;
    &&::before {
        content: '';
        position: absolute; left: 0; top: calc(100vw*(-8/414));
        width: calc(100vw*(16/414)); height: calc(100vw*(16/414));
        text-align: center;
        background: #fafafa;
        border: calc(100vw*(1/414)) solid #ed3342;
        border-radius: calc(100vw*(4/414));
    }
`
const CheckboxText = styled.label`
    font-size: calc(100vw*(13/414));
    color: #707070;
    margin-left: calc(100vw*(35/414));
`
const DeleteBtn = styled.button`
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    color: #707070;
    background: #f7f7f7;
    padding: calc(100vw*(6/414)) calc(100vw*(15/414));
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(8/414));
`