//react
import {useState, useEffect} from 'react';

//css
import styled from "styled-components"

export default function OptionListLayout({item,shopOption,getOptionName,optionlist,setSelectList,prdKey}) {
    const [count,setCount] = useState(item.count);
    
    const countTextChange = (e) =>{
        setCount(optionlist);
    }
    
    const clickBtnM = () => {
        if(count > 1){
            setSelectList(
                list =>{
                    list[prdKey].count = count - 1;
                    return JSON.parse(JSON.stringify(list));
                }
            )
            setCount(parseInt(count)-1)
        }
    }
    const clickBtnP = () => {
        setSelectList(
            list =>{
                list[prdKey].count = count + 1;
                return JSON.parse(JSON.stringify(list));
            }
        )
        setCount(parseInt(count)+1)
    }

    console.log(getOptionName(optionlist));
    return (
        <>
            <DeliveryTextWrap>
                {
                    shopOption ?
                    <DeliveryText>{getOptionName(optionlist)}</DeliveryText>
                    :
                    <DeliveryText></DeliveryText>
                }
                <OptionNumberWrap>
                    <OptionNumberInput value={count} onChange={(e) => countTextChange(e)} disabled/>
                    <CountBtnM onClick={()=> clickBtnM()}>-</CountBtnM>
                    <CountBtnP onClick={()=> clickBtnP()}>+</CountBtnP>
                </OptionNumberWrap>
            </DeliveryTextWrap>
        </>
    );
}

const DeliveryTextWrap = styled.div`
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: calc(100vw*(10/414)) 0;
`
const DeliveryText = styled.p`
    font-size: calc(100vw*(14/414));
    color: #707070;
`
const OptionNumberWrap = styled.div`
    position: relative;
`
const OptionNumberInput = styled.input`
    width: calc(100vw*(130/414)); height: calc(100vw*(38/414));
    border: calc(100vw*(1/414)) solid #d1d1d1;
    border-radius: calc(100vw*(8/414));
    font-size: calc(100vw*(15/414));
    text-align: center;
    color: #707070;
`
const CountBtnM = styled.span`
    position: absolute; top: 0; left: calc(100vw*(10/414));
    font-size: calc(100vw*(15/414));
    padding: calc(100vw*(10/414));
    color: #707070;
`
const CountBtnP = styled(CountBtnM)`
    left: auto; right: calc(100vw*(10/414));
`