//react
import {Link} from "react-router-dom";

//css
import styled from "styled-components"


export default function MypageMainTitleMenu() {
    
  return (
        <Container>
            <MypageTitleMenuWrap>
                <MypageTitleMenuList>
                    <TitleMenuText>
                        <Link to={"/cartMarket"}>장바구니</Link>
                    </TitleMenuText>
                    <TitleMenuText>
                        <Link to={"/wishList"}>찜한상품</Link>
                    </TitleMenuText>
                    <TitleMenuText style={{borderRight: "none"}}>
                        <Link to={"/latelyList"}>최근 본 상품</Link>
                    </TitleMenuText>
                </MypageTitleMenuList>
            </MypageTitleMenuWrap>
        </Container>
  );
}

//css
const Container = styled.div` 
    width: 100%;
    padding-top: calc(100vw*(10/414));
`
const MypageTitleMenuWrap = styled.div` 
    width: 100%;
    background: #fff;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    padding: calc(100vw*(12/414)) 0;
`
const MypageTitleMenuList = styled.ul` 
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: calc(100vw*(10/414)) calc(100vw*(12/414));
`
const TitleMenuText = styled.li`
    width: 100%;
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    text-align: center;
    border-right: calc(100vw*(1/414)) solid #e4e4e4;
    padding: calc(100vw*(10/414)) 0;
`