//react
import {Link} from "react-router-dom";
import {useState, useEffect} from 'react';

//css
import styled from "styled-components"


export default function OrderDetailBtn() {

    return (
        <Container>
            <OrderDetailBtnWrap>
                <OrderBtn>구매후기쓰기</OrderBtn>
                <OrderBtn>주문내역 삭제</OrderBtn>
            </OrderDetailBtnWrap>
        </Container>
    );
}

//css
const Container = styled.div` 
    width: 100%;
    background: #fff;
    margin-bottom: calc(100vw*(10/414));
`
const OrderDetailBtnWrap = styled.div`
    width: 100%;
    padding-top: calc(100vw*(20/414));
    padding: calc(100vw*(38/414)) calc(100vw*(15/414)) calc(100vw*(150/414));
`
const OrderBtn = styled.button`
    width: 100%;
    padding: calc(100vw*(14/414)) 0;
    border-radius: calc(100vw*(10/414));
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.13);
    font-size: calc(100vw*(15/414));
    font-weight: 900;
    color: #4b4b4b;
    margin-top: calc(100vw*(8/414));
`