//react
import {Link} from "react-router-dom";

//css
import styled from "styled-components"


export default function LoginPW() {

    return (
        <Container>
            <LoginWrap>
                <LoginInputWrap>
                    <div>
                        <LoginText>이메일</LoginText>
                        <LoginTextInput type="email" placeholder={"이메일을 입력하세요"}/>
                    </div>
                    <div>
                        <LoginText>아이디</LoginText>
                        <LoginTextInput type="text" placeholder={"아이디를 입력하세요"}/>
                    </div>

                    <IDTextWrap>
                        <IDText>입력하신 정보가 올바르지 않습니다.<br/>다시 입력하여주세요.</IDText>
                    </IDTextWrap>
                    <LoginBtnWrap>
                        <Link to={"/loginEditPW"}>
                            <LoginBtn>비밀번호 찾기</LoginBtn>
                        </Link>
                    </LoginBtnWrap>

                </LoginInputWrap>
            </LoginWrap>
            <SignUpWrap>
                <Link to={"/signUpStep1"}>
                    <MainBackBtn>회원가입 가기</MainBackBtn>
                </Link>
            </SignUpWrap>
        </Container>
  );

}

const Container = styled.div`
    position: relative;
    width: 100%; height: calc(100vh - calc(100vw*(65/414)));
    z-index: 100;
`
const LoginWrap = styled.div`
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    width: 100%; height: calc(100vh - calc(100vw*(160/414)));
    background: #fff;
    padding: 0 calc(100vw*(36/414));
`
const LoginInputWrap = styled.div`
    width: 100%;
`
const LoginText = styled.p`
    font-size: calc(100vw*(15/414));
    font-weight: 900;
    margin-top: calc(100vw*(15/414));
    margin-bottom: calc(100vw*(8/414));
`
const LoginTextInput = styled.input`
    width: 100%; height: calc(100vw*(50/414));
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    border: calc(100vw*(1/414)) solid #707070;
    border-radius: calc(100vw*(8/414));
    text-indent: calc(100vw*(30/414));
`
const LoginBtnWrap = styled.div`
    width: 100%;
`
const LoginBtn = styled.button`
    width: 100%;
    background: #ed2939;
    border: calc(100vw*(3/414)) solid #bc343f;
    color: #fff;
    border-radius: calc(100vw*(6/414));
    padding: calc(100vw*(18/414)) 0;
    font-size: calc(100vw*(18/414));
    font-weight: 900;
`
const IDTextWrap = styled.div`
    display: flex;
    align-items: center;
    padding: calc(100vw*(26/414)) 0;
`
const IDText = styled.p`
    width: 100%;
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    text-align: center;
    line-height: 1.3;
    color: #979697;
`
const SignUpWrap = styled.div`
    position: absolute; bottom: 0;
    width: 100%;
    padding: calc(100vw*(32/414)) 0;
    background: #fff;
    text-align: center;
`
const MainBackBtn = styled.button`
    width: calc(100vw*(342/414));
    background: #fff;
    color: #4b4b4b;
    border: calc(100vw*(2/414)) solid #efefef;
    border-radius: calc(100vw*(13/414));
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.13);
    padding: calc(100vw*(19/414)) 0;
    font-size: calc(100vw*(19/414));
    font-weight: 900;
`