//react
import {Link} from "react-router-dom";

//css
import styled from "styled-components"

//img
import MarketInfoImg from "../../../img/market_info_img.png"


export default function MarketInfo({tabBtnA}) {

    if(tabBtnA == false)
    return null;
  
    return (
        <Container>
            <MarketInfoWrap>
                <MainTitleWrap>
                    <MainTitleBox>
                        <MainTitle>마켓소개</MainTitle>
                    </MainTitleBox>
                </MainTitleWrap>
                <SubTitleWrap>
                    <SubTitle>오늘의 마켓딜 !</SubTitle>
                </SubTitleWrap>
                <MarketInfoImgWrap>
                    <img src={MarketInfoImg} alt="Market image"/>
                </MarketInfoImgWrap>

                {/* MarketInfo */}
                <MarketInfoTextWrap>                
                    <MarketInfoText>WELCOME!</MarketInfoText>
                    <MarketBranchText>마트클럽 계산점</MarketBranchText>
                    <MarketInfoLine></MarketInfoLine>
                </MarketInfoTextWrap>
                <MarketDetailWarp>
                    <MarketDetailTitle>
                        <MarketTime>영업시간</MarketTime>
                        <DeliveryTime>배달가능시간</DeliveryTime>
                        <DeliveryArea>배달가능지역</DeliveryArea>
                        <MarketTel>대표번호</MarketTel>
                        <MarketAddress>주소</MarketAddress>
                    </MarketDetailTitle>
                    <MarketDetailText>
                        <MarketTimeText>오전 07:00 ~ 오후 22:00 (연중무휴)</MarketTimeText>
                        <DeliveryTimeText>오전 10:00 ~ 오후 19:00 (월~금요일)</DeliveryTimeText>
                        <DeliveryAreaText>계산동, 임학동, 병방동, 박촌동</DeliveryAreaText>
                        <MarketTelText>032-546-9435</MarketTelText>
                        <MarketAddressText>인천광역시 계양구 주부토로 573, 3층</MarketAddressText>
                    </MarketDetailText>
                </MarketDetailWarp>
                <MarketTextWrap>
                    <MarketText>안녕하세요. 마트클럽 계양점 점장입니다.<br/>
                    코로나19로 걱정이 많은 요즘, 고객님의 안전한 쇼핑을 위해 오전,
                    오후 총 2번 매일 매장을 소독하고 있습니다.
                    합리적인 가격, 최고의 품질, 고객만족 서비스를 위해 항상 노력하고 있습니다.
                    앞으로도 많은 이용 부탁드립니다.</MarketText>
                </MarketTextWrap>
                <Link to={"../market"}>
                    <BackMarketBtn>
                        <BackMarketText>돌아가기</BackMarketText>
                    </BackMarketBtn>
                </Link>

            </MarketInfoWrap>

        </Container>
    );
}

/* main title */
const Container = styled.div` 
    width:100%;
`
const MarketInfoWrap = styled.div` 
    width:100%;
    background: #fff;
    padding-bottom: calc(100vw*(5/414));
    margin-bottom: calc(100vw*(20/414));
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
`
const MainTitleWrap = styled.div`
    width: 100%;
`
const MainTitleBox = styled.h2`
    display: flex;
    justify-content: space-between;
    align-items: center;
    width:100%;
    padding: calc(100vw*(24/414)) calc(100vw*(26/414)) calc(100vw*(18/414));
    color: #ED2939;
`
const MainTitle = styled.span`
    font-size: calc(100vw*(15/414));
    color: #ED2939;
    font-weight: 900;
`
const SubTitleWrap = styled.div`
    background: #ff6752;
`
const SubTitle = styled.p`
    width: 100%;
    font-size: calc(100vw*(15/414));
    font-weight: 900;
    padding: calc(100vw*(13/414)) calc(100vw*(26/414));
    color: #fff;
`
const MarketInfoImgWrap = styled.div`
    width: 100%;
`
/* MarketInfo */
const MarketInfoTextWrap = styled.div`
    width: 100%;
`
const MarketInfoText = styled.b`
    display: block;
    width: 100%;
    color: #ED2939;
    font-size: calc(100vw*(33/414));
    font-weight: 900;
    text-align: center;
    margin-top: calc(100vw*(17/414));
`
const MarketBranchText = styled.p`
    display: block;
    width: 100%;
    font-size: calc(100vw*(15/414));
    font-weight: 900;
    text-align: center;
    margin-top: calc(100vw*(12/414));
`
const MarketInfoLine = styled.div`
    display: block;
    width: calc(100vw*(283/414)); height: calc(100vw*(1/414));
    margin: calc(100vw*(18/414)) auto 0;
    background: #e4e4e4;
`
const MarketDetailWarp = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: calc(100vw*(25/414)) 0;
`
const MarketDetailTitle = styled.div`
    padding-left: calc(100vw*(30/414));
`
const MarketDetailText = styled.div`
    padding-right: calc(100vw*(30/414));
`
const MarketTime = styled.p`
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    text-align: left;
    line-height: 1.2;
    margin: calc(100vw*(10/414)) 0;
`
const DeliveryTime = styled(MarketTime)``
const DeliveryArea =styled(MarketTime)``
const MarketTel = styled(MarketTime)``
const MarketAddress = styled(MarketTime)``

const MarketTimeText = styled.p`
    font-size: calc(100vw*(13/414));
    text-align: right;
    line-height: 1.2;
    margin: calc(100vw*(10/414)) 0;
`
const DeliveryTimeText = styled(MarketTimeText)``
const DeliveryAreaText =styled(MarketTimeText)``
const MarketTelText = styled(MarketTimeText)``
const MarketAddressText = styled(MarketTimeText)``

const MarketTextWrap = styled.div`
    padding: calc(100vw*(10/414)) calc(100vw*(30/414));
    margin-bottom: calc(100vw*(20/414));
`
const MarketText = styled.p`
    font-size: calc(100vw*(15/414));
    line-height: 2;
`
const BackMarketBtn = styled.div`
    padding: calc(100vw*(0/414)) calc(100vw*(15/414));
    margin-bottom: calc(100vw*(42/414));
`
const BackMarketText = styled.p`
    font-size: calc(100vw*(20/414));
    color: #4b4b4b;
    font-weight: 900;
    text-align: center;
    padding: calc(100vw*(20/414)) 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.13);
    border-radius: calc(100vw*(13/414));
`
