//react
import {Link} from "react-router-dom";

//css
import styled from "styled-components"

//img
import IconReadMore from "../../../img/ico_read_more.png"


export default function ShopProductGuide() {

    return (
        <Container>
            <ShopLine></ShopLine>

            <Link to={"/product/guide"}>
                <ReviewTitleWrap>
                    <ReviewTitle>배송/ 교환/ 반품 안내</ReviewTitle>
                        <IcoReadMore src={IconReadMore} alt="상품평 모두보기"/>
                </ReviewTitleWrap>
            </Link>
        </Container>
    );
}


const Container = styled.div`
    width: 100%;
    background: #fff;
    margin-bottom: calc(100vw*(20/414));
`
const ShopLine = styled.div`
    width: 100%;
    height: calc(100vw*(10/414));
    background: #f7f7f7;
`
const ReviewTitleWrap = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: calc(100vw*(30/414)) calc(100vw*(25/414));
`
const ReviewTitle = styled.p`
    font-size: calc(100vw*(18/414));
    font-weight: bold;
`
const IcoReadMore = styled.img`
    width: calc(100vw*(8/414));
    margin-left: calc(100vw*(9/414));
`