//react
import {Link} from "react-router-dom";
import {useState, useEffect} from 'react';

//css
import styled from "styled-components"

//img
import IconCredit from '../../../img/ico_credit_card.svg'
import IconRemit from '../../../img/ico_remit.svg'
import IconSelect from '../../../img/ico_select_red.png'


export default function ShopPayment() {

    return (
        <Container>
            <ShopPayPointWrap>
                <ShopPayInfoTitleWrap>
                    <ShopPayInfoTitle>결제수단</ShopPayInfoTitle>
                </ShopPayInfoTitleWrap>
                <ShopPaymentWrap>
                    <CreditCardBox>
                        <PaymentText>신용카드</PaymentText>
                    </CreditCardBox>
                    {/* <RemitBox>
                        <PaymentText>실시간 계좌이체</PaymentText>
                    </RemitBox> */}
                    <RemitBox>
                        <PaymentText>가상계좌</PaymentText>
                    </RemitBox>
                </ShopPaymentWrap>
                <>
                    <PaymentSelectWrap>
                        {/* <SelectListWrap name="" id="">
                            <option value="">하나은행</option>
                            <option value="">국민은행</option>
                            <option value="">기업은행</option>
                            <option value="">신한은행</option>
                            <option value="">농협은행</option>
                        </SelectListWrap> */}
                    </PaymentSelectWrap>
                    <PaymentTextPriceWrap>
                        {/* <PaymentTextPrice type="number" placeholder="일시불 (5만원 이상 할부 가능)"/> */}
                    </PaymentTextPriceWrap>
                </>
            </ShopPayPointWrap>
        </Container>
    );
}

//css
const Container = styled.div` 
    position: relative;
    width: 100%;
    background: #fff;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    z-index: 10;
    margin-top: calc(100vw*(10/414));
`
const ShopPayPointWrap = styled.div`
    width: 100%;
    padding-top: calc(100vw*(20/414));
    padding: calc(100vw*(24/414)) calc(100vw*(36/414)) calc(100vw*(28/414));
`
const ShopPayInfoTitleWrap = styled.div`
    width: 100%;
    padding-bottom: calc(100vw*(35/414));
`
const ShopPayInfoTitle = styled.p`
    font-size: calc(100vw*(18/414));
    font-weight: bold;
`
const ShopPaymentWrap = styled.div`
    display: flex;
    align-items: center;
`
const CreditCardBox = styled.div`
    width: calc(100vw*(96/414)); height: calc(100vw*(77/414));
    border-radius: calc(100vw*(5/414));
    border: calc(100vw*(2/414)) solid #e4e4e4;
    background: url( ${IconCredit} ) no-repeat top calc(100vw*(9/414)) center/calc(100vw*(40/414));
    margin-right: calc(100vw*(16/414));

    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    border-color: #ed2939;
`
const RemitBox = styled(CreditCardBox)`
    background-image: url( ${IconRemit} );
`
const PaymentText = styled.p`
    font-size: calc(100vw*(12/414));
    font-weight: bold;
    text-align: center;
    padding-top: calc(100vw*(51/414));
`
const PaymentSelectWrap = styled.div`
    padding-top: calc(100vw*(18/414));
`
const SelectListWrap = styled.select`
    width: 100%; height: calc(100vw*(40/414));
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(3/414));
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    text-indent: calc(100vw*(20/414));
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background: url(${IconSelect}) no-repeat 93% 50%/calc(100vw*(9/414));
`
const PaymentTextPriceWrap = styled.div`
    padding-top: calc(100vw*(2/414));
`
const PaymentTextPrice = styled.input`
    width: 100%; height: calc(100vw*(40/414));
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(3/414));
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    text-indent: calc(100vw*(20/414));
`