//react
import {Link, useHistory} from "react-router-dom";
import { useCookies } from 'react-cookie';

//css
import styled from "styled-components"

//img
import IconAlarm from "../../img/ico_alarm.png"

//page
import serverController from '../../server/serverController';

export default function MypageMainMenuList() {
    const [cookies, setCookie, removeCookie] = useCookies([]);
    const history = useHistory();

    const LogoutClick = () =>{
        setCookie(decodeURIComponent(document.cookie));
        serverController.connectFetchController("user/logout","POST",null,function(res){
            removeCookie("jwtT");
            return history.push("/login");
        });
    }
  return (
        <Container>
            <MypageMenuListWrap>
                <MenuListWrap>
                    <Link to={"/orderList"}>
                        <MenuList>주문,배송 목록</MenuList>
                    </Link>
                    <Link to={"/returnList"}>
                        <MenuList>취소,반품,교환 목록</MenuList>
                    </Link>
                    <Link to={"/purchaseList"}>
                        <MenuList>구매내역</MenuList>
                    </Link>
                    <Link to={"/serviceCenter"}>
                        <MenuList>고객센터</MenuList>
                    </Link>
                </MenuListWrap>
                <MenuListLine></MenuListLine>
                <MenuListWrap>
                    <Link to={"/alarm"}>
                        <MenuList>알림내역
                            <IcoAlarm src={IconAlarm} alt="알림내역"/>
                            <AlarmCount>99+</AlarmCount>
                        </MenuList>
                    </Link>
                    <MenuList onClick={LogoutClick}>로그아웃</MenuList>
                    <Link to={"/withdrawal"}>
                        <MenuList>회원탈퇴</MenuList>
                    </Link>
                </MenuListWrap>
            </MypageMenuListWrap>
        </Container>
  );
}

//css
const Container = styled.div` 
    width: 100%;
    padding-top: calc(100vw*(10/414));
`
const MypageMenuListWrap = styled.div` 
    width: 100%;
    background: #fff;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    padding-bottom: calc(100vw*(15/414));
`
const MenuListWrap = styled.ul` 
    width: 100%;
    padding: calc(100vw*(5/414)) calc(100vw*(50/414));
`
const MenuList = styled.li`
    position: relative;
    display: flex;
    align-items: center;
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    padding: calc(100vw*(25/414)) 0;
`
const MenuListLine = styled.div`
    width: 100%; height: calc(100vw*(5/414));
    background: #f7f7f7;
`
const IcoAlarm = styled.img`
    width: calc(100vw*(19/414));
    margin-left: calc(100vw*(8/414));
`
const AlarmCount = styled.span`
    position: absolute; top: calc(100vw*(22/414)); left: calc(100vw*(72/414));
    display: block;
    padding: calc(100vw*(1/414)) calc(100vw*(3/414));
    background: #ED2939;
    color: #fff;
    border-radius: calc(100vw*(6/414));
    font-size: calc(100vw*(7/414));
`