//react
import {Link} from "react-router-dom";
import {useState, useEffect} from 'react';

//css
import styled from "styled-components"

//img
import IconMore from "../../../img/ico_arrow_red.png"

//page
import serverController from '../../../server/serverController';


export default function ShopPayAddress() {
    const [myInfo,setMyInfo] = useState([]);

    useEffect(()=>{
        serverController.connectFetchController(`user/mypage`,"GET",null,function(res){
        if(res.result == 1) {
            setMyInfo(res.list[0]);
        }});
    },[]);

    return (
        <Container>
            <ShopPayInfoWrap>
                <ShopPayInfoTitleWrap>
                    <ShopPayInfoTitle>배송지 정보</ShopPayInfoTitle>
                </ShopPayInfoTitleWrap>
                <ShopPayInfoInner>
                    <ShopPayInfoBox>
                        <ShopPayInfoBoxTitle>이름</ShopPayInfoBoxTitle>
                        <ShopPayInfoBoxText>{myInfo.adr_name}</ShopPayInfoBoxText>
                    </ShopPayInfoBox>
                    <ShopPayInfoBox>
                        <ShopPayInfoBoxTitle>주소</ShopPayInfoBoxTitle>
                        <ShopPayInfoBoxText>{myInfo.adr_address1} {myInfo.adr_address2}</ShopPayInfoBoxText>
                    </ShopPayInfoBox>
                    <ShopPayInfoBox>
                        <ShopPayInfoBoxTitle>연락처</ShopPayInfoBoxTitle>
                        <ShopPayInfoBoxText>{myInfo.adr_phone}</ShopPayInfoBoxText>
                    </ShopPayInfoBox>
                    <ShopPayInfoBox>
                        <ShopPayInfoBoxText>{myInfo.adr_memo}</ShopPayInfoBoxText>
                    </ShopPayInfoBox>
                    <MoreBtnWrap>
                        <Link to={"/shop/pay/address"}>
                            <MoreBtn></MoreBtn>
                        </Link>
                    </MoreBtnWrap>
                </ShopPayInfoInner>
            </ShopPayInfoWrap>
        </Container>
    );
}

//css
const Container = styled.div` 
    position: relative;
    width: 100%;
    background: #fff;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    z-index: 10;
    margin-top: calc(100vw*(10/414));
`
const ShopPayInfoWrap = styled.div`
    width: 100%;
    padding-top: calc(100vw*(20/414));
    padding: calc(100vw*(24/414)) calc(100vw*(65/414)) calc(100vw*(28/414)) calc(100vw*(36/414));
`
const ShopPayInfoTitleWrap = styled.div`
    width: 100%;
`
const ShopPayInfoTitle = styled.p`
    font-size: calc(100vw*(18/414));
    font-weight: bold;
    margin-bottom: calc(100vw*(5/414));
`
const ShopPayInfoInner = styled.div`
    position: relative;
    width: 100%;
`
const ShopPayInfoBox = styled.div`
    display: flex;
    align-items: baseline;
    padding: calc(100vw*(12/414)) 0;
`
const ShopPayInfoBoxTitle = styled.p`
    width: calc(100vw*(68/414));
    font-size: calc(100vw*(13/414));
    font-weight: bold;
`
const ShopPayInfoBoxText = styled.p`
    width: calc(100% - calc(100vw*(68/414)));
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    word-break: keep-all;
`
const MoreBtnWrap = styled.div`
    position: absolute; top: 50%; right: calc(100vw*(-35/414));
    width: calc(100vw*(30/414)); height: calc(100vw*(40/414));
    margin-top: calc(100vw*(-30/414));
`
const MoreBtn = styled.button`
    width: calc(100vw*(30/414)); height: calc(100vw*(40/414));
    background: url(${IconMore}) no-repeat center/calc(100vw*(12/414));
`