import {useState} from 'react';

import SubHeader from '../../../component/common/SubHeader';
import OrderList from '../../../component/mypage/shop/OrderList';
import FooterMenu from '../../../component/common/FooterMenu';
import OptionModal from '../../../component/common/modal/OptionModal';
import ReviewModal from '../../../component/mypage/shop/ReviewModal';


//OrderList page
export default function OrderListPage() {

    const [prdOptionModal,setprdOptionModal] = useState(false);
    const [revOptionModal,setRevOptionModal] = useState(false);
    const openprdOptionModal = (onOff) =>{ setprdOptionModal(onOff);}
    const openrevOptionModal = (onOff) =>{ setRevOptionModal(onOff);}

  return (
    <>
        <SubHeader rank={true} title={"주문,배송 목록"}/>
        <OrderList openprdOptionModal={openprdOptionModal} openrevOptionModal={openrevOptionModal}/>
        <FooterMenu />
        <OptionModal prdOptionModal={prdOptionModal} openprdOptionModal={openprdOptionModal}/>
        <ReviewModal revOptionModal={revOptionModal} openrevOptionModal={openrevOptionModal}/>
    </>
  );
}
