//react
import {Link} from "react-router-dom";
import {useState, useEffect} from 'react';

//css
import styled from "styled-components"

//img
import CustomerImg from "../../../../img/customer_img.png"
import IconstarOn from "../../../../img/ico_star_on.svg"
import IconstarOff from "../../../../img/ico_star_off.svg"

//page
import serverController from '../../../../server/serverController';
import ReviewLayout from "../../../common/board/Review";

export default function ReviewDetail({rev_id}) {
    
    const [reviewList,setReviewList] = useState([]);
    const url_rev_id = rev_id;

    useEffect(()=>{
        serverController.connectFetchController(`review/content`,"GET",null,function(res){
        if(res.result == 1) {
            setReviewList(res.list)
        }});
    },[]);

  return (
        <Container>
            <ReviewDetailWrap>
                <ReviewInnerWrap>
                    {
                    reviewList.map((value,index)=>{
                        if(url_rev_id == value.rev_id){
                            return (<ReviewLayout rank={true} value={value}/>)
                        }
                    })
                    }
                </ReviewInnerWrap>
            </ReviewDetailWrap>
        </Container>
  );
}

//css
const Container = styled.div` 
    position: relative;
    width: 100%;
    z-index: 100;
    background: #fff;
`
const ReviewDetailWrap = styled.div` 
    width: 100%; min-height: calc(100vh - calc(100vw*(240/414)));
`
const ReviewInnerWrap = styled.div`
    width: 100%;
`