//React
import React ,{useState,useEffect} from 'react';

//css
import styled from "styled-components"

//숫자 3자리수 콤마 코드
function numberWithCommas(x) { return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");}

export default function CartOrderPrice({priceInfo,reduxZipcode}) {
   
  return (
        <Container>
            <CartPrice>
                <div>
                    <CartTotalPrice>총 삼품 금액</CartTotalPrice>
                    <OptionCost>옵션 추가 금액</OptionCost>
                    <DeliveryCost>배달비</DeliveryCost>
                    <DiscountAmount>할인 예상 금액</DiscountAmount>
                </div>
                <div>
                    <CartTotalPriceText>{numberWithCommas(priceInfo.originalPrice)}원</CartTotalPriceText>
                    <OptionPriceText>{numberWithCommas(priceInfo.optionPrice)}원</OptionPriceText>
                    <DeliveryCostText><DeliveryText>( 30,000원 이상 구매시 무료배달 )</DeliveryText> {numberWithCommas(reduxZipcode)}원</DeliveryCostText>
                    <DiscountAmountText>- {numberWithCommas(priceInfo.salePrice)}원</DiscountAmountText>
                </div>
            </CartPrice>
            <CartPriceTotal>
                <div>
                    <PaymentAmount>결제 예상 금액</PaymentAmount>
                    <CartPoint>적립혜택</CartPoint>
                </div>
                <div>
                    <PaymentAmountText>{numberWithCommas(priceInfo.totalPrice + reduxZipcode)}원</PaymentAmountText>
                    {/* 적립금 */}
                    <CartPointText>{numberWithCommas(Math.floor(priceInfo.totalPrice * 1 / 100))}원</CartPointText>
                </div>
            </CartPriceTotal>
            <CartOrderBtnWrap>
                <OrderBtn>
                    <OrderCount>총 {numberWithCommas(priceInfo.productCount)} 개</OrderCount>
                    <TotalPrice>주문하기</TotalPrice>
                </OrderBtn>
            </CartOrderBtnWrap>
        </Container>
            
);
}

/* main title */
const Container = styled.div`
  width: 100%;
  padding: calc(100vw*(22/414)) calc(100vw*(10/414)) calc(100vw*(105/414));
`
const CartPrice = styled.div`
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: calc(100vw*(18/414)) calc(100vw*(26/414));
    background: #fff;
    border-radius: calc(100vw*(20/414)) calc(100vw*(20/414)) 0 0;
    border-bottom: calc(100vw*(1/414)) solid #707070;
`
const CartPriceTotal = styled.div`
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: calc(100vw*(18/414)) calc(100vw*(24/414));
    background: #fff;
    border-radius: 0 0 calc(100vw*(20/414)) calc(100vw*(20/414));
`
const CartTotalPrice = styled.p`
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    margin: calc(100vw*(8/414)) 0;
`
const DeliveryCost = styled(CartTotalPrice)``
const OptionCost = styled(CartTotalPrice)``
const DiscountAmount = styled(CartTotalPrice)``
const CartTotalPriceText = styled(CartTotalPrice)`
    text-align: right;
`
const OptionPriceText = styled(CartTotalPrice)`
    text-align: right;
`
const DeliveryCostText = styled(CartTotalPrice)`
    text-align: right;
`
const DiscountAmountText = styled(CartTotalPrice)`
    text-align: right;
`

const DeliveryText = styled.span`
    font-size: calc(100vw*(10/414));
    font-weight: 700;
`
const PaymentAmount = styled.p`
    font-size: calc(100vw*(18/414));
    font-weight: 900;
    color: #ed2939;
    margin: calc(100vw*(8/414)) 0;
`
const PaymentAmountText = styled(PaymentAmount)`
    text-align: right;
`
const CartPoint = styled.p`
    font-size: calc(100vw*(15/414));
    font-weight: 900;
    color: #4A4A4A;
    margin: calc(100vw*(8/414)) 0;
`
const CartPointText = styled(CartPoint)`
    text-align: right;
`
const CartOrderBtnWrap = styled.div`
    text-align: center;
`
const OrderBtn = styled.button`
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding: calc(100vw*(16/414)) 0;
    background: #ed2939;
    border: calc(100vw*(3/414)) solid #bc343f;
    border-radius: calc(100vw*(32/414));
    color: #fff;
    font-weight: 900;
    margin-top: calc(100vw*(44/414));
`
const OrderCount = styled.span`
    color: #fff;
    font-size: calc(100vw*(20/414));
    font-family: 'NanumSquareRound',sans-serif;
    margin-right: calc(100vw*(14/414));
`
const TotalPrice = styled(OrderCount)``