//react
import { useState } from 'react';

import SubHeader from '../../../component/common/SubHeader';
import ShopPayEditAddress from '../../../component/shop/pay/ShopPayEditAddress';
import Popup from '../../../component/common/modal/PopupPostCode';


export default function PayEditAddressPage({match}) {
  const [adressShow,setAdressShow] = useState(false);
  const [adrZipcode,setAdrZipcode] = useState("");
  const [adrAddress,setAdrAddress] = useState("");

  return (
    <>
        <SubHeader rank={true} title={"배송지 수정"}/>
        <ShopPayEditAddress
          setAdressShow={setAdressShow}
          adrZipcode={adrZipcode}
          setAdrZipcode={setAdrZipcode}
          adrAddress={adrAddress}
          setAdrAddress={setAdrAddress}
          adr_id={match.params.adr_id}
        />
        {
          adressShow ?
          <Popup setAdressShow={setAdressShow} setAdrZipcode={setAdrZipcode} setAdrAddress={setAdrAddress}/>
          :
          null
        }
    </>
  );
}
