//react
import React ,{useState,useEffect} from 'react';
import {Link} from "react-router-dom";

//css
import styled from "styled-components"

//page
import serverController from '../../../server/serverController';
import LatelyProduct from "../../common/product/LatelyProduct";
import CartSelect from './CartSelect';


export default function CartMarket() {
    const [itemList,setItemList] = useState([]);
    const [likeId,setLikeId] = useState([]);
    const [currentList,setCurrentList] = useState([]);
    const [oldList,setOldList] = useState([]);
    const [checkProduct,setCheckProduct] = useState([]);
    
    const idArray = [];

    useEffect(()=>{
        serverController.connectFetchController(`current/list`,"GET",null,function(res){
            if(res.result == 1) {
                setCurrentList(res.current);
                setOldList(res.old);
                setItemList([...res.current,...res.old]);
                console.log(res);
            }
        });
    },[]);
    
    const handleAllCheck = (checked) =>{
        if(checked){
            itemList.forEach((el) => idArray.push(el.cur_id));
            setCheckProduct(idArray);
        }else{
            setCheckProduct([]); 
        }
    }

    const handleSingleCheck = (checked, id)=>{ 
        if(checked == true){
            setCheckProduct([...checkProduct,id]);   
        }else{
            setCheckProduct(checkProduct.filter((el) => el !== id))
        }
    }

    const clickDelete = () => {
        checkProduct.map((cur_id)=>{
            serverController.connectFetchController(`current/delete?cur_id=${cur_id}`,"DELETE",null,function(res){
            if(res.result == 1) {
                alert("최근 목록에서 삭제되었습니다.");
                serverController.connectFetchController(`current/list`,"GET",null,function(res){
                    if(res.result == 1) {
                        setCurrentList(res.current);
                        setOldList(res.old);
                        setItemList([...res.current,...res.old]);
                    }
                });
            }});
        })
    }


    return(
        <Container>
            <CartTitleWrap>
                <CartTitleBox>
                    <CartMarketTitle>
                        <Link to={"/wishList"}>찜한상품</Link>
                    </CartMarketTitle>
                    <CartShopTitle>
                        <Link to={"/latelyList"}>최근 본 상품</Link>
                    </CartShopTitle>
                </CartTitleBox>
            </CartTitleWrap>
            <CartWrap>
                <CartDelete>
                    <CartSelect handleAllCheck={handleAllCheck} clickDelete={clickDelete} itemList={itemList} checkProduct={checkProduct}/>
                </CartDelete>
                <>
                    <DateFilterWrap>
                        <DateFilterText>오늘 본 상품</DateFilterText>
                    </DateFilterWrap>
                    <CartListWrap>
                        {
                            currentList.length <= 0 ?
                            <NoText>오늘 본 상품 목록이 없습니다.</NoText>
                            :
                            currentList.map((value,index)=>{ 
                                return (
                                    <LatelyProduct handleSingleCheck={handleSingleCheck} rank={false} value={value} checkProduct={checkProduct}/>
                                )
                            })
                        }
                    </CartListWrap>
                </>
                <>
                    <DateFilterWrap>
                        <DateFilterText>이전에 본 상품</DateFilterText>
                    </DateFilterWrap>
                    <CartListWrap>
                        {
                            oldList.length <= 0 ?
                            <NoText>이전에 본 상품이 없습니다.</NoText>
                            :
                            oldList.map((value,index)=>{ 
                                return (
                                    <LatelyProduct handleSingleCheck={handleSingleCheck} rank={false} value={value} checkProduct={checkProduct}/>
                                );
                            })
                        }
                    </CartListWrap>
                </>
            </CartWrap>
        </Container>




    );
}

/* cart title */
const Container = styled.div`
    width: 100%;
`
const CartTitleWrap = styled.div`
    width: 100%;
    padding-top: calc(100vw*(10/414));
`
const CartTitleBox = styled.ul`
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: calc(100vw*(10/414));
`
const CartTitle = styled.li`
    position: relative;
    width: calc(100vw*(160/414));
    padding: calc(100vw*(14/414)) 0 calc(100vw*(24/414));
    margin: 0 calc(100vw*(4/414));
    background: #fff;
    font-size: calc(100vw*(15/414));
    font-weight: 900;
    color: #7e7e7e;
    text-align: center;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
`
const CartMarketTitle = styled(CartTitle)``
const CartShopTitle = styled(CartTitle)`
    color: #ED2939;
    &::after {
        content: '';
        position: absolute;
        display: block;
        width: 100%; height: calc(100vw*(20/414));
        background: #fff;
        z-index: 90;
    }
`

/* cart list */
const CartWrap = styled.div`
    position: relative;
    width: 100%; min-height: calc(100vh - calc(100vw*(160/414)));
    background: #fff;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    z-index: 10;
    padding-bottom: calc(100vw*(115/414));
`
const CartDelete = styled.div`
    width: 100%;
`
const LineBar = styled.div`
    width: calc(100vw*(392/414)); height: calc(100vw*(1/414));
    background: #d1d1d1;
    margin: 0 auto;
`
const CartListWrap = styled.div`
    width: 100%
`
const DateFilterWrap = styled.div`
    width: 100%;
    padding: calc(100vw*(5/414)) calc(100vw*(11/414)) 0;
`
const DateFilterText = styled.p`
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    padding: calc(100vw*(8/414)) calc(100vw*(20/414));
    border-bottom: calc(100vw*(1/414)) solid #d1d1d1;
`
const NoText = styled.p`
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    color: #707070;
    text-align: center;
    padding: calc(100vw*(60/414)) 0;
`


