//react
import React from 'react';
import { useState, useEffect } from 'react';
import { Link,useHistory } from "react-router-dom";

//css
import styled from "styled-components"

//page
import serverController from '../../../server/serverController';

export default function ShopPayAddAddress({setAdressShow, adrZipcode, adrAddress}) {
    const [next,setNext] = useState(false);
    const [memId,setMemId] = useState("");

    const [adrName,setAdrName] = useState("");
    const [adrAddressDetail,setAdrAddressDetail] = useState("");
    const [adrPhone,setAdrPhone] = useState("");
    const [adrMemo,setAdrMemo] = useState("");

    const adrNameChange = (e) =>{ setAdrName(e.target.value); }
    const adrAddressDetailChange = (e) =>{ setAdrAddressDetail(e.target.value); }
    const adrPhoneChange = (e) =>{ setAdrPhone(e.target.value); }
    const adrMemoChange = (e) =>{ setAdrMemo(e.target.value); }

    const checkNameVaildate = () =>{ return adrName.length > 1; };
    const checkAddressVaildate = () =>{ return adrAddress.length != 0 }
    const checkAdrDetailVaildate = () =>{ return adrAddressDetail.length != 0 }
    const checkPhoneVaildate = () =>{ return adrPhone.length != 0 }

    const history = useHistory();
    const goBack = () =>{
        history.goBack();
    }

    const openPostCode = () => {
        setAdressShow(true);
    }
    const closePostCode = () => {
        setAdressShow(false);
    }

    useEffect(()=>{
        serverController.connectFetchController(`user/mypage`,"GET",null,function(res){
        if(res.result == 1) {
            setMemId(res.list[0].mem_id);
        }});
    },[]);

    useEffect(()=>{
        if(checkNameVaildate() && checkAddressVaildate() && checkAdrDetailVaildate() && checkPhoneVaildate())
            setNext(true);
        else
            setNext(false);
    },[adrName,adrAddress,adrAddressDetail,adrPhone])

    const adrBtnClick = () =>{
        if(true){
            const formData = new FormData();
            formData.append('adr_type','0');
            formData.append('mem_id',memId);
            formData.append('adr_name',adrName);
            formData.append('adr_zipcode',adrZipcode);
            formData.append('adr_address1',adrAddress);
            formData.append('adr_address2',adrAddressDetail);
            formData.append('adr_phone',adrPhone);
            formData.append('adr_memo',adrMemo);
            serverController.connectFetchController('user/add_address',"POST",formData,function(res){
                if(res.result == 1) {
                    console.log(res);
                    return history.push("/shop/pay/address");
                } else {
                    alert("배송지 추가를 실패했습니다.");
                }
            });
        }
    }

  return (
        <Container>
            <AddressWrap>
                <AddressListWrap>
                    <SetList>
                        <SetListTitle>이름 :</SetListTitle>
                        <SetListText type="text" placeholder="이름를 입력하세요." value={adrName} onChange={adrNameChange}/>
                    </SetList>
                    <SetList>
                        <SetListTitle>주소 :</SetListTitle>
                        <SetListAddressWrap>
                            <AddresssearchWrap>
                                <AddresssearchInput type="text" value={adrZipcode} readonly/>
                                <AddresssearchBtn onClick={openPostCode}>주소검색</AddresssearchBtn>
                            </AddresssearchWrap>
                            <SetListAddress type="text" value={adrAddress} readonly/>
                            <SetListAddress type="text" placeholder="상세 주소를 입력하세요." value={adrAddressDetail} onChange={adrAddressDetailChange}/>
                        </SetListAddressWrap>
                    </SetList>
                    <SetList>
                        <SetListTitle>연락처 :</SetListTitle>
                        <SetListText type="number" placeholder="휴대폰번호를 입력하세요." value={adrPhone} onChange={adrPhoneChange}/>
                    </SetList>
                    <SetList>
                        <SetListTitle>배송요청사항 :</SetListTitle>
                        <SetListTextarea placeholder="배송요청사항을 입력하세요." value={adrMemo} onChange={adrMemoChange}></SetListTextarea>
                    </SetList>
                    <AddressBtnWrap>
                        <AddressBtn onClick={goBack}>취소</AddressBtn>
                        <AddressBtn adrBtnOn={next} onClick={adrBtnClick}>저장</AddressBtn>
                    </AddressBtnWrap>
                </AddressListWrap>
            </AddressWrap>
        </Container>
  );
}

//css
const Container = styled.div` 
    width: 100%;
`
const AddressWrap = styled.div` 
    width: 100%;
    padding: calc(100vw*(25/414)) calc(100vw*(15/414));
`
const AddressListWrap = styled.div` 
    width: 100%;
    padding: calc(100vw*(50/414)) calc(100vw*(30/414)) calc(100vw*(80/414));
    background: #fff;
    border: calc(100vw*(2/414)) solid #efefef;
    border-radius: calc(100vw*(17/414));
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
`
const SetList = styled.div`
    margin: calc(100vw*(20/414)) 0;
`
const SetListTitle = styled.p`
    width: 100%;
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    margin-bottom: calc(100vw*(8/414));
`
const SetListText = styled.input`
    width: 100%; height: calc(100vw*(40/414));
    font-size: calc(100vw*(14/414));
    font-weight: bold;
    text-align: left;
    text-indent: calc(100vw*(15/414));
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(3/414));
    color: #4b4b4b;
`
const SetListTextarea = styled.textarea`
    width: 100%; height: calc(100vw*(115/414));
    font-size: calc(100vw*(14/414));
    font-weight: bold;
    text-align: left;
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(3/414));
    color: #4b4b4b;
    padding: calc(100vw*(9/414)) calc(100vw*(18/414));
`
const SetListAddressWrap = styled.div`
    width: 100%;
`
const SetListAddress = styled.input`
    width: 100%; height: calc(100vw*(40/414));
    font-size: calc(100vw*(14/414));
    font-weight: bold;
    text-align: left;
    text-indent: calc(100vw*(15/414));
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(3/414));
    color: #4b4b4b;
    margin: calc(100vw*(3/414)) 0;
`
const AddresssearchWrap = styled.div`
    display: flex;
    align-items: center;
`
const AddresssearchInput = styled.input`
    width: calc(100% - calc(100vw*(100/414))); height: calc(100vw*(40/414));
    font-size: calc(100vw*(14/414));
    font-weight: bold;
    text-align: left;
    text-indent: calc(100vw*(15/414));
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(3/414));
    color: #4b4b4b;
    margin: calc(100vw*(3/414)) 0;
`
const AddresssearchBtn = styled.button`
    width: calc(100vw*(100/414)); height: calc(100vw*(40/414));
    color: #fff;
    background: #646464;
    border: calc(100vw*(2/414)) solid #979797;
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    margin-left: calc(100vw*(10/414));
    border-radius: calc(100vw*(3/414));
`
const AddressBtnWrap = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: calc(100vw*(40/414));
`
const AddressBtn = styled.button`
    width: calc(100vw*(130/414)); height: calc(100vw*(48/414));
    color: #fff;
    background: #646464;
    border: calc(100vw*(2/414)) solid #979797;
    border-radius: calc(100vw*(3/414));
    font-size: calc(100vw*(18/414));
    font-weight: bold;
    margin: 0 calc(100vw*(6/414));
    ${({adrBtnOn})=>{
        return adrBtnOn ? 
        `
            background: #ed2939;
            border-color: #bc343f;
        `
        :
        `
            background: #b4b4b4;
            border-color: #a5a5a5;
        `
    }}
`