//react
import {Link} from "react-router-dom";
import React ,{useState,useEffect} from 'react';

//css
import styled from "styled-components"

//page
import serverController from '../../../server/serverController';
import MyQnaLayout from "./MyQna";
import DateFilter from "../../common/product/DateFilter";

export default function MyQnaList() {
    const [qnaList,setQnaList] = useState([]);

    useEffect(()=>{
        serverController.connectFetchController(`qna/selectOne?type=0`,"get",null,function(res){
        if(res.result == 1) {
            setQnaList(res.list)
        }});
    },[]);

    const updateBtnon = (e) => {
        serverController.connectFetchController(`qna/selectOne?type=${e}`,"get",null,function(res){
        if(res.result == 1) {
            setQnaList(res.list)
            console.log(e);
            console.log("업데이트");
        }});
    }
    return(
        <Container>
            <MyReviewTitleWrap>
                <MyReviewTitleBox>
                    <ReviewTitle><Link to={"/myReview"}>리뷰</Link></ReviewTitle>
                    <QnaTitle><Link to={"/myQna"}>QnA</Link></QnaTitle>
                </MyReviewTitleBox>
            </MyReviewTitleWrap>
            <MyReviewWrap>
                <DateFilter updateBtnon={updateBtnon}/>
                <OrderListWrap>
                    {
                        qnaList.length > 0 ?
                        qnaList.map((value,index)=>{ 
                            return (
                                <MyQnaLayout rank={true} value={value}/>
                            );
                        })
                        :
                        <OrderNoneWrap>
                            <OrderNoneText>작성하신 문의내역이 없습니다.</OrderNoneText>
                        </OrderNoneWrap>
                    }
                </OrderListWrap>
            </MyReviewWrap>
        </Container>
    );
}

const Container = styled.div`
    width: 100%;
`
const MyReviewTitleWrap = styled.div`
    width: 100%;
    padding-top: calc(100vw*(10/414));
`
const MyReviewTitleBox = styled.ul`
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: calc(100vw*(10/414));
`
const CartTitle = styled.li`
    position: relative;
    width: calc(100vw*(160/414));
    padding: calc(100vw*(14/414)) 0 calc(100vw*(24/414));
    margin: 0 calc(100vw*(4/414));
    background: #fff;
    font-size: calc(100vw*(15/414));
    font-weight: 900;
    color: #7e7e7e;
    text-align: center;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
`
const ReviewTitle = styled(CartTitle)``
const QnaTitle = styled(CartTitle)`
    color: #ED2939;
    &::after {
        content: '';
        position: absolute;
        display: block;
        width: 100%; height: calc(100vw*(20/414));
        background: #fff;
        z-index: 90;
    }
`
/* list */
const MyReviewWrap = styled.div`
    position: relative;
    width: 100%; min-height: calc(100vh - calc(100vw*(150/414)));
    background: #fff;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    z-index: 10;
    margin-top: calc(100vw*(-10/414));
    padding-bottom: calc(100vw*(100/414));
`
const OrderListWrap = styled.div`
    width: 100%;
    padding: calc(100vw*(15/414)) calc(100vw*(15/414));
`
const OrderNoneWrap = styled.div`
    width: 100%;
`
const OrderNoneText = styled.p`
    text-align: center;
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    color: #979697;
    padding: calc(100vw*(28/414)) 0;
`