import {Link} from "react-router-dom";
import React ,{useState,useEffect} from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';

//css
import 'swiper/swiper-bundle.css';
import styled from "styled-components"

//img
import MarketSlideImg1 from "../../img/market_slide_img1.png"
import productImg from "../../img/product_img.png"
import IcoReadMore from "../../img/ico_read_more.png"

//page
import serverController from '../../server/serverController';
import PurchaseProduct from "../common/product/PurchaseProduct";


export default function MainRecentPurchases({rank,prd_id}) {
    const [itemList,setItemList] = useState([]);
    
    useEffect(()=>{
        serverController.connectFetchController(`products/hit?prd_type=1`,"GET",null,function(res){
        if(res.result == 1) {
            setItemList(res.info)
        }});
    },[]);

  return (
        <Container>
            {
                rank ? 
                <Mainline></Mainline>
                :
                null
            }
            <MainTitleWrap>
                <MainTitle>자주 구매한 상품<MainText>우리마트의 이런 제품들은 어떠세요?</MainText></MainTitle>
                <Link to={"/marketBest"}>
                    <ReadMoreText>바로가기 <IconReadMore src={IcoReadMore} alt="read more"/></ReadMoreText>
                </Link>
            </MainTitleWrap>

            {/* Purchase slide */}
            <PurchaseWrap className="PurchaseWrap">
                {
                    itemList.length > 0 ?
                    <Swiper spaceBetween={5}  slidesPerView={2.2}>
                        {
                            itemList.map((value,index)=>{ 
                                return (
                                    <SwiperSlide>
                                        <Link to={`/shop/product/${value.prd_type}/${value.prd_id}`}>
                                            <Slide>
                                                <PurchaseProduct value={value}/> 
                                            </Slide>
                                        </Link>
                                    </SwiperSlide>
                                );
                            })
                        }
                    </Swiper>
                    :
                    null

                }
                
                    
            </PurchaseWrap>
        </Container>
  );
}


/* main title */
const Container = styled.div` 
    width:100%;
`
const Mainline = styled.span`
    display: block;
    width: calc(100vw*(390/414)); height: calc(100vw*(3/414));
    background: #d9d9d9;
    margin: 0 auto;
`
const MainTitleWrap = styled.div` 
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    width:100%;
    padding-top: calc(100vw*(20/414));
    padding-left: calc(100vw*(22/414));
    margin-bottom: calc(100vw*(17/414));
`
const MainTitle = styled.h2`
    font-size: calc(100vw*(15/414));
    line-height: 1.2;
    font-weight: 900;
`
const MainText = styled.p`
    width: 100%;
    font-size: calc(100vw*(12/414));
    font-weight: normal;
`
const ReadMoreText = styled.a`
    font-size: calc(100vw*(12/414));
    font-weight: bold;
    margin-right: calc(100vw*(22/414));
`
const IconReadMore = styled.img`
    width: calc(100vw*(6/414)); height: calc(100vw*(8/414));
    margin-left: calc(100vw*(5/414));
`

/* Purchase slide */
const PurchaseWrap = styled.div` 
    width: 100%;
    padding-bottom: calc(100vw*(36/414));
`
const Slide = styled.div`
    width: 100%;
    border-radius: calc(100vw*(16/414));
    overflow: hidden;
    background: #fff;
`















/* market slide */
const SlideW = styled.div`
    position: relative;
    height: calc(100vw*(122/414));
    border-radius: calc(100vw*(16/414));
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    overflow: hidden;
`
const MarketSlideImgA = styled.img`
    position: absolute; top: 0; left: 0;
    width: 100%; height: 100%; object-fit: cover;
`
const MarketSlideTextboxA = styled.div`
    position: absolute; top: 0; left: 0;
    width: 100%; height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
`
const MarketSlideTextA = styled.p`
    display: block;
    font-size: calc(100vw*(13/414));
    text-align: center;
    color: #fff;
    line-height: 1.1;
`