//react
import {Link} from "react-router-dom";
import {useState, useEffect} from 'react';

//css
import styled from "styled-components"

const alarmNoticeList = [
    {   alarm_id: 1,
        alarm_text: "업데이트 알림 알려드립니다.",
        checked: false
    },
    {
        alarm_id: 2,
        alarm_text: "설날연휴 배송안내드립니다.",
        checked: false
    },
    {
        alarm_id: 3,
        alarm_text: "설날연휴 좋아.",
        checked: false
    }
]

export default function AlarmList() {
    
    const [isSelectAll,setIsSelectAll] = useState(false);
    const [alarmList,setAlarmList] = useState(alarmNoticeList);

    const selectAllCartItem = (checked) =>{
        alarmList.map(((value)=>{  value.checked = checked; }));
        setAlarmList(JSON.parse(JSON.stringify(alarmList)));
        setIsSelectAll(checked);
    }


    return (
            <Container>
                <AlarmListWrap>
                    <AllCheckWrap>
                        <Checks>
                            <ChkBox checked={isSelectAll} onChange={(e)=>{selectAllCartItem(e.target.checked)}} type="checkbox" name="" id="AllChk"/>
                            <ChkBoxLabel htmlFor="AllChk"></ChkBoxLabel>
                            <CheckboxText htmlFor="AllChk">전체선택</CheckboxText>
                        </Checks>
                        <>
                            <DeleteBtn>삭제</DeleteBtn>
                        </>
                    </AllCheckWrap>
                    <AlarmNoticeWrap>
                    {
                        alarmList.map((value,index)=>{ 
                            function changeCheckedState(){
                                alarmList[index].checked = !alarmList[index].checked;
                                var checkedCount = 0;
                                alarmList.map(value=>{ checkedCount += value.checked ? 1 : 0 ;});
                                                    //checkedCount = checkedCount  + (value.chekced == true ? 1 : 0 )
                                setIsSelectAll(checkedCount == alarmList.length);
                                setAlarmList(JSON.parse(JSON.stringify(alarmList)));
                            }

                            return (
                                <AlarmNoticeListWrap>
                                    <Checks>
                                        <ChkBox id={value.alarm_id} checked={value.checked} type="checkbox" onChange={(e)=>{changeCheckedState(false)}}/>
                                        <ChkBoxLabel htmlFor={value.alarm_id}></ChkBoxLabel>
                                    </Checks>
                                    <AlarmListText changeCheckedState={changeCheckedState}>{value.alarm_text}</AlarmListText>
                                </AlarmNoticeListWrap>
                            );
                        })
                    }

                    </AlarmNoticeWrap>
                    
                </AlarmListWrap>
            </Container>
    );
}

//css
const Container = styled.div` 
    position: relative;
    width: 100%; min-height: calc(100vh - calc(100vw*(80/414)));
    background: #fff;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    z-index: 10;
    margin-top: calc(100vw*(12/414));
    padding-bottom: calc(100vw*(100/414));
`
const AlarmListWrap = styled.div`
    width: 100%; 
    padding-top: calc(100vw*(20/414));
    padding: calc(100vw*(18/414)) calc(100vw*(11/414)) 0;
`
const AllCheckWrap = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: calc(100vw*(12/414)) calc(100vw*(15/414));
    border-bottom: calc(100vw*(1/414)) solid #d1d1d1;
`
const Checks = styled.div`
    position: relative;
`
const ChkBox = styled.input`
    position: absolute;
    width: calc(100vw*(1/414)); height: calc(100vw*(1/414));
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip:rect(0,0,0,0);
    border: 0;
    &:checked + label::after {
        content: '';
        position: absolute; top: calc(100vw*(-3/414)); left: calc(100vw*(5/414));
        width: calc(100vw*(8/414)); height: calc(100vw*(8/414));
        background: #ed3342;
        border-radius: calc(100vw*(2/414));
    }
`
const ChkBoxLabel = styled.label`
    display: inline-block;
    position: relative;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    text-indent: -9999px;
    &&::before {
        content: '';
        position: absolute; left: 0; top: calc(100vw*(-8/414));
        width: calc(100vw*(16/414)); height: calc(100vw*(16/414));
        text-align: center;
        background: #fafafa;
        border: calc(100vw*(1/414)) solid #ed3342;
        border-radius: calc(100vw*(4/414));
    }
`
const CheckboxText = styled.label`
    font-size: calc(100vw*(13/414));
    color: #707070;
    margin-left: calc(100vw*(35/414));
`
const DeleteBtn = styled.button`
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    color: #707070;
    background: #f7f7f7;
    padding: calc(100vw*(6/414)) calc(100vw*(15/414));
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(8/414));
`
const AlarmNoticeWrap = styled.div`
    width: 100%;
`
const AlarmNoticeListWrap = styled.div`
    width: 100%;
    display: flex;
    align-items: center;
    padding: calc(100vw*(24/414)) calc(100vw*(16/414));
    border-bottom: calc(100vw*(1/414)) solid #e4e4e4;
`
const AlarmListText = styled.p`
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    padding-left: calc(100vw*(35/414));
`