//react
import {Link} from "react-router-dom";
import {useState, useEffect} from 'react';

//css
import styled from "styled-components"

//page
import serverController from '../../../server/serverController';

//숫자 3자리수 콤마 코드
function numberWithCommas(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

export default function ShopPayPoint({usePoint, setUsePoint, priceInfo}) {
    const [myInfo,setmyInfo] = useState([]);
    const [myPoint,setMyPoint] = useState([]);

    const pointChange = (e) =>{
        setUsePoint(e.target.value);
        if(e.target.value > myPoint){
            setUsePoint(myPoint)
        }
        if(e.target.value == 0){
            setUsePoint(0)
        }
        return usePoint <= myPoint;
    }
    
    useEffect(()=>{
        serverController.connectFetchController(`user/mypage`,"GET",null,function(res){
        if(res.result == 1) {
            setmyInfo(res.list[0])
            setMyPoint(res.list[0].pt_point)
        }});
    },[]);

    const onClickUse = () => {
        setUsePoint(myPoint)
    }
    
    return (
        <Container>
            <ShopPayPointWrap>
                <ShopPayInfoTitleWrap>
                    <ShopPayInfoTitle>적립금</ShopPayInfoTitle>
                </ShopPayInfoTitleWrap>
                <ShopPayInfoInner>
                    <ShopPayInfoBox>
                        <ShopPayInfoBoxTitleRed>예상 적립금</ShopPayInfoBoxTitleRed>
                        <ShopPayInfoBoxTextRed>{numberWithCommas(Math.floor(priceInfo.totalPrice * 1 / 100))}원</ShopPayInfoBoxTextRed>
                    </ShopPayInfoBox>
                    <ShopPayInfoBox>
                        <ShopPayPointBtnWrap>
                            <ShopPayInfoBoxTitle>적립금 사용</ShopPayInfoBoxTitle>
                            <ShopPayPointBtn onClick={() => onClickUse()}>전액사용</ShopPayPointBtn>
                        </ShopPayPointBtnWrap>
                        <ShopPayPointBtnWrap>
                            <ShopPayPointInputWrap>
                                <ShopPayPointInput type="number" value={usePoint} onChange={pointChange}/>
                                <ShopPayInfoBoxText>원</ShopPayInfoBoxText>
                            </ShopPayPointInputWrap>
                        </ShopPayPointBtnWrap>
                        <RemainingPointWrap>
                            <RemainingPointText>잔여 적립금 : {numberWithCommas(myInfo.pt_point - usePoint)}원</RemainingPointText>
                        </RemainingPointWrap>
                    </ShopPayInfoBox>
                </ShopPayInfoInner>
            </ShopPayPointWrap>
        </Container>
    );
}

//css
const Container = styled.div` 
    position: relative;
    width: 100%;
    background: #fff;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    z-index: 10;
    margin-top: calc(100vw*(10/414));
`
const ShopPayPointWrap = styled.div`
    width: 100%;
    padding-top: calc(100vw*(20/414));
    padding: calc(100vw*(24/414)) calc(100vw*(36/414)) calc(100vw*(28/414));
`
const ShopPayInfoTitleWrap = styled.div`
    width: 100%;
`
const ShopPayInfoTitle = styled.p`
    font-size: calc(100vw*(18/414));
    font-weight: bold;
    margin-bottom: calc(100vw*(5/414));
`
const ShopPayInfoInner = styled.div`
    position: relative;
    width: 100%;
`
const ShopPayInfoBox = styled.div`
    display: flex;
    align-items: baseline;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: calc(100vw*(12/414)) 0;
`
const ShopPayInfoBoxTitle = styled.p`
    font-size: calc(100vw*(13/414));
    font-weight: bold;
`
const ShopPayInfoBoxTitleRed = styled(ShopPayInfoBoxTitle)`
    color: #ed2939;
`
const ShopPayInfoBoxText = styled.p`
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    word-break: keep-all;
    text-align: right;
`
const ShopPayInfoBoxTextRed = styled(ShopPayInfoBoxText)`
    color: #ed2939;
`
const ShopPayPointBtnWrap = styled.div`
    display: flex;
    align-items: center;
`
const ShopPayPointInputWrap = styled(ShopPayPointBtnWrap)``
const ShopPayPointInput = styled.input`
    width: calc(100vw*(116/414)); height: calc(100vw*(27/414));
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(3/414));
    margin-right: calc(100vw*(6/414));
    text-align: right;
    padding-right: calc(100vw*(3/414));
`
const ShopPayPointBtn = styled.button`
    height: calc(100vw*(27/414));
    padding: 0 calc(100vw*(8/414));
    font-size: calc(100vw*(13/414));
    color: #4b4b4b;
    font-weight: bold;
    border: calc(100vw*(1/414)) solid #4b4b4b;
    border-radius: calc(100vw*(3/414));
    margin-left: calc(100vw*(6/414));
`
const RemainingPointWrap = styled.div`
    width: 100%;
`
const RemainingPointText = styled.p`
    width: 100%;
    text-align: right;
    padding: calc(100vw*(5/414)) 0;
    font-size: calc(100vw*(13/414));
    font-weight: bold;
`

