//react
import {Link} from "react-router-dom";
import React ,{useState,useEffect} from 'react';

//css
import styled from "styled-components"

//page
import appInfoController from '../../../server/appInfoController'

const orderText = {
    1 : { title : "상품 준비중" },
    2 : { title : "배송중" },
    3 : { title : "배송완료" },
    4 : { title : "배송완료" },
    5 : { title : "배송완료" }
}

//숫자 3자리수 콤마 코드
function numberWithCommas(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}
const btnlist = (txn_state ,openrevOptionModal) => {
    switch(txn_state){
        case 1 : 
                return (
                    <OrderBtnWrap>
                        <OrderBtnBasic>배송지변경</OrderBtnBasic>
                        <OrderBtnBasic>주문취소</OrderBtnBasic>
                    </OrderBtnWrap>
                );
        case 2 : 
                return (
                    <OrderBtnWrap>
                        <OrderBtnRed>배송조회</OrderBtnRed>
                    </OrderBtnWrap>
                );
        case 3 : 
                return (
                    <OrderBtnWrap>
                        <OrderBtnBasic>교환신청</OrderBtnBasic>
                        <OrderBtnBasic>반품신청</OrderBtnBasic>
                        <OrderBtnRed onClick={()=>{openrevOptionModal(true)}}>리뷰적기</OrderBtnRed>
                    </OrderBtnWrap>
                );
        case 4 : 
                return (
                    <OrderBtnWrap>
                        <OrderBtnRed onClick={()=>{openrevOptionModal(true)}}>리뷰적기</OrderBtnRed>
                        <OrderBtnBasic>구매확정</OrderBtnBasic>
                        <OrderBtnText>00일이 지난후에는 교환 및 반품이 불가능 합니다.</OrderBtnText>
                    </OrderBtnWrap>
                );
        case 5 : 
                return (
                    <OrderBtnWrap>
                        <OrderBtnRed>재구매</OrderBtnRed>
                    </OrderBtnWrap>
                );
        default : return null;
    }
}

export default function OrderProduct({openprdOptionModal, openrevOptionModal, value}) {
    const cart_items = JSON.parse(value.cart_items);
    const [priceInfo,setPriceInfo] = useState({productCount : 0, totalPrice : 0, originalPrice: 0,  price : 0 , optionPrice : 0 , salePrice : 0});

    console.log(value.cart_items);
    useEffect(()=>{
        setPriceInfo(getPrice(cart_items));
    },[]);

    //최종 리스트에 대한 돈계산.
    function getPrice(option){
        let price = 0;
        let optionPrice = 0;
        let salePrice = 0;
        let totalPrice = 0;
        let originalPrice = 0;
        let options = option.data;

        for(let j = 0; j<options.length;j++){
        
            var p = 0;
            var ogp = 0;
            var op = 0;
            var sp = 0;
            let optionList = options[j].options.optionList;

            for(let calKey in optionList){
                for(let z =0;z<	optionList[calKey].length ;z++){
                    op += optionList[calKey][z].opt_price;
                }
            }

            if(options[j].options.isOptional == false){
                ogp += options[j].prd_price;
                p += Math.round((options[j].prd_price) - ((options[j].prd_price * options[j].prd_sale_rate) / 100));
                sp += Math.round(((options[j].prd_price * options[j].prd_sale_rate) / 100));
            }

            originalPrice += (ogp * options[j].count);
            price += (p * options[j].count);
            optionPrice += (op * options[j].count);
            salePrice += (sp * options[j].count);
            totalPrice += (p + op) *  options[j].count;
        }

        return { totalPrice : totalPrice, originalPrice: originalPrice,  price : price , optionPrice : optionPrice , salePrice : salePrice };
    }

    return (
        <Container>
            <OrderDateTitleWrap>
                <OrderDate>{value.tax_payment_date}</OrderDate>
                <OrderDetailBtn>
                    <Link to={"/orderDetail"}>주문상세보기</Link>
                </OrderDetailBtn>
            </OrderDateTitleWrap>
            <OrderProductTitleWrap>
                <OrderTitleText>{orderText[3].title} {/*orderText[value.txn_state].title*/} </OrderTitleText>
                <OrderTitleDate>1.2(목) 도착 예정</OrderTitleDate>
            </OrderProductTitleWrap>
            <CartListWrap>
                <CartList>
                        <CartListImg>
                            <Link to={`/shop/product/${value.prd_type}/${value.prd_id}`}>
                                <img src={appInfoController.ImagePath + value.prd_img} alt="product image"/>
                            </Link>
                        </CartListImg>
                    <CartListText>
                            <ProductTitle>
                                <Link to={`/shop/product/${value.prd_type}/${value.prd_id}`}>
                                    <ProductName>{cart_items.prd_title} 외 {cart_items.prd_count ? cart_items.prd_count : "없음"}건</ProductName>
                                </Link>
                                <ProductText>{cart_items.prd_detail1 ? cart_items.prd_detail1 : "없음"}</ProductText>
                                <ProductTextDetail>{cart_items.prd_detail2 ? cart_items.prd_detail2 : "없음"}</ProductTextDetail>
                                <TotalPrice>{numberWithCommas(priceInfo.price)}원 <ProductQty>{cart_items.prd_qty ? cart_items.prd_qty : ""}개</ProductQty></TotalPrice>
                            </ProductTitle>
                            <CartBtnWrap onClick={()=>{openprdOptionModal(true)}}>
                                <IConMenuList></IConMenuList>
                                <IConMenuList></IConMenuList>
                                <IConMenuList></IConMenuList>
                            </CartBtnWrap>
                    </CartListText>
                </CartList>
            </CartListWrap>
            {
                btnlist(3 , openrevOptionModal)
            }
        </Container>
  );
}

