//react
import {Link} from "react-router-dom";
import React ,{useState,useEffect} from 'react';
import { useSelector } from 'react-redux'; // 불러오기

//css
import styled from "styled-components"

//page
import serverController from '../../../server/serverController';
import CartProduct from "../../common/product/CartProduct";
import CartSelect from './CartSelect';
import CartOrderPrice from './CartOrderPrice';

export default function CartMarket() {
    const [tabCartMarket,tabCartShop] = useState(true);
    const [itemList,setItemList] = useState([]);
    const [checkProduct,setCheckProduct] = useState([]);
    const [priceInfo,setPriceInfo] = useState({productCount : 0, totalPrice : 0, originalPrice: 0,  price : 0 , optionPrice : 0 , salePrice : 0});
    const [reduxZipcode,setReduxZipcode] = useState([]);

    const idArray = [];
    const openUserData = useSelector(state=>{ return state.userInfo}); //불러오기

    useEffect(()=>{
        serverController.connectFetchController(`cart/list?cart_type=1`,"GET",null,function(res){
            if(res.result == 1) {
                setItemList(res.list);
        }});
        serverController.connectFetchController(`user/fee?zipcode=${openUserData.ardId.adr_zipcode}`,"GET",null,function(res){
            if(res.result == 1) {
                setReduxZipcode(res.mart_fee);
        }});
    },[]);

    
    useEffect(()=>{
        getTotalPrice();
    },[checkProduct]);
    
    const handleAllCheck = (checked) =>{
        if(checked){
            var value = itemList;
            for(let i in value){
                idArray.push(value[i].cart_id);
            }
            setCheckProduct(idArray);
        }else{
            setCheckProduct([]); 
        }
    }

    const handleSingleCheck = (checked, id)=>{ 
        if(checked == true){
            setCheckProduct([...checkProduct,id]);   
        }else{
            setCheckProduct(checkProduct.filter((el) => el !== id))
        }
    }
    const clickDelete = () => {
        alert("장바구니에서 선택하신 상품이 삭제되었습니다.");
        checkProduct.map((cart_id)=>{
            serverController.connectFetchController(`cart/deleteOne?cart_id=${cart_id}`,"DELETE",null,function(res){
            if(res.result == 1) {
                serverController.connectFetchController(`cart/list?cart_type=1`,"GET",null,function(res){
                if(res.result == 1) {
                    setItemList(res.list);
                }});
            }});
        })
    }

    //장바구니에 담아진 최종 리스트에 대한 돈계산.
    function getPrice(option){
        let price = 0;
        let optionPrice = 0;
        let salePrice = 0;
        let totalPrice = 0;
        let originalPrice = 0;
        let options = option.data;
 
        for(let j = 0; j<options.length;j++){
          
            var p = 0;
            var ogp = 0;
            var op = 0;
            var sp = 0;
            let optionList = options[j].options.optionList;
    
            for(let calKey in optionList){
              for(let z =0;z<	optionList[calKey].length ;z++){
                op += optionList[calKey][z].opt_price;
              }
            }
    
            if(options[j].options.isOptional == false){
              ogp += options[j].prd_price;
              p += Math.round((options[j].prd_price) - ((options[j].prd_price * options[j].prd_sale_rate) / 100));
              sp += Math.round(((options[j].prd_price * options[j].prd_sale_rate) / 100));
            }
    
            originalPrice += (ogp * options[j].count);
            price += (p * options[j].count);
            optionPrice += (op * options[j].count);
            salePrice += (sp * options[j].count);
            totalPrice += (p + op) *  options[j].count;
        }
    
        return { totalPrice : totalPrice, originalPrice: originalPrice,  price : price , optionPrice : optionPrice , salePrice : salePrice };
     }
 
     function getTotalPrice(){
 
        let productInfoTemp = {
            productCount : 0, totalPrice : 0, originalPrice: 0,  price : 0 , optionPrice : 0 , salePrice : 0
        };
 
        itemList.map((value)=>{
        
            if(checkProduct.indexOf(value.cart_id) == -1)
                return;

            let cart_items = JSON.parse(value.cart_items);
            let info = getPrice(cart_items);
            
            productInfoTemp.totalPrice += info.totalPrice * cart_items.count;
            productInfoTemp.originalPrice += info.originalPrice * cart_items.count;
            productInfoTemp.price += info.price * cart_items.count;
            productInfoTemp.optionPrice += info.optionPrice * cart_items.count;
            productInfoTemp.salePrice += info.salePrice * cart_items.count;
            productInfoTemp.productCount++;
        });
        
        setPriceInfo(productInfoTemp);
    }
 
     function updateItem(list){
        setItemList(list);
        getTotalPrice();
    }

    return(
        <Container>
            <CartTitleWrap>
                <CartTitleBox>
                    {/* <CartMarketTitle tabCartMarket={tabCartMarket} onClick={()=>{tabCartShop(true)}}>마켓 바로배달</CartMarketTitle>
                    <CartShopTitle tabCartMarket={tabCartMarket} onClick={()=>{tabCartShop(false)}}>쇼핑몰 택배발송</CartShopTitle> */}
                    <CartMarketTitle><Link to={"/cartMarket"}>마켓 바로배달</Link></CartMarketTitle>
                    <CartShopTitle><Link to={"/cartMall"}>쇼핑몰 택배발송</Link></CartShopTitle>
                </CartTitleBox>
            </CartTitleWrap>
            <CartWrap>
                <CartDelete>
                    <CartSelect handleAllCheck={handleAllCheck} clickDelete={clickDelete} itemList={itemList} checkProduct={checkProduct} />
                </CartDelete>
                <LineBar></LineBar>
                
                <CartListWrap>
                    {
                        itemList.length <= 0 ?
                        <CartNoneText>장바구니에 담긴 상품이 없습니다.</CartNoneText>
                        :
                        itemList.map((value,index)=>{
                            const updateCartItemList = (cart_items) => {
                                let list = itemList;
                                list[index].cart_items = JSON.stringify(cart_items);
                                updateItem(list);
                            }
                            return <CartProduct  updateCartItemList={updateCartItemList} handleSingleCheck={handleSingleCheck} rank={true} value={value} checkProduct={checkProduct} />
                        })
                    }
                </CartListWrap>
                <CartPriceWrap>
                    <CartOrderPrice reduxZipcode={reduxZipcode} priceInfo={priceInfo}/>
                </CartPriceWrap>
            </CartWrap>
        </Container>

    );
}

/* cart title */
const Container = styled.div`
    width: 100%;
`
const CartTitleWrap = styled.div`
    width: 100%;
    padding-top: calc(100vw*(10/414));
`
const CartTitleBox = styled.ul`
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: calc(100vw*(10/414));
`
const CartTitle = styled.li`
    position: relative;
    width: calc(100vw*(160/414));
    padding: calc(100vw*(14/414)) 0 calc(100vw*(24/414));
    margin: 0 calc(100vw*(4/414));
    background: #fff;
    font-size: calc(100vw*(15/414));
    font-weight: 900;
    color: #7e7e7e;
    text-align: center;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
`
const CartMarketTitle = styled(CartTitle)`
    color: #ED2939;
    &::after {
        content: '';
        position: absolute;
        display: block;
        width: 100%; height: calc(100vw*(20/414));
        background: #fff;
        z-index: 90;
    }
`
const CartShopTitle = styled(CartTitle)``

/* cart list */
const CartWrap = styled.div`
    position: relative;
    width: 100%; min-height: calc(100vh - calc(100vw*(160/414)));
    background: #fff;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    z-index: 10;
`
const CartDelete = styled.div`
    width: 100%;
`
const LineBar = styled.div`
    width: calc(100vw*(392/414)); height: calc(100vw*(1/414));
    background: #d1d1d1;
    margin: 0 auto;
`
const CartListWrap = styled.div`
    width: 100%;
`
const CartNoneText = styled.div`
    width: 100%;
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    color: #7e7e7e;
    text-align: center;
    padding: calc(100vw*(50/414)) 0;
`
/* cart price */
const CartPriceWrap = styled.div`
    width: 100%;
`