//react
import {Link} from "react-router-dom";
import React ,{useState,useEffect} from 'react';

//css
import styled from "styled-components"

//img
import CustomerImg from "../../../../img/customer_img.png"
import IconstarOn from "../../../../img/ico_star_on.svg"
import IconstarOff from "../../../../img/ico_star_off.svg"

//page
import serverController from '../../../../server/serverController';
import ReviewLayout from "../../../common/board/Review";

//숫자 3자리수 콤마 코드
function numberWithCommas(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

const getStarImageList = (count) =>{
    let imageList = [];
    for(var i =0;i < 5;i++){
        imageList.push(
            <Stars>
                <img src={i < count ? IconstarOn : IconstarOff} alt=""/>
            </Stars>
        )
    }
    return imageList;
}

export default function ShopProductReview({prd_id, rev_id}) {
    const [reviewList,setReviewList] = useState([]);
    const [bestBtnColor,setBestBtnColor] = useState(true);
    const [averageStar,setAverageStar] = useState(0);


    useEffect(()=>{
        console.log(prd_id);
        serverController.connectFetchController(`review/selectPrd?type=2&prd_id=${prd_id}`,"GET",null,function(res){
        if(res.result == 1) {
            setReviewList(res.list)

            let ave = 0;
            res.list.map((value,index) => {
                //ave = ave + value.rev_score;
                ave += value.rev_score;
            })
            setAverageStar(Math.floor(ave / res.list.length));
        }});
    },[]);

    const bestReviewClick = (e) => {
        if(e == true){
            serverController.connectFetchController(`review/selectPrd?type=2&prd_id=${prd_id}`,"GET",null,function(res){
            if(res.result == 1) {
                setReviewList(res.list)
                setBestBtnColor(true);
            }});
        } else {
            serverController.connectFetchController(`review/selectPrd?type=1&prd_id=${prd_id}`,"GET",null,function(res){
            if(res.result == 1) {
                setReviewList(res.list)
                setBestBtnColor(false);
            }});
        }
    } 

    return (
        <Container>
            <ShopLine></ShopLine>

            <ReviewWrap>
                <ReviewTitleWrap>
                    <StarsWrap>
                        {
                            getStarImageList(averageStar)
                        }
                    </StarsWrap>
                    <ReviewTitle>상품평 ({numberWithCommas(reviewList.length)})</ReviewTitle>
                </ReviewTitleWrap>
                <ReviewImgListWrap>
                    <ReviewImgWrap>
                        <ReviewImage src={CustomerImg} alt="review image"/>
                    </ReviewImgWrap>
                </ReviewImgListWrap>

                <ReviewFilterWrap>
                    <FilterBestBtn bestBtnColor={bestBtnColor} onClick={()=>{bestReviewClick(true)}}>베스트순</FilterBestBtn>
                    <FilterLatelyBtn bestBtnColor={bestBtnColor} onClick={()=>{bestReviewClick(false)}}>최신순</FilterLatelyBtn>
                </ReviewFilterWrap>
                <ReviewLine></ReviewLine>
                <ReviewInnerWrap>
                    {
                        reviewList.length > 0 ?
                        reviewList.map((value,index)=>{ 
                            return (
                            <ReviewLayout rank={false} value={value}/>
                            )
                        })
                        :
                        <RevNoneWrap>
                            <RevNoneText>작성된 리뷰가 없습니다.</RevNoneText>
                        </RevNoneWrap>

                    }
                </ReviewInnerWrap>
            </ReviewWrap>
        </Container>
    );
}


const Container = styled.div`
    width: 100%;
`
const ReviewWrap = styled.div`
    width: 100%; min-height: calc(100vh - calc(100vw*(90/414)));
    background: #fff;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0,0,0,0.16);
    padding-bottom: calc(100vw*(80/414));
`
const ShopLine = styled.div`
    width: 100%;
    height: calc(100vw*(14/414));
    background: #f7f7f7;
`
const ReviewTitleWrap = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: calc(100vw*(30/414)) calc(100vw*(25/414));
`
const ReviewTitle = styled.p`
    font-size: calc(100vw*(18/414));
    font-weight: bold;
`
const StarsWrap = styled.div`
    display: flex;
`
const Stars = styled.span`
    display: block;
    width: calc(100vw*(21/414));
    margin: 0 calc(100vw*(1/414));
`

/* review img list */
const ReviewImgListWrap = styled.div`
    width: 100%;
    padding: calc(100vw*(15/414)) 0 calc(100vw*(35/414));
    display: flex;
    white-space: nowrap;
    overflow-y: visible;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: none;
    ::-webkit-scrollbar {
        display:none;
    }
`
const ReviewImgWrap = styled.div`
    width: 100%;
    &:first-child{
        margin-left: calc(100vw*(15/414));
    }
`
const ReviewImage = styled.img`
    width: calc(100vw*(88/414));
    margin: 0 calc(100vw*(1.5/414));
`
const ReviewFilterWrap = styled.div`
    padding: 0 calc(100vw*(18/414));
`
const FilterBestBtn = styled.button`
    font-size: calc(100vw*(13/414));
    padding: calc(100vw*(5/414));
    margin: 0 calc(100vw*(5/414));
    color: #4b4b4b;
    ${({bestBtnColor})=>{
        return bestBtnColor ? 
        `
            font-weight: bold;
            color: #ed2939;
        `
        :
        `
        color: #4b4b4b;
        `
    }}
`
const FilterLatelyBtn = styled(FilterBestBtn)`
    ${({bestBtnColor})=>{
        return !bestBtnColor ? 
        `
            font-weight: bold;
            color: #ed2939;
        `
        :
        `
        color: #4b4b4b;
        `
    }}
`
const ReviewLine = styled.div`
    width: calc(100vw*(392/414));
    height: calc(100vw*(1/414));
    background: #d1d1d1;
    margin: 0 auto;
`
const ReviewInnerWrap = styled.div`
    width: 100%;
`
const RevNoneWrap = styled.div`
    width: 100%;
`
const RevNoneText = styled.p`
    text-align: center;
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    color: #979697;
    padding: calc(100vw*(40/414)) 0;
`