//css
import styled from "styled-components"

//img
import IconDownload from "../../../img/ico_coupon_download.png"
import Downbg from "../../../img/coupon_bg.png"

//숫자 3자리수 콤마 코드
function numberWithCommas(x) { return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");}

export default function CouponList({value}) {
    
    return (
        <Container>
            <CouponModalList>
                <CouponPriceWrap>
                    {
                        value.cpn_price > 9999 ? 
                        <CouponPrice>{numberWithCommas(value.cpn_price % 10000 == 0 ? 1 : value.cpn_price % 10000)}만원</CouponPrice>
                        :
                        <CouponPrice>{numberWithCommas(value.cpn_price )}원</CouponPrice>
                    }
                </CouponPriceWrap>
                <div>
                    <CouponText>마켓회원 전용</CouponText>
                    <CouponText>20,000원 이상 구매시</CouponText>
                </div>
                <div>
                    <IcoDown src={IconDownload} alt="Coupon Download"/>
                </div>
            </CouponModalList>
        </Container>
  );
}

const Container = styled.div`
    width: 100%;
`
const CouponModalList = styled.div`
    width: calc(100vw*(374/414));
    display: flex;
    align-items: center;
    padding: calc(100vw*(34/414)) 0;
    background: url(${Downbg}) no-repeat center/100%;
    margin: calc(100vw*(8/414)) auto;
`
const CouponPriceWrap = styled.div`
    width: calc(100vw*(170/414));
`
const CouponPrice = styled.p`
    width: 100%;
    font-size: calc(100vw*(23/414));
    font-weight: 900;
    color: #fff;
    text-align: center;
`
const CouponText = styled.p`
    font-size: calc(100vw*(12/414));
    font-weight: 900;
    color: #fff;
    margin: calc(100vw*(3.5/414)) 0;
`
const IcoDown = styled.img`
    width: calc(100vw*(29/414));
    margin-left: calc(100vw*(18/414));
`