//react
import {Link, useHistory} from "react-router-dom";
import {useState, useEffect} from 'react';

//css
import styled from "styled-components"

//img
import IconSelect from "../../../../img/ico_shop_select.png"

//page
import serverController from '../../../../server/serverController'

export default function InquiryWrite({prd_id}) {
    const [next,setNext] = useState(false);
    const [qnaName,setQnaName] = useState("");
    const [qnaContent,setQnaContent] = useState("");
    const [optionList,setOptionList] = useState(1);
    
    const TitleChange = (e) => { setQnaName(e.target.value); }
    const ContentChange = (e) => { setQnaContent(e.target.value); }
    const TitleChangedate = () =>{ return qnaName.length > 3; };
    const ContentChangedate = () =>{ return qnaContent.length > 3; };
    const history = useHistory();

    useEffect(()=>{
        if(TitleChangedate() && ContentChangedate())
            setNext(true);
        else
            setNext(false);
    },[qnaName,qnaContent])

    const qnaClick = () =>{
        if(next == true){
            const formData = new FormData();
            formData.append('qna_name',qnaName);
            formData.append('qna_content',qnaContent);
            formData.append('qna_cat_id', optionList);
            formData.append('prd_id',prd_id);
            serverController.connectFetchController('qna/insert',"POST",formData,function(res){
            if(res.result == 1) {
                console.log(res);
                return history.push("/product/inquiryComplete");
            }});
        }
    }
    
    return (
        <Container>
            <ShopLine></ShopLine>
            <InquiryWrap>
                <InquiryListWrap>
                    <InquiryList onChange={(e)=>{setOptionList(e.target.value)}}>
                        <option value={1}>상품문의</option>
                        <option value={2}>배송문의</option>
                        <option value={3}>취소/반품문의</option>
                        <option value={4}>기타</option>
                    </InquiryList>
                </InquiryListWrap>
                <InquiryTitleWrap>
                    <InquiryTitle type="text" onChange={TitleChange}/>
                </InquiryTitleWrap>
                <InquiryTextWrap>
                    <InquiryText onChange={ContentChange}/>
                </InquiryTextWrap>
                <InquiryBtnWrap>
                    <Link to={"/product/inquiryComplete"}>
                        <InquiryBtn DetailBtnOn={next} onClick={qnaClick}>문의 등록하기</InquiryBtn>
                    </Link>
                </InquiryBtnWrap>
            </InquiryWrap>

        </Container>
  );
}

const Container = styled.div`
    width: 100%;
`
const ShopLine = styled.div`
    width: 100%;
    height: calc(100vw*(14/414));
    background: #f7f7f7;
`
const InquiryWrap = styled.div`
    width: 100%;
    background: #fff;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0,0,0,0.16);
    padding: calc(100vw*(46/414)) calc(100vw*(28/414));
`
const InquiryListWrap = styled.div`
    width: 100%;
    margin-bottom: calc(100vw*(10/414));
`
const InquiryList = styled.select`
    width: 100%; height: calc(100vw*(40/414));
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    color: #4b4b4b;
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(3/414));
    text-indent: calc(100vw*(16/414));
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background: url(${IconSelect}) no-repeat 93% 50%/calc(100vw*(14/414));
`
const InquiryTitleWrap = styled.div`
    width: 100%;
    margin-bottom: calc(100vw*(15/414));
`
const InquiryTextWrap = styled.div`
    width: 100%;
    margin-bottom: calc(100vw*(20/414));
`
const InquiryTitle = styled.input`
    width: 100%; height: calc(100vw*(40/414));
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(3/414));
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    color: #4b4b4b;
    text-indent: calc(100vw*(16/414));
`
const InquiryText = styled.textarea`
    width: 100%; height: calc(100vw*(306/414));
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(3/414));
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    color: #4b4b4b;
    padding: calc(100vw*(8/414)) calc(100vw*(16/414));
    line-height: 1.3;
`
const InquiryBtnWrap = styled.div`
    width: 100%;
    text-align: center;
`
const InquiryBtn = styled.button`
    width: 100%;
    background: #b4b4b4;
    border: calc(100vw*(3/414)) solid #a5a5a5;
    color: #fff;
    border-radius: calc(100vw*(6/414));
    padding: calc(100vw*(18/414)) 0;
    font-size: calc(100vw*(18/414));
    font-weight: 900;
    ${({DetailBtnOn})=>{
        return DetailBtnOn ? 
        `
            background: #ED2939;
            border-color: #BC343F;
        `
        :
        ``
    }}
`