//react
import {Link, useHistory} from "react-router-dom";

//css
import styled from "styled-components"


export default function ReviewComplete() {
    const history = useHistory();
    const goBack = () =>{
        history.go(-2);
    }
  return (
        <Container>
            <ReviewCompleteWrap>
                <ReviewCompleteInner>
                    <ReviewCompleteText>상품리뷰를 성공적으로 업로드 하였습니다.</ReviewCompleteText>
                    <ReviewCompleteBtn onClick={goBack}>돌아가기</ReviewCompleteBtn>
                </ReviewCompleteInner>
            </ReviewCompleteWrap>
        </Container>
  );
}

//css
const Container = styled.div` 
    width: 100%; 
`
const ReviewCompleteWrap = styled.div` 
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    position: relative;
    width: 100%; height: calc(100vh - calc(100vw*(150/414)));
    background: #fff;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    z-index: 10;
    margin-top: calc(100vw*(12/414));
`
const ReviewCompleteInner = styled.div`
    width: 100%;
    padding: 0 calc(100vw*(30/414));
`
const ReviewCompleteText = styled.p`
    font-size: calc(100vw*(13/414));
    font-weight: 900;
    text-align: center;
    width: 100%;
    margin-bottom: calc(100vw*(55/414));
`
const ReviewCompleteBtn = styled.button`
    width: 100%;
    background: #ED2939;
    border: calc(100vw*(3/414)) solid #BC343F;
    border-radius: calc(100vw*(6/414));
    color: #fff;
    padding: calc(100vw*(20/414)) 0;
    font-size: calc(100vw*(18/414));
    font-weight: 900;
`