//react
import {Link,useHistory} from "react-router-dom";
import {useState, useEffect} from 'react';

//css
import styled from "styled-components"

//img
import CustomerImg from "../../../../img/customer_img.png"
import IconBackBtn from "../../../../img/ico_arrow_white.png"

//page
import serverController from '../../../../server/serverController';

export default function ReviewImgDetail({rev_id}) {

    const history = useHistory();
    const goBack = () =>{
        history.goBack();
    }
    
    const [reviewList,setReviewList] = useState([]);
    const url_rev_id = rev_id;
    useEffect(()=>{

        serverController.connectFetchController(`review/content`,"GET",null,function(res){
        if(res.result == 1) {
            setReviewList(res.list)
        }});
    },[]);
    
    return (
        <Container>
            <ReviewImgDetailWrap>
                <ImgTitleBarWrap>
                    <BackBtnWrap onClick={goBack}>
                        <BackBtn></BackBtn>
                    </BackBtnWrap>
                </ImgTitleBarWrap>
                {
                    reviewList.map((value)=>{
                        if(url_rev_id == value.rev_id){
                            return(
                                <ReviewImgWrap>
                                    <ImgDetailNameWrap>
                                        <ImgDetailName>{value.mem_username}</ImgDetailName>
                                    </ImgDetailNameWrap>
                                    <ReviewImageWrap>
                                        <ReviewImg src={CustomerImg} alt="Customer image"/>
                                    </ReviewImageWrap>
                                    <ImgDetailTitleWrap>
                                        <ImgDetailTitle>{value.prd_title} {value.prd_detail1}</ImgDetailTitle>
                                        <ImgDetailText>{value.rev_content}</ImgDetailText>
                                        <Link to={`/reviewDetail/${value.rev_id}`}>
                                            <ImgDetailBtn>본문보기</ImgDetailBtn>
                                        </Link>
                                    </ImgDetailTitleWrap>
                                </ReviewImgWrap>
                            )
                        }
                    })
                }
            </ReviewImgDetailWrap>
        </Container>
    );
}

//css 
const Container = styled.div` 
    width: 100%;
    background: #000;
`
const ImgTitleBarWrap = styled.div` 
    position: fixed; top: 0;
    display: flex;
    align-items: center;
    width: 100%; height: calc(100vw*(65/414));
`
const ReviewImgDetailWrap = styled.div` 
    width: 100%; min-height: 100vh;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
`
const BackBtnWrap = styled.div`
    position: absolute; left: calc(100vw*(15/414));
    width: calc(100vw*(45/414)); height: calc(100vw*(45/414));
`
const BackBtn = styled.button`
    width: calc(100vw*(45/414)); height: calc(100vw*(45/414));
    background: url(${IconBackBtn}) no-repeat center/calc(100vw*(10/414));
`
const ReviewImgWrap = styled.div`
    width: 100%
`
const ImgDetailNameWrap = styled.div`
    padding: calc(100vw*(7/414)) calc(100vw*(36/414));
`
const ImgDetailName = styled.p`
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    color: #fff;
`
const ReviewImageWrap = styled.div`
    width: 100%;
`
const ReviewImg = styled.img`
    width: 100%; max-height: calc(100vw*(414/414));
`
const ImgDetailTitleWrap = styled.div`
    width: 100%;
    padding: calc(100vw*(24/414)) calc(100vw*(36/414)) 0;
`
const ImgDetailTitle = styled.p`
    font-size: calc(100vw*(15/414));
    color: #fff;
    margin-bottom: calc(100vw*(10/414));
`
const ImgDetailText = styled.p`
    font-size: calc(100vw*(13/414));
    color: #fff;
    margin-bottom: calc(100vw*(2/414));
    line-height: 1.2;
    word-break: keep-all;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    white-space: normal;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
`
const ImgDetailBtn = styled.span`
    font-size: calc(100vw*(11/414));
    font-weight: bold;
    color: #ed2939;
`