//react
import {useEffect, useState, useRef} from 'react';
import { useSelector } from 'react-redux'; // 불러오기

import SubHeader from '../../../component/common/SubHeader';
import ShopPayInfo from '../../../component/shop/pay/ShopPayInfo';
import ShopPayAddress from '../../../component/shop/pay/ShopPayAddress';
import ShopPayCoupon from '../../../component/shop/pay/ShopPayCoupon';
import ShopPayPoint from '../../../component/shop/pay/ShopPayPoint';
import ShopPayPrice from '../../../component/shop/pay/ShopPayPrice';
import ShopPayment from '../../../component/shop/pay/ShopPayment';
import ShopPayBtn from '../../../component/shop/pay/ShopPayBtn';
import ShopTermModal from '../../../component/shop/pay/TermModal';
import serverController from '../../../server/serverController';

export default function ShopPayPage() {
  
  const [itemList,setItemList] = useState([]);
  const [priceInfo,setPriceInfo] = useState({productCount : 0, totalPrice : 0, originalPrice: 0,  price : 0 , optionPrice : 0 , salePrice : 0});
  const [TermModal,setPurchaseModal] = useState(false);
  const [usePoint,setUsePoint] = useState("0");
  const [resultPrice,SetResultPrice] = useState("0");
  const [shipPrice,setShipPrice] = useState([]);
  const [reduxZipcode,setReduxZipcode] = useState([]);

  const openTermModal = (onOff) =>{ setPurchaseModal(onOff);}
  const openUserData = useSelector(state=>{ return state.userInfo}); //불러오기

  useEffect(()=>{
    setItemList(JSON.parse(localStorage.selectList));
    setReduxZipcode(openUserData.ardId.adr_zipcode);

    serverController.connectFetchController(`user/fee?zipcode=${openUserData.ardId.adr_zipcode}`,"GET",null,function(res){
      if(res.result == 1) {
        setShipPrice(res.mart_fee);
    }});
  },[])
  useEffect(()=>{
    getTotalPrice();
  },[itemList])

  //장바구니에 담아진 최종 리스트에 대한 돈계산.
  function getPrice(option){
    let price = 0;
    let optionPrice = 0;
    let salePrice = 0;
    let totalPrice = 0;
    let originalPrice = 0;
    let options = option.data;

    for(let j = 0; j<options.length;j++){
        var p = 0;
        var ogp = 0;
        var op = 0;
        var sp = 0;
        let optionList = options[j].options.optionList;

        for(let calKey in optionList){
          for(let z =0;z<	optionList[calKey].length ;z++){
            op += optionList[calKey][z].opt_price;
          }
        }

        if(options[j].options.isOptional == false){
          ogp += options[j].prd_price;
          p += Math.round((options[j].prd_price) - ((options[j].prd_price * options[j].prd_sale_rate) / 100));
          sp += Math.round(((options[j].prd_price * options[j].prd_sale_rate) / 100));
        }

        originalPrice += (ogp * options[j].count);
        price += (p * options[j].count);
        optionPrice += (op * options[j].count);
        salePrice += (sp * options[j].count);
        totalPrice += (p + op) *  options[j].count;
    }

    return { totalPrice : totalPrice, originalPrice: originalPrice,  price : price , optionPrice : optionPrice , salePrice : salePrice };
  }

  function getTotalPrice(){
      let productInfoTemp = {
          productCount : 0, totalPrice : 0, originalPrice: 0,  price : 0 , optionPrice : 0 , salePrice : 0
      };

       itemList.map((value)=>{
          console.log(value);

          let cart_items = JSON.parse(value.cart_items);
          let info = getPrice(cart_items);
          console.log(getPrice(cart_items));
          
          productInfoTemp.totalPrice += info.totalPrice * cart_items.count;
          productInfoTemp.originalPrice += info.originalPrice * cart_items.count;
          productInfoTemp.price += info.price * cart_items.count;
          productInfoTemp.optionPrice += info.optionPrice * cart_items.count;
          productInfoTemp.salePrice += info.salePrice * cart_items.count;
          productInfoTemp.productCount++;
       });
      
      setPriceInfo(productInfoTemp);
  }

  //구매하기 버튼 -> 금액 계산 -> (제일 중요) 백단에서 금액 계산 -> (프론트+백단 계산이 동일할시) DB에 insert 저장 -> insert 성공이면 (결제 팝업 오픈) 결제 진행
  const payForm = useRef();

  function pay() {
    var pf = payForm.current;
    var fileName = "https://apitest.payjoa.co.kr/pay/link";
    var PAYJOA = window.open("", "PAYJOA", "width=468,height=750");
    pf.target = "PAYJOA";
    pf.action = fileName;
    pf.method = "post";
    pf.submit();
  }

  //new Date().getTime() + user_id
  //prd_title 외 3건 
  //CODE + new Date().getTime() + prd_id

  return (
    <>
      <SubHeader rank={true} title={"주문/결제"}/>
      <ShopPayInfo/>
      <ShopPayAddress/>
      {/* <ShopPayCoupon/> */}
      <ShopPayPoint usePoint={usePoint} setUsePoint={setUsePoint} priceInfo={priceInfo}/>
      <ShopPayPrice openTermModal={openTermModal} priceInfo={priceInfo} usePoint={usePoint} SetResultPrice={SetResultPrice} shipPrice={shipPrice}/>
      <ShopPayment/>
      <ShopPayBtn shipPrice={shipPrice} pay={pay} usePoint={usePoint} resultPrice={resultPrice} itemList={itemList} reduxZipcode={reduxZipcode}/>
      <ShopTermModal TermModal={TermModal} openTermModal={openTermModal}/>
      
      <form ref={payForm} nmae="payForm" accept-charset="EUC-KR" style={{display:"none"}}>
        <input name="TYPE" value="P"/>
        <input name="PAYMETHOD" value="VACCT"/>
        <input name="CERTTYPE" value="01"/>
        <input name="ORDERNO" value="20210409132343"/>
        <input name="PRODUCTTYPE" value="1"/>
        <input name="AMOUNT" value="1004"/>
        <input name="PRODUCTNAME" value="테스트상품"/>
        <input name="PRODUCTCODE" value="A001"/>
        <input name="USERID" value="tester"/>

        <input name="CPID" value="CTS16761"/>
        <input name="RETURNURL" value="/pay/success/page"/>
      </form>
    </>
  );
}
