//react
import {Link} from "react-router-dom";

//css
import styled from "styled-components"


export default function SignUpStep2() {

    return (
        <Container>
            <SignUpWrap>
                <InterestTypeList>
                    <InterestTypeWrap>
                        <InterestType>분야1</InterestType>
                    </InterestTypeWrap>
                    <InterestTypeWrap>
                        <InterestType>분야1</InterestType>
                    </InterestTypeWrap>
                    <InterestTypeWrap>
                        <InterestType>분야1</InterestType>
                    </InterestTypeWrap>
                    <InterestTypeWrap>
                        <InterestType>분야1</InterestType>
                    </InterestTypeWrap>
                    <InterestTypeWrap>
                        <InterestType>분야1</InterestType>
                    </InterestTypeWrap>
                    <InterestTypeWrap>
                        <InterestType>분야1</InterestType>
                    </InterestTypeWrap>
                    <InterestTypeWrap>
                        <InterestType>분야1</InterestType>
                    </InterestTypeWrap>
                </InterestTypeList>
                <SignUpBtnWrap>
                    <Link to={"/signUpStep3"}>
                        <SignUpBtn>회원가입 하기</SignUpBtn>
                    </Link>
                </SignUpBtnWrap>
                <CheckGuideWrap>
                    <CheckGuideText>3개 이상의 관심분야를 선택해주세요</CheckGuideText>
                </CheckGuideWrap>
            </SignUpWrap>
        </Container>
  );
}

const Container = styled.div`
    position: relative;
    width: 100%; height: calc(100vh - calc(100vw*(65/414)));
    z-index: 100;
    background: #fff;
`
const SignUpWrap = styled.div`
    width: 100%;
`
const InterestTypeList = styled.div`
    width: 100%;
    padding: calc(100vw*(36/414));
`
const InterestTypeWrap = styled.div`
    width: 100%;
    margin-bottom: calc(100vw*(5/414));
`
const InterestType = styled.p`
    font-size: calc(100vw*(28/414));
    font-weight: bold;
    text-align: center;
    padding: calc(100vw*(25/414)) 0;
    border-radius: calc(100vw*(17/414));
    
    ${({TypeChkOn})=>{
        return TypeChkOn ? 
        `
            background: #4b4b4b;
            color: #fff;
        `
        :
        ``
    }}
`
const SignUpBtnWrap = styled.div`
    text-align: center;
`
const SignUpBtn = styled.button`
    width: calc(100vw*(342/414));
    background: #ed2939;
    border: calc(100vw*(3/414)) solid #bc343f;
    color: #fff;
    border-radius: calc(100vw*(6/414));
    padding: calc(100vw*(18/414)) 0;
    font-size: calc(100vw*(18/414));
    font-weight: 900;
`
const CheckGuideWrap = styled.div`
    background: #646464;
    padding: calc(100vw*(36/414)) 0;
    margin-top: calc(100vw*(18/414));
`
const CheckGuideText = styled.p`
    font-size: calc(100vw*(18/414));
    font-weight: 900;
    text-align: center;
    color: #fff;
`