import MypageMainTitle from '../../component/mypage/MypageMainTitle';
import MypageMainPoint from '../../component/mypage/MypageMainPoint';
import MypageMainTitleMenu from '../../component/mypage/MypageMainTitleMenu';
import MypageMainMenuList from '../../component/mypage/MypageMainMenuList';
import FooterPadding from '../../component/common/FooterPadding';
import FooterMenu from '../../component/common/FooterMenu';


//Mypage page
export default function Mypage() {

  return (
    <>
        <MypageMainTitle/>
        <MypageMainPoint rank={true}/>
        <MypageMainTitleMenu/>
        <MypageMainMenuList/>
        <FooterPadding option={{backgroundColor:"#fff"}}/>
        <FooterMenu />
      
    </>
  );
}
