//css
import styled from "styled-components"

//img
import IconClosed from "../../../img/ico_closed_btn_bk.png"
import IconDownload from "../../../img/ico_download.png"

//page
import Coupon from "../../common/coupon/Coupon";

const CouponList = [
    {
        cpn_id: 0,
        cpn_price:"20000",
    },
    {
        cpn_id: 1,
        cpn_price:"14000",
    },
    {
        cpn_id: 1,
        cpn_price:"15000",
    },
    {
        cpn_id: 1,
        cpn_price:"5000",
    }
]

export default function CouponModal({couponModal,openCouponModal}) {

    if(couponModal == false)
        return null;

    return (
        <Container>
            <CouponModalWrap>
                <CouponTitleWrap>
                    <CouponMainTitle>회원전용 1월 식료품 할인쿠폰</CouponMainTitle>
                    <CouponTitle>사용기간 2021.01.01-2021.01.31</CouponTitle>
                </CouponTitleWrap>
                {
                CouponList.map((value)=>{ return (<Coupon value={value}/>)})
                }
                
                <CouponLine></CouponLine>
                <AllDownWrap>
                    <AllDownBtn>전체 받기 <IcoAllDownBtn src={IconDownload} alt=""/></AllDownBtn>
                </AllDownWrap>
                <IcoClosed onClick={()=>{openCouponModal(false)}}></IcoClosed>
            </CouponModalWrap>
        </Container>
    );
}


const Container = styled.div`
    position: fixed; bottom: 0;
    width: 100%; height: 100vh;
    z-index: 500;
    background: rgba(1,1,1,0.1);
`
const CouponModalWrap = styled.div`
    position: fixed; bottom: 0;
    width: 100%;
    background: #fff;
    padding: calc(100vw*(50/414)) 0 calc(100vw*(100/414));

`
const CouponTitleWrap = styled.div`
    padding: 0 calc(100vw*(30/414)) calc(100vw*(6/414));
`
const CouponMainTitle = styled.p`
    font-size: calc(100vw*(19/414));
    margin-bottom: calc(100vw*(6/414));
`
const CouponTitle = styled.span`
    font-size: calc(100vw*(12/414));
`
const CouponLine = styled.div`
    width: calc(100vw*(392/414)); height: calc(100vw*(1/414));
    background: #d1d1d1;
    margin: calc(100vw*(30/414)) auto 0;
`
const AllDownWrap = styled.div`
    font-size: calc(100vw*(12/414));
    padding: calc(100vw*(25/414)) 0;
    text-align: center;
`
const AllDownBtn = styled.button`
    font-size: calc(100vw*(13/414));
    font-weight: 900;
    color: #ff6752;
`
const IcoAllDownBtn = styled.img`
    width: calc(100vw*(14/414));
    margin-left: calc(100vw*(5/414));
`
const IcoClosed = styled.button`
    position: absolute; top: calc(100vw*(25/414)); right: calc(100vw*(25/414));
    width: calc(100vw*(30/414)); height: calc(100vw*(30/414));
    background: url(${IconClosed}) no-repeat center/calc(100vw*(12/414));
`