import {Link} from "react-router-dom";

//css
import styled from "styled-components"

//img
import iconCategory from "../../img/ico_footer_category.svg"
import iconHome from "../../img/ico_footer_home.svg"
import iconCart from "../../img/ico_footer_cart.svg"
import iconMypage from "../../img/ico_footer_mypage.svg"
import iconLately from "../../img/ico_footer_lately.svg"
import FooterBg from "../../img/footer_bg.png"


export default function FooterMenu() {
    //서버에서 불러오는 부분들만 common 공통으로 빼기

    return (
        <Container>
            <FooterWrap>
                <Footermenu>
                    <FootermenuWrap>
                        <Link to={"/category"}>
                            <FootermenuLink>
                                <FooterCategoryImg src={iconCategory} alt="category"/>
                                <FooterCategoryText>카테고리</FooterCategoryText>
                            </FootermenuLink>
                        </Link>
                    </FootermenuWrap>
                    <FootermenuWrap>
                        <Link to={"/cartMarket"}>
                            <FootermenuLink>
                                <FooterCartImg src={iconCart} alt="home"/>
                                <FooterCategoryText>장바구니</FooterCategoryText>
                            </FootermenuLink>
                        </Link>
                    </FootermenuWrap>
                    <FootermenuWrap>
                        <Link to={"/"}>
                            <FootermenuHomeLink>
                                <FooterHomeImg src={iconHome} alt="cart"/>
                                <FooterHomeText>HOME</FooterHomeText>
                            </FootermenuHomeLink>
                        </Link>
                    </FootermenuWrap>
                    <FootermenuWrap>
                        <Link to={"/mypage"}>
                            <FootermenuLink>
                                <FooterMypageImg src={iconMypage} alt="cart"/>
                                <FooterCategoryText>마이페이지</FooterCategoryText>
                            </FootermenuLink>
                        </Link>
                    </FootermenuWrap>
                    <FootermenuWrap>
                        <Link to={"/latelyList"}>
                            <FootermenuLink>
                                <FooterLatelyImg src={iconLately} alt="cart"/>
                                <FooterCategoryText>최근본 상품</FooterCategoryText>
                            </FootermenuLink>
                        </Link>
                    </FootermenuWrap>
                </Footermenu>
            </FooterWrap>
        </Container>
    );
}

//css
const Container = styled.div`
    position: fixed; bottom: calc(100vw*(-1/414)); left: 0;
    width: 100%;
    z-index: 1000;
`
const FooterWrap = styled.div` 
    width: 100%;
    background: url( ${FooterBg} ) no-repeat bottom calc(100vw*(-14/414)) center/calc(100vw*(440/414));
    padding-top: calc(100vw*(10/414));
`
const Footermenu = styled.ul` 
    display: flex;
    align-items: center;
`
const FootermenuWrap = styled.li`
    display: flex;
    justify-content: center;
    align-items: flex-end;
    width: calc(100%/5); height: calc(100vw*(81/414));
    padding-bottom: calc(100vw*(22/414));
`
const FootermenuLink = styled.p`
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
`
const FootermenuHomeLink = styled(FootermenuLink)`
    position: absolute; top: calc(100vw*(-8/414)); left: 50%;
    width: calc(100vw*(58/414)); height: calc(100vw*(58/414));
    margin-left: calc(100vw*(-29/414));
    border-radius: 50%;
    border: calc(100vw*(3/414)) solid #DB1424;
    background: #EC2939;
`
const FooterCategoryImg = styled.img` 
    width: calc(100vw*(23/414));
`
const FooterCartImg = styled.img` 
    width: calc(100vw*(32/414));
`
const FooterHomeImg = styled.img` 
    width: calc(100vw*(30/414));
    margin-top: calc(100vw*(10/414));
`
const FooterMypageImg = styled.img` 
    width: calc(100vw*(28/414));
`
const FooterLatelyImg = styled.img` 
    width: calc(100vw*(28/414));
`
const FooterCategoryText = styled.span`
    display: block;
    width: 100%;
    font-size: calc(100vw*(10/414));
    text-align: center;
    color: #979797;  
    margin-top: calc(100vw*(5/414));
`
const FooterHomeText = styled(FooterCategoryText)` 
    margin-top: calc(100vw*(23/414));
`