//react
import { useState,useEffect } from 'react';
import { Link,useHistory } from "react-router-dom";

//css
import styled from "styled-components"

//img
import wishBtnOn from "../../../img/ico_wish_on.svg"
import wishBtnOff from "../../../img/ico_wish_off.svg"
import IconstarOn from "../../../img/ico_star_on.svg"
import IconstarOff from "../../../img/ico_star_off.svg"
import IconDelivery from "../../../img/ico_delivery.png"
import IconBack from "../../../img/ico_arrow_back.png"

//page
import appInfoController from '../../../server/appInfoController'
import serverController from '../../../server/serverController';

//숫자 3자리수 콤마 코드
function numberWithCommas(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

const getStarImageList = (count) =>{
    let imageList = [];
    for(var i =0;i < 5;i++){
        imageList.push(
            <Stars>
                <img src={i < count ? IconstarOn : IconstarOff} alt=""/>
            </Stars>
        )
    }
    return imageList;
}

export default function ShopProduct({value, prd_id, shopOption}) {
    const [likeId,setLikeId] = useState("");
    const [likeType,setLikeType] = useState();

    useEffect(()=>{
        serverController.connectFetchController(`products/detail?prd_id=${prd_id}`,"GET",null,function(res){
        if(res.result == 1) {
            setLikeType(res.info.like_type == 1);
            setLikeId(res.info.like_id);
        }});
    },[]);

    const wishHandler = () => {
        if (likeType == 0) {
            serverController.connectFetchController(`like/update?prd_id=${prd_id}`,"PUT",null,function(res){
                if(res.result == 1) {
                    setLikeType(true);
                    console.log(res);
                }
                serverController.connectFetchController(`products/detail?prd_id=${prd_id}`,"GET",null,function(res){
                if(res.result == 1) {
                    setLikeType(res.info.like_type == 1);
                    setLikeId(res.info.like_id);
                }});
            });
        } else {
            serverController.connectFetchController(`like/delete?like_id=${likeId}`,"DELETE",null,function(res){
                if(res.result == 1) {
                    setLikeType(false);
                    console.log(res);
                }
            });
        }
    }

    const history = useHistory();
    const goBack = () =>{
        history.goBack();
    }
    return (
        <Container>
            <ProductMainImgWrap>
                <ProductMainImg src={appInfoController.ImagePath + value.prd_img} alt="Product img"/>
                <ProductBackWrap onClick={goBack}>
                    <ProductBackImg src={IconBack} alt="뒤로가기"/>
                </ProductBackWrap>
            </ProductMainImgWrap>

            <ShopLine></ShopLine>

            <ProductInfoWrap>
                <ProductInfoTitleWrap>
                    <ProductName>{value.prd_title}</ProductName>
                    <ProductText>{value.prd_detail1}</ProductText>
                    <WishBtnWrap onClick={wishHandler}>
                        {
                            likeType ?
                            <WishBtn><img src={wishBtnOn}/></WishBtn>
                            :
                            <WishBtn><img src={wishBtnOff}/></WishBtn>
                        }
                    </WishBtnWrap>
                </ProductInfoTitleWrap>
                <div>
                    <Point>적립금 <PointText>{numberWithCommas(value.prd_earn_price ? value.prd_earn_price : "0")}</PointText>원</Point>
                    {
                        value.prd_sale_rate > 0 ? 
                        <DiscountRate><Discount>Discount</Discount>{value.prd_sale_rate}%</DiscountRate>
                        :
                        null
                    }
                </div>
                <div>
                    {
                        value.prd_sale_rate > 0 ?
                        <Price>{numberWithCommas(value.prd_price)}원</Price>
                        :
                        null
                    }
                    <TotalPrice><TotalPriceText>{numberWithCommas(value.prd_price - value.prd_price*value.prd_sale_rate/100)}</TotalPriceText>원</TotalPrice>
                </div>
                {
                    shopOption ?
                    <StarsWrap>
                        {
                            getStarImageList(Math.floor(value.rev_score))
                        }
                        <ProductScoreText>상품평 ({numberWithCommas(value.rev_score ? value.rev_score : "0")})</ProductScoreText>
                    </StarsWrap>
                    :
                    null
                }
                <DeliveryTextWrap>
                    <IcoDelivery src={IconDelivery} alt=""/>
                    <DeliveryText>0원이상 구매시 배송비 무료</DeliveryText>
                </DeliveryTextWrap>
            </ProductInfoWrap>
        </Container>
  );
}

const Container = styled.div`
    width: 100%;
`
const ProductMainImgWrap = styled.div`
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    background: #fff;
`
const ProductBackWrap = styled.div`
    position: absolute; top: calc(100vw*(12/414)); left: calc(100vw*(18/414));
    width: calc(100vw*(15/414));
`
const ProductBackImg = styled.img`
    width: calc(100vw*(15/414));;
`
const ShopLine = styled.div`
    width: 100%;
    height: calc(100vw*(10/414));
    background: #f7f7f7;
`
const ProductInfoWrap = styled.div`
    display: flex;
    align-items: flex-end;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
    background: #fff;
    padding: calc(100vw*(25/414)) calc(100vw*(18/414)) calc(100vw*(25/414));
`
const ProductInfoTitleWrap = styled.div`
    position: relative;
    width: 100%;
`
const WishBtnWrap = styled.button`
    position: absolute; top: calc(100vw*(-5/414)); right: calc(100vw*(-5/414));
`
const WishBtn = styled.button`
    display: flex;
    align-items: center;
    width: calc(100vw*(26/414));
    padding: calc(100vw*(5/414));
`
const ProductMainImg = styled.img`
    width: 100%;
`
const ProductName = styled.p`
    width: 100%;
    font-size: calc(100vw*(19/414));
    margin-bottom: calc(100vw*(3/414));
    padding-right: calc(100vw*(30/414));
`
const ProductText = styled.p`
    width: 100%;
    font-size: calc(100vw*(15/414));
`
const Point = styled.p`
    font-size: calc(100vw*(11/414));
    color: #292929;
    font-weight: bold;
    margin-top: calc(100vw*(18/414));
`
const PointText = styled.span`
    color: #292929;
    font-weight: 900;
`
const Discount = styled.span`
    font-size: calc(100vw*(11/414));
    color: #e20000;
    font-weight: normal;
    margin-right: calc(100vw*(5/414));
`
const DiscountRate = styled.span`
    display: block;
    width: 100%;
    font-size: calc(100vw*(15/414));
    color: #e20000;
    font-weight: 900;
    margin-top: calc(100vw*(3/414));
`
const Price = styled.span`
    display: block;
    font-size: calc(100vw*(15/414));
    color: #707070;
    text-align: right;
    text-decoration: line-through;
`
const TotalPrice = styled.span`
    display: flex;
    align-items: center;
    font-size: calc(100vw*(17/414));
    margin-top: calc(100vw*(6/414));
`
const TotalPriceText = styled.span`
    font-size: calc(100vw*(23/414));
    font-weight: 900;
`
const StarsWrap = styled.div`
    width: 100%;
    display: flex;
    margin-top: calc(100vw*(15/414));
`
const Stars = styled.span`
    display: block;
    width: calc(100vw*(15/414));
    margin: 0 calc(100vw*(1/414));
`
const ProductScoreText = styled.p`
    font-size: calc(100vw*(13/414));
    color: #707070;
    margin-left: calc(100vw*(10/414));
`
const DeliveryTextWrap = styled.div`
    width: 100%;
    display: flex;
    align-items: center;
    margin-top: calc(100vw*(22/414));
    padding-bottom: calc(100vw*(17/414));
    &::after {
        content: '';
        position: absolute; left: 50%;
        width: calc(100vw*(392/414));
        height: calc(100vw*(1/414));
        background: #d1d1d1;
        margin-top: calc(100vw*(50/414));
        margin-left: calc(100vw*(-196/414));
    }
`
const IcoDelivery = styled.img`
    width: calc(100vw*(22/414));
    margin-right: calc(100vw*(8/414));
`
const DeliveryText = styled.p`
    font-size: calc(100vw*(14/414));
    color: #707070;
`