import {useState} from "react";

//css
import styled from "styled-components"


export default function ShopTermModal({value}) {

    const [showTerm, setshowTerm] = useState(true);

    return (
        <Container>
            <TermModalListWrap>
                <TermModalTextWrap>
                    <TermModalText>{value.term_title}</TermModalText>
                    <TermModalBtn onClick={()=>{setshowTerm(e => !e)}}>보기</TermModalBtn>
                </TermModalTextWrap>
                    {
                        showTerm ?
                        null
                        :
                        <TermModalDtailWrap>
                            <TermModalDtail>{value.term_text}</TermModalDtail>
                        </TermModalDtailWrap>
                    }
            </TermModalListWrap>
        </Container>
    );
}


const Container = styled.div`
    width: 100%;
`
const TermModalListWrap = styled.div`
    width: 100%;
    padding: calc(100vw*(5/414)) 0;
`
const TermModalTextWrap = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: calc(100vw*(7/414)) 0;
`
const TermModalText = styled.p`
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    color: #969696;
`
const TermModalBtn = styled.p`
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    color: #969696;
`
const TermModalDtailWrap = styled.div`
    width: 100%; height: calc(100vw*(160/414));
    background: #f7f7f7;
    padding: calc(100vw*(5/414)) calc(100vw*(9/414));
    overflow-y: scroll;
`
const TermModalDtail = styled.p`
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    color: #969696;
`