import {useState} from "react";

//css
import styled from "styled-components"


export default function OptionModal({prdOptionModal ,openprdOptionModal}) {

    if(prdOptionModal == false)
        return null;

    return(
        <Container>
            <ProductOptionWrap>
                <ProductOptionBtnWrap>
                    <ProductOptionBtn>기록삭제</ProductOptionBtn>
                    <Line></Line>
                    <ProductOptionBtn onClick={()=>{openprdOptionModal(false)}}>취소</ProductOptionBtn>
                </ProductOptionBtnWrap>
            </ProductOptionWrap>
        </Container>

    );
}

/* cart title */
const Container = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed; bottom: 0;
    width: 100%; height: 100vh;
    z-index: 1500;
    background: rgba(1,1,1,0.4);
`
const ProductOptionWrap = styled.div`
    width: calc(100vw*(389/414));
    background: #fff;
    border-radius: 15px;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    padding: 0 calc(100vw*(8/414));
`
const Line = styled.div`
    width: 100%; height: calc(100vw*(1/414));
    background: #E4E4E4;
`
const ProductOptionBtnWrap = styled.div`
    padding: calc(100vw*(5/414)) 0;
    text-align: center;
`
const ProductOptionBtn = styled.button`
    font-size: calc(100vw*(15/414));
    font-weight: 900;
    color: #4b4b4b;
    padding: calc(100vw*(25/414)) 0;
`