//react
import {Link} from "react-router-dom";
import {useState, useEffect} from 'react';

//css
import styled from "styled-components"


export default function PayDetail() {

    return (
        <Container>
            <PayDetailWrap>
                <PayDetailTitleWrap>
                    <PayDetailTitle>2020.12.30</PayDetailTitle>
                    <OrderNumber>주문번호 12345678901234</OrderNumber>
                </PayDetailTitleWrap>
                {/* info */}
                <PayDetailListWrap>
                    <PayDetailList>
                        <DetailListTitleWrap>
                            <DetailListTitle>결제정보</DetailListTitle>
                        </DetailListTitleWrap>
                        <DetailListTextWrap>
                            <DetailListText>상품가격</DetailListText>
                            <DetailListSubText>25,900원</DetailListSubText>
                        </DetailListTextWrap>
                        <DetailListTextWrap>
                            <DetailListText>쿠폰할인</DetailListText>
                            <DetailListSubText>-0원</DetailListSubText>
                        </DetailListTextWrap>
                        <DetailListTextWrap>
                            <DetailListText>배송비</DetailListText>
                            <DetailListSubText>0원</DetailListSubText>
                        </DetailListTextWrap>
                        <DetailListTextWrap>
                            <DetailListText>적립금사용</DetailListText>
                            <DetailListSubText>-0원</DetailListSubText>
                        </DetailListTextWrap>
                        <DetailListTextWrap>
                            <DetailListText>하나카드</DetailListText>
                            <DetailListSubText>25,900원</DetailListSubText>
                        </DetailListTextWrap>
                        <Line></Line>
                        <DetailListTextWrap>
                            <TotalDetailText>하나카드</TotalDetailText>
                            <TotalDetailSubText>25,900원</TotalDetailSubText>
                        </DetailListTextWrap>
                    </PayDetailList>
                </PayDetailListWrap>

                {/* point */}
                <PayDetailListWrap>
                    <PayDetailList>
                        <DetailListTitleWrap>
                            <DetailListTitle>적립금 내역</DetailListTitle>
                        </DetailListTitleWrap>
                        <DetailListTextWrap>
                            <DetailListText>적립완료 적립금</DetailListText>
                            <DetailListSubText>120원</DetailListSubText>
                        </DetailListTextWrap>
                        <DetailListTextWrap>
                            <DetailListText>적립금 사용</DetailListText>
                            <DetailListSubText>-0원</DetailListSubText>
                        </DetailListTextWrap>
                    </PayDetailList>
                </PayDetailListWrap>

                {/* address */}
                <PayDetailListWrap>
                    <PayDetailList>
                        <DetailListTitleWrap>
                            <DetailListTitle>배송정보</DetailListTitle>
                        </DetailListTitleWrap>
                        <DetailListTextWrap>
                            <DetailListText>이름</DetailListText>
                            <DetailListSubText>홍길동</DetailListSubText>
                        </DetailListTextWrap>
                        <DetailListTextWrap>
                            <DetailListText>주소</DetailListText>
                            <DetailListSubText>서울특별시 강남구 서초동 131-1 아파트 103동 103호</DetailListSubText>
                        </DetailListTextWrap>
                        <DetailListTextWrap>
                            <DetailListText>휴대폰</DetailListText>
                            <DetailListSubText>010-0000-0000</DetailListSubText>
                        </DetailListTextWrap>
                        <MessageTextWrap>
                            <DetailListSubText>문앞에다가 놓아주세요.</DetailListSubText>
                        </MessageTextWrap>
                    </PayDetailList>
                </PayDetailListWrap>
            </PayDetailWrap>
        </Container>
    );
}

//css
const Container = styled.div` 
    position: relative;
    width: 100%;
    background: #fff;
    z-index: 10;
`
const PayDetailWrap = styled.div`
    width: 100%;
    padding: calc(100vw*(25/414)) calc(100vw*(15/414)) 0;
`
const PayDetailTitleWrap = styled.div`
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 calc(100vw*(15/414));
`
const PayDetailTitle = styled.p`
    font-size: calc(100vw*(18/414));
    font-weight: bold;
`
const OrderNumber = styled.p`
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    color: #979797;
`
const PayDetailListWrap = styled.div`
    width: 100%;
    margin-top: calc(100vw*(18/414));
`
const PayDetailList = styled.div`
    width: 100%;
    border: calc(100vw*(1/414)) solid #dddddc;
    border-radius: calc(100vw*(12/414));
    padding: calc(100vw*(10/414)) calc(100vw*(15/414)) calc(100vw*(25/414));
`
const DetailListTitleWrap = styled.div`
    width: 100%;
    padding: calc(100vw*(10/414)) calc(100vw*(15/414));
`
const DetailListTitle = styled.p`
    font-size: calc(100vw*(18/414));
    font-weight: bold;
`
const DetailListTextWrap = styled.div`
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    width: 100%;
    padding: calc(100vw*(8/414)) calc(100vw*(15/414));
`
const DetailListText = styled.p`
    width: calc(100vw*(90/414));
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    color: #979797;
`
const TotalDetailText = styled(DetailListText)`
    color: #ed2939;
`
const DetailListSubText = styled.p`
    width: calc(100% - calc(100vw*(90/414)));
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    color: #4b4b4b;
    text-align: right;
    word-break: keep-all;
`
const TotalDetailSubText = styled(DetailListSubText)`
    color: #ed2939;
`
const Line = styled.div`
    width: 100%; height: calc(100vw*(1/414));
    background: #d1d1d1;
    margin-top: calc(100vw*(8/414));
`
const MessageTextWrap = styled.div`
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 100%;
    padding: calc(100vw*(8/414)) calc(100vw*(15/414));
`