import { Swiper, SwiperSlide } from 'swiper/react';
import SwiperCore, { Pagination } from 'swiper';

//css
import 'swiper/swiper-bundle.css';
import styled from "styled-components"

//img
import mainSlideImg from "../../img/main_slide_img.png"

const Data = [
    {
        src : mainSlideImg
    },
    {
        src : mainSlideImg
    },
    {
        src : mainSlideImg
    },
    {
        src : mainSlideImg
    },
    {
        src : mainSlideImg
    },
]

SwiperCore.use([Pagination]);
export default function MainBanner() {
  return (
      <MainBannerWrap className="MainBannerWrap">
        {/* main slide */}
        <Swiper
            spaceBetween={8}
            slidesPerView={1}
            pagination={{ clickable: true }}
        >
            {
                Data.map((value,index)=>{
                    
                    return (
                        <SwiperSlide>
                            <Slide>
                                <MainSlideImg src={value.src} alt="main slide"/>
                            </Slide>
                        </SwiperSlide>
                    )
                })
            }
        </Swiper>
    </MainBannerWrap>
  );
}


//css
const MainBannerWrap = styled.div` 
    width: 100%;
`
/* main slide */
const Slide = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    height: calc(100vw*(234/414));
    overflow: hidden;
`
const MainSlideImg = styled.img`
    width: 100%; height: 100%; object-fit: cover;
`