//react
import {Link , useHistory} from "react-router-dom";
import {useState, useEffect} from 'react';

//css
import styled from "styled-components"

//img
import IconClosed from "../../../img/ico_closed_btn_bk.png"

//page
import serverController from '../../../server/serverController';
import OptionListLayout from './OptionListLayout'

//숫자 3자리수 콤마 코드
function numberWithCommas(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

export default function PurchaseModal({purchaseModal,openPurchaseModal,prd_id, prd_type, product, selectList, setSelectList, shopOption}) {
    const [itemList,setItemList] = useState([]);
    const [cartType,setCartType] = useState("");
    const history = useHistory();
    
    useEffect(()=>{
        serverController.connectFetchController(`products/detail?prd_id=${prd_id}`,"GET",null,function(res){
            if(res.result == 1) {
                setItemList(res.info);
            }
        });
        
        if(prd_type == 2){
            setCartType(3);
        }else{
            setCartType(1);
        }
    },[]);

    const cartClick = () =>{
        var count = 0;
        for(let key in selectList){
            if(selectList[key].options.isOptional == false && selectList[key].prd_id == product.prd_id)
                count++;
        }
        if(shopOption == true){
            if(count == 0){
                alert("필수 상품을 선택해주세요");
                return false;
            }
        }
    
        var formData = new FormData();
        var resultArray = [];
        
        for(var key in selectList){
            resultArray.push(selectList[key]);
        }
        formData.append("cart_items",JSON.stringify({
            prd_id: product.prd_id, 
            prd_price: product.prd_price, 
            prd_sale_rate: product.prd_sale_rate, 
            prd_title: product.prd_title, 
            prd_img: product.prd_img,
            count: 1,
            data: resultArray
        }));
        formData.append("prd_id",product.prd_id);
        serverController.connectFetchController(`cart/insert?cart_type=${cartType}`,"POST",formData,function(res){
            if(res.result == 1) {
                return history.push( cartType == 3 ? "/cartMall" : `/cartMarket`);
            }
        });
        return true;
    };

    const payClick = () => {
        var count = 0;
        for(let key in selectList){
            if(selectList[key].options.isOptional == false && selectList[key].prd_id == product.prd_id)
                count++;
        }
            
        if(count == 0){
            alert("상품을 선택해주세요");
            return;   		
        }

        var resultArray = [];
    
        for(var key in selectList){
            resultArray.push(selectList[key]);
        }
       
      
        if(resultArray.length == 0){
            alert("상품을 선택해주세요");
            return false;
        }
        
        localStorage.selectList=JSON.stringify([{
            cart_items : JSON.stringify({
                prd_id : product.prd_id, 
                prd_price : product.prd_price, 
                prd_sale_rate : product.prd_sale_rate, 
                prd_title : product.prd_title, 
                prd_img : product.prd_img, 
                count:1,
                data:resultArray
            })
        }]).toString();

        history.push({
            pathname: "/shop/pay"
        });
    }

    //단순 selectList 에 대한 돈계산.
    function calculatePrice(list){
        var totalPrice = 0;
        var count = 0;
        
        for(var key in list){
        var price = 0;
        var optionList = list[key].options.optionList;
        count++;
        for(var calKey in optionList){
        for(var i =0;i<	optionList[calKey].length ;i++){
            price += optionList[calKey][i].opt_price;
        }
        }

        if(list[key].options.isOptional == false){
        price += Math.round((list[key].prd_price) - ((list[key].prd_price * list[key].prd_sale_rate) / 100));
        }
        
        price *= list[key].count;
        totalPrice += price;
        }
        
        //배송비 추후 따로 받아와야함
        if(count > 0)
        totalPrice += 0; //shipPrice;

        return totalPrice;
    }

    function getOptionName(optionlist){

        var optionText="";
        for(let key in optionlist){
            optionText += "[옵션: ";
            let list = optionlist[key];     
            for(let z =0;z < list.length;z++){
                var opt = list[z];
                optionText += opt.opt_name + ( z + 1 == list.length ?"" : " / ") + (opt.opt_price > 0 ? " +" + numberWithCommas(opt.opt_price) + "원" : "");
            }
            optionText+="]";
        }
        return optionText;
    }

    function getOptionListLayout(optArray,prdKey,optionlist){
        optArray.push(
            <OptionListLayout
                prdKey={prdKey}
                item={selectList[prdKey]}
                shopOption={shopOption}
                getOptionName={getOptionName}
                optionlist={optionlist}
                setSelectList={setSelectList}
            />
        );
    }

    function modalOptionList(){
        var optArray = [];

        for(let prdKey in selectList){
            let optionlist = selectList[prdKey].options.optionList;   
            getOptionListLayout(optArray,prdKey,optionlist);
        } 

        console.log(selectList);
        console.log(calculatePrice(selectList));
        return optArray;

    }


    if(purchaseModal == false)
        return null;
    return (
        <Container>
            <PurchaseModalWrap>
                <ProductInfoWrap>
                    <ProductInfoTitleWrap>
                        <ProductName>{itemList.prd_title}</ProductName>
                        <ProductText>{itemList.prd_detail1}</ProductText>
                        <WishBtnWrap onClick={()=>{openPurchaseModal(false);}}>
                            <WishBtn src={IconClosed} alt="Closed button"/>
                        </WishBtnWrap>
                    </ProductInfoTitleWrap>
                    <OptionListWrap>
                        { modalOptionList() }
                    </OptionListWrap>
                    <OptionLine></OptionLine>
                    <PriceBoxWrap>
                        <PriceText>총 상품 금액</PriceText>
                        <TotalPrice><TotalPriceText>{numberWithCommas(calculatePrice(selectList))}</TotalPriceText>원</TotalPrice>
                    </PriceBoxWrap>
                </ProductInfoWrap>
                <ShopBtnWrap>
                    <CartBtn onClick={cartClick}>장바구니
                        {/* <Link to={"/cartMarket"}>장바구니</Link> */}
                    </CartBtn>
                    <PurchaseBtn onClick={payClick}>바로구매
                        {/* <Link to={"/shop/pay"}>바로구매</Link> */}
                    </PurchaseBtn>
                </ShopBtnWrap>
            </PurchaseModalWrap>
        </Container>
    );
}


const Container = styled.div`
    position: fixed; bottom: 0;
    width: 100%; height: 100vh;
    z-index: 1500;
    background: rgba(1,1,1,0.1);
`
const PurchaseModalWrap = styled.div`
    position: fixed; bottom: 0;
    width: 100%;
    background: #fff;
    padding: calc(100vw*(20/414)) 0 calc(100vw*(50/414));

`
const ProductInfoWrap = styled.div`
    display: flex;
    align-items: flex-end;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
    padding: calc(100vw*(25/414)) calc(100vw*(30/414)) calc(100vw*(25/414));
`
const ProductInfoTitleWrap = styled.div`
    position: relative;
    width: 100%;
    margin-bottom: calc(100vw*(25/414));
`
const WishBtnWrap = styled.button`
    position: absolute; top: calc(100vw*(-20/414)); right: 0;
`
const WishBtn = styled.img`
    width: calc(100vw*(14/414));
`
const ProductName = styled.p`
    width: 100%;
    font-size: calc(100vw*(19/414));
    margin-bottom: calc(100vw*(3/414));
    padding-right: calc(100vw*(30/414));
`
const ProductText = styled.p`
    width: 100%;
    font-size: calc(100vw*(15/414));
`
const PriceBoxWrap = styled.div`
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: calc(100vw*(10/414)) 0;
`
const PriceText = styled.p`
    font-size: calc(100vw*(18/414));
    font-weight: 900;
`
const TotalPrice = styled.p`
    display: flex;
    align-items: center;
    font-size: calc(100vw*(17/414));
    margin-top: calc(100vw*(6/414));
`
const TotalPriceText = styled.span`
    font-size: calc(100vw*(23/414));
    font-weight: 900;
`
const OptionLine = styled.div`
    width: calc(100vw*(392/414));
    height: calc(100vw*(1/414));
    background: #d1d1d1;
    margin-top: calc(100vw*(10/414));
`
const OptionListWrap = styled.div`
    width: 100%; max-height: calc(100vw*(240/414));
    overflow-y: scroll;
`
const DeliveryTextWrap = styled.div`
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: calc(100vw*(10/414)) 0;
`
const DeliveryText = styled.p`
    font-size: calc(100vw*(14/414));
    color: #707070;
`
const OptionNumberWrap = styled.div`
    position: relative;
`
const OptionNumberInput = styled.input`
    width: calc(100vw*(130/414)); height: calc(100vw*(38/414));
    border: calc(100vw*(1/414)) solid #d1d1d1;
    border-radius: calc(100vw*(8/414));
    font-size: calc(100vw*(15/414));
    text-align: center;
    color: #707070;
`
const CountBtnM = styled.span`
    position: absolute; top: 0; left: calc(100vw*(10/414));
    font-size: calc(100vw*(15/414));
    padding: calc(100vw*(10/414));
    color: #707070;
`
const CountBtnP = styled(CountBtnM)`
    left: auto; right: calc(100vw*(10/414));
`
const ShopBtnWrap = styled.div`
    width: 100%;
    display: flex;
    padding: calc(100vw*(6/414)) calc(100vw*(14/414));
`
const ShopBtn = styled.button`
    width: 100%;
    padding: calc(100vw*(11/414)) 0;
    background: #b4b4b4;
    border: calc(100vw*(3/414)) solid #a5a5a5;
    border-radius: calc(100vw*(6/414));
    color: #fff;
    font-size: calc(100vw*(18/414));
    font-weight: 900;
    margin: 0 calc(100vw*(4/414));
`
const CartBtn = styled(ShopBtn)``
const PurchaseBtn = styled(ShopBtn)`
    background: #ed2939;
    border-color: #bc343f;

`