//react
import { Link,useHistory } from "react-router-dom";

//css
import styled from "styled-components"

//img
import IconBackBtn from "../../img/ico_arrow_back.png"


export default function SubHeader({title,rank}) {
    
const history = useHistory();
const goBack = () =>{
    history.goBack();
}
  return (
        <Container>
            <SubHeaderWrap>
                {
                    rank ?
                    <BackBtnWrap onClick={goBack}>
                        <BackBtn></BackBtn>
                    </BackBtnWrap>
                    :
                    null
                }
                <div>
                    <SubHeaderTitle>{title}</SubHeaderTitle>
                </div>
            </SubHeaderWrap>
        </Container>
  );
}

//css
const Container = styled.div` 
    width: 100%;
`
const SubHeaderWrap = styled.div` 
    width: 100%; height: calc(100vw*(65/414));
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #fff;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
`
const BackBtnWrap = styled.div`
    position: absolute; left: calc(100vw*(15/414));
    width: calc(100vw*(45/414)); height: calc(100vw*(45/414));
`
const BackBtn = styled.button`
    width: calc(100vw*(45/414)); height: calc(100vw*(45/414));
    background: url(${IconBackBtn}) no-repeat center/calc(100vw*(10/414));
`
const SubHeaderTitle = styled.h2` 
    position: relative;
    display: flex;
    width: 100%;
    font-size: calc(100vw*(20/414));
    font-weight: bold;
`