//react
import React ,{useState,useEffect} from 'react';

//css
import styled from "styled-components"

//img
import IcoFilterBK from "../../../img/ico_filter_bk.png"
import IcoClosed from "../../../img/ico_closed_btn_bk.png"

//page
import ListFilter from "../../common/product/ListFilter"
import ListFilterSale from "../../common/product/ListFilterSale"
import ListFilterPrice from "../../common/product/ListFilterPrice"


export default function ProductFilter({filterModal ,openFilterModal}) {

    //modal scroll
    const body = document.querySelector('body');

    if(filterModal == false)
        return null;
    else
        body.style.overflow = 'hidden';

    return (
        <Container>
            <ProductFilterWrap>
                <FilterTopWrap>
                    <FilterTitleWrap>
                        <FilterTitleImg src={IcoFilterBK} alt="filter"/>
                        <FilterTitleText>필터</FilterTitleText>
                    </FilterTitleWrap>
                    <FilterTitleWrap>
                        <FilterClosedBtn onClick={()=>{openFilterModal(false); body.style.overflow = 'auto';}}></FilterClosedBtn>
                    </FilterTitleWrap>
                </FilterTopWrap>
                <ListFilter/>
                <ListFilterPrice/>
                <ListFilterSale/>
                <FilterTitleBtnWrap>
                    <FilterTitleBtn>적용</FilterTitleBtn>
                </FilterTitleBtnWrap>
            </ProductFilterWrap>
        </Container>
    );
}


const Container = styled.div`
    position: fixed; right: 0; top: 0;
    width: calc(100vw*(294/414)); height: 100vh;
    z-index: 1000;
    background: #fff;
    box-shadow: 0 calc(100vw*(3/414)) calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    padding-bottom: calc(100vw*(35/414));
    overflow-y: scroll;
`
const ProductFilterWrap = styled.div`
    width: 100%;
`
const FilterTopWrap = styled.div`
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #f7f7f7;
    padding: calc(100vw*(14/414)) calc(100vw*(22/414));
`
const FilterTitleWrap = styled.div`
    display: flex;
    align-items: center;
`
const FilterTitleImg = styled.img`
    width: calc(100vw*(9/414));
`
const FilterTitleText = styled.p`
    font-size: calc(100vw*(15/414));
    font-weight: 900;
    margin-left: calc(100vw*(8/414));
`
const FilterClosedBtn = styled.button`
    width: calc(100vw*(20/414)); height: calc(100vw*(20/414));
    background: url(${IcoClosed}) no-repeat center/calc(100vw*(15/414));
`
const FilterTitleBtnWrap = styled.div`
    text-align: right;
    padding: calc(100vw*(15/414)) calc(100vw*(22/414));
`
const FilterTitleBtn = styled.button`
    height: calc(100vw*(26/414));
    padding: 0 calc(100vw*(14/414));
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    color: #555;
    background: #fff;
    border-radius: calc(100vw*(3/414));
    border: calc(100vw*(1/414)) solid #979797;
`