//react
import {Link} from "react-router-dom";

//css
import styled from "styled-components"

//img
import IconstarOn from "../../../img/ico_star_on.svg"
import IconstarOff from "../../../img/ico_star_off.svg"

const getStarImageList = (count) =>{
    let imageList = [];
    for(var i =0;i < 5;i++){
        imageList.push(
            <Stars>
             <img src={i < count ? IconstarOn : IconstarOff} alt=""/>
            </Stars>
        )
    }
    return imageList;
}

export default function ReviewLayout({value,rank}) {
    
    return (
        <Container>
            <Link to={`/reviewImgDetail/${value.rev_id}`}>
                <CustomerImgWrap>
                    <CustomerImage src={value.rev_image ? value.rev_image : null} alt="Customer images"/>
                </CustomerImgWrap>
            </Link>
            {
                rank ?
                <ReviewBox>
                    <ReviewBoxTitleWrap>
                        <ReviewBoxTitle>{value.mem_username}</ReviewBoxTitle>
                        <StarsWrap>
                            {
                                getStarImageList(value.rev_score)
                            }
                        </StarsWrap>
                    </ReviewBoxTitleWrap>
                    <>
                        <ReviewDate>{value.rev_date}</ReviewDate>
                    </>
                    <ReviewProductWrap>
                        <ReviewProductText>{value.prd_title} {value.prd_detail1}</ReviewProductText>
                    </ReviewProductWrap>
                    <ReviewTextWrap>
                        <ReviewTextDetail>{value.rev_content}</ReviewTextDetail>
                    </ReviewTextWrap>
                </ReviewBox>
            :
                <Link to={`/reviewDetail/${value.rev_id}`}>
                    <ReviewBox>
                        <ReviewBoxTitleWrap>
                            <ReviewBoxTitle>{value.mem_username}</ReviewBoxTitle>
                            <StarsWrap>
                                {
                                    getStarImageList(value.rev_score)
                                }
                            </StarsWrap>
                        </ReviewBoxTitleWrap>
                        <>
                            <ReviewDate>{value.rev_date}</ReviewDate>
                        </>
                        <ReviewTextWrap>
                            <ReviewTextDetail>{value.rev_content}</ReviewTextDetail>
                        </ReviewTextWrap>
                    </ReviewBox>
                </Link>
            }

                {
                    rank ?
                    null
                    :
                    <ReviewLine></ReviewLine>
                }
        </Container>
  );
}

const Container = styled.div`
    width: 100%;
`
/* review */
const CustomerImgWrap = styled.div`
    display: flex;
    white-space: nowrap;
    overflow-y: visible;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: none;
    ::-webkit-scrollbar {
        display:none;
    }
    padding-top: calc(100vw*(18/414));
`
const CustomerImage = styled.img`
    width: calc(100vw*(112/414));
    margin: 0 calc(100vw*(1.5/414));
    &:first-child{
        margin-left: calc(100vw*(15/414));
    }
`
const ReviewBox = styled.div`
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: calc(100vw*(12/414)) calc(100vw*(22/414)) calc(100vw*(21/414));
`
const ReviewBoxTitleWrap = styled.div`
    display: flex;
    align-items: center;
`
const ReviewBoxTitle = styled.p`
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    white-space: nowrap;
    margin-right: calc(100vw*(12/414));
`
const ReviewDate = styled.p`
    font-size: calc(100vw*(13/414));
    color: #969696;
`
const ReviewProductWrap = styled.div`
    width: 100%;
    margin-top: calc(100vw*(18/414));
`
const ReviewProductText = styled.p`
    font-size: calc(100vw*(13/414));
    color: #ed2939;
    padding-bottom: calc(100vw*(8/414));
`
const ReviewTextWrap = styled.div`
    width: 100%;
`
const ReviewTextDetail = styled.p`
    font-size: calc(100vw*(13/414));
    line-height: 1.5;
`
const StarsWrap = styled.div`
    display: flex;
`
const Stars = styled.span`
    display: block;
    width: calc(100vw*(15/414));
    margin: 0 calc(100vw*(1/414));
`
const ReviewLine = styled.div`
    width: 100%; height: calc(100vw*(1/414));
    background: #f7f7f7;
`
