//react
import {useState, useEffect} from 'react';
import {Link, useHistory} from "react-router-dom";

//css
import styled from "styled-components"

//page
import serverController from '../../server/serverController';


export default function SignUpStep3({setAdressShow, adrZipcode, setAdrZipcode, adrAddress, setAdrAddress}) {
    const [next,setNext] = useState(false);

    const [userName,setuserName] = useState("");
    const [userId,setUserId] = useState("");
    const [userError,setUserError] = useState("");
    const [userCheck,setUserCheck] = useState(0);

    const [laterChk,setLaterChk] = useState(false);

    const [password,setPassword] = useState("");
    const [repassword,setRepassword] = useState("");
    const [email,setEmail] = useState("");
    const [phone,setPhone] = useState("");
    const [phoneCheck,setPhoneCheck] = useState(true);
    const [name,setName] = useState("");
    const [adrAddressDetail,setAdrAddressDetail] = useState("");
    const [phone2,setPhone2] = useState("");

    const [consent1,setConsent1] = useState(false);
    const [consent2,setConsent2] = useState(false);
    const [consent3,setConsent3] = useState(false);

    const history = useHistory();
    const userTextChange = (e) => { setuserName(e.target.value); checkUserwordVaildate();}
    const userIdTextChange = (e) =>{ setUserId(e.target.value); }
    const passwordTextChange = (e) => { setPassword(e.target.value); }
    const repasswordTextChange = (e) => { setRepassword(e.target.value); }
    const emailTextChange = (e) =>{ setEmail(e.target.value); }
    const phoneTextChange = (e) => { setPhone(e.target.value); }
    const nameTextChange = (e) => { setName(e.target.value); }
    const adrAddressDetailChange = (e) => { setAdrAddressDetail(e.target.value); checkAdrDetailVaildate();}
    const phone2Change = (e) => { setPhone2(e.target.value); checkPhone2Vaildate();}

    const consent1Change = () => { setConsent1(!consent1); }
    const consent2Change = () => { setConsent2(!consent2); }
    const consent3Change = () => { setConsent3(!consent3); }

    const openPostCode = () => { setAdressShow(true); }
    const closePostCode = () => { setAdressShow(false); }

    const checkPasswordRegex = () =>{
        //특수기호 포함
        var strongRegex = new RegExp("^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{8,})");
        //중간단계 (영문 숫자)
        var mediumRegex = new RegExp("^(((?=.*[a-z])(?=.*[A-Z]))|((?=.*[a-z])(?=.*[0-9]))|((?=.*[A-Z])(?=.*[0-9])))(?=.{8,})");
        return mediumRegex.test(password);
    }
    const checkUserwordVaildate = () =>{ return userName.length >= 2; }
    const checkpasswordVaildate = () =>{ return password == repassword; }
    const checkEmailVaildate = () =>{ return email.length > 6 && email.split('.').length > 1 && email.split('@').length == 2 
                                        && email.split('@')[1].length > 2 
                                        && email.split('@')[1].split('.').pop().length > 1 ; };
    const checkNameVaildate = () => { return name.length != 0 }
    const checkZipcodeVaildate = () =>{ return adrZipcode.length != 0 }
    const checkAddressVaildate = () =>{ return adrAddress.length != 0 }
    const checkAdrDetailVaildate = () => { return adrAddressDetail.length != 0 }
    const checkPhone2Vaildate = () => { return phone2.length != 0 }
    
    const Idtest = async ()=>{
        if(userId.length <= 3 || userId.length >= 10){
            alert("아이디는 최소 4자 이상, 10자 이하로 작성해야 합니다.")
            return setUserError("");
        }
        let res = await serverController.connectFetchController(`user/checkId?mem_name=${userId}`,"GET",null,null,null);
        setUserCheck(res.result);
        setUserError(res.message);
    }

    const getPasswordErrorMessage = () =>{
        if(repassword.length == 0)
            return "";
        if(checkpasswordVaildate() == false)
            return "비밀번호가 일치하지 않습니다.";
        else if(checkPasswordRegex() == false)
            return "비밀번호가 취약합니다.";
    }

    const getEmailErrorMessage = () =>{
        if(email.length == 0)
            return "";
        else if(checkEmailVaildate() == false){
            return "이메일이 정확하지 않습니다.";
        }
    }

    useEffect(()=>{
        if(checkUserwordVaildate() == true && userCheck == 1 && getPasswordErrorMessage.toString("") && getEmailErrorMessage.toString("")){
            if(laterChk == true){ //배송정보 나중에 작성 체크
                if(consent1 == true && consent2 == true){ //동의1,동의2
                    setNext(true);
                }else{
                   setNext(false);
                }
            }else{ //배송정보 입력
                if(checkNameVaildate() == true && checkZipcodeVaildate() == true && checkAddressVaildate() == true && checkAdrDetailVaildate() == true && checkAdrDetailVaildate() == true && checkPhone2Vaildate() == true){
                    if(consent1 == true && consent2 == true){ //동의1,동의2
                        setNext(true);
                    }
                } else{
                    setNext(false);
                }
            }
        }
        else{
            setNext(false);
        }
    },[userCheck,userId,password,repassword,email,laterChk,userName,adrZipcode,adrAddress,adrAddressDetail,phone2,consent1,consent2])

    const signUpClick = () =>{
        if(next == true){
            const formData = new FormData();
            formData.append('mem_name',userId);
            formData.append('mem_password',repassword);
            formData.append('mem_email',email);
            formData.append('mem_phone',phone);
            formData.append('mem_username',userName); 
            formData.append('mem_role',"USER");
            serverController.connectFetchController('user/join',"POST",formData,function(res){
            if(res.result == 1) {
                return history.push("/signUpComplete");
            }});
        }
    }
    
    const laterChkChange = (e) => {
        if(e === true){
            setLaterChk(true)
        }else{
            setLaterChk(false)
        }
    }

    return (
        <Container>
            <SignUpWarp>
                <SignUpSection>
                    <SignUpTitleWrap>
                        <SignUpTitleText>회원정보</SignUpTitleText>
                    </SignUpTitleWrap>
                    <SignUpListWrap>
                        <SignUpList>
                            <SignUpLabel htmlFor="">이름</SignUpLabel>
                            <SignUpInput type="text" placeholder="이름을 입력하세요." value={userName} onChange={userTextChange} disabled={laterChk} />
                            <SignUpErrorWrap>
                            </SignUpErrorWrap>
                        </SignUpList>
                        <SignUpList>
                            <SignUpLabel >아이디</SignUpLabel>
                            <SignUpInput type="text" placeholder="아이디를 입력하세요." value={userId} onChange={userIdTextChange} onBlur={Idtest}/>
                            <SignUpErrorWrap>
                                <SignUpErrorText>{userError}</SignUpErrorText>
                            </SignUpErrorWrap>
                        </SignUpList>
                        <SignUpList>
                            <SignUpPassword>
                                <SignUpLabel htmlFor="">비밀번호</SignUpLabel>
                                <SignUpInput type="password" placeholder="비밀번호를 입력하세요." value={password} onChange={passwordTextChange}/>
                            </SignUpPassword>
                            <SignUpPassword>
                                <SignUpLabel htmlFor="">비밀번호<br/>확인</SignUpLabel>
                                <SignUpInput type="password" placeholder="비밀번호를 다시 입력하세요." value={repassword} onChange={repasswordTextChange}/>
                            </SignUpPassword>
                            <SignUpErrorWrap>
                                <SignUpErrorText>{getPasswordErrorMessage()}</SignUpErrorText>
                            </SignUpErrorWrap>
                        </SignUpList>
                        <SignUpList>
                            <SignUpLabel htmlFor="">이메일</SignUpLabel>
                            <SignUpInput type="email" placeholder="이메일을 입력하세요." value={email} onChange={emailTextChange}/>
                            <SignUpErrorWrap>
                                <SignUpErrorText>{getEmailErrorMessage()}</SignUpErrorText>
                            </SignUpErrorWrap>
                        </SignUpList>
                        <SignUpList>
                            <SignUpPassword>
                                <SignUpLabel htmlFor="">연락처</SignUpLabel>
                                <SignUpInputTel type="tel" placeholder="연락처를 입력하세요." value={phone} onChange={phoneTextChange}/>
                                <AuthenticationBtn>발송</AuthenticationBtn>
                                <SignUpErrorText>{ phoneCheck ? "" : "휴대번호를 인증해주세요."}</SignUpErrorText>
                            </SignUpPassword>
                            <SignUpPassword>
                                <SignUpLabel htmlFor="">인증번호</SignUpLabel>
                                <SignUpInputTel type="tel" placeholder="인증번호를 입력하세요." value={phone} onChange={phoneTextChange}/>
                                <AuthenticationBtn>인증</AuthenticationBtn>
                                <SignUpErrorWrap>
                                    <SignUpErrorText>{ phoneCheck ? "" : "휴대번호를 인증해주세요."}</SignUpErrorText>
                                </SignUpErrorWrap>
                            </SignUpPassword>
                        </SignUpList>
                    </SignUpListWrap>
                </SignUpSection>
                
                <SignUpSection>
                    <SignUpTitleWrap>
                        <SignUpTitleText>배송정보</SignUpTitleText>
                        <LaterWrap>
                            <ChkBox type="checkbox" name="" id="later_chk" checked={laterChk} onChange={(e)=>laterChkChange(e.target.checked)}/>
                            <ChkBoxLabel htmlFor="later_chk"></ChkBoxLabel>
                            <ChkBoxText>나중에 작성하기</ChkBoxText>
                        </LaterWrap>
                    </SignUpTitleWrap>
                    <SignUpListWrap>
                        <SignUpList>
                            <SignUpLabel htmlFor="">이름</SignUpLabel>
                            <SignUpInput type="text" placeholder="이름을 입력하세요." value={name} onChange={nameTextChange} disabled={laterChk} />
                            <SignUpErrorWrap>
                            </SignUpErrorWrap>
                        </SignUpList>
                        <SignUpList>
                            <SignUpLabel htmlFor="">주소</SignUpLabel>
                            <SignUpAddress type="number" disabled={laterChk} value={adrZipcode} readonly/>
                            <AddressBtn onClick={openPostCode}>주소검색</AddressBtn>
                            <SignUpPassword>
                                <SignUpLabel htmlFor=""></SignUpLabel>
                                <SignUpInput type="text" disabled={laterChk} value={adrAddress} readonly/>
                            </SignUpPassword>
                            <SignUpPassword>
                                <SignUpLabel htmlFor=""></SignUpLabel>
                                <SignUpInput type="text" placeholder="상세주소를 입력하세요." disabled={laterChk} value={adrAddressDetail} onChange={adrAddressDetailChange}/>
                            </SignUpPassword>
                            <SignUpErrorWrap></SignUpErrorWrap>
                        </SignUpList>
                        <SignUpList>
                            <SignUpLabel htmlFor="">연락처</SignUpLabel>
                            <SignUpInput type="email" placeholder="연락처를 입력하세요." disabled={laterChk} value={phone2} onChange={phone2Change}/>
                            <SignUpErrorWrap>
                            </SignUpErrorWrap>
                        </SignUpList>
                    </SignUpListWrap>
                </SignUpSection>

                <ConsentWrap>
                    <ConsentList>
                        <ConsentchkBox type="checkbox" name="" id="consent_type1" required checked={consent1} onClick={consent1Change}/>
                        <ConsentListText htmlFor="consent_type1">개인정보 취급 방침에 동의합니다.</ConsentListText>
                        <ConsentListTextSub>전체내용 보기(필수)</ConsentListTextSub>
                    </ConsentList>
                    <ConsentList>
                        <ConsentchkBox type="checkbox" name="" id="consent_type2" required checked={consent2} onClick={consent2Change}/>
                        <ConsentListText htmlFor="consent_type2">이용약관에 동의합니다.</ConsentListText>
                        <ConsentListTextSub>전체내용 보기(필수)</ConsentListTextSub>
                    </ConsentList>
                    <ConsentList>
                        <ConsentchkBox type="checkbox" name="" id="consent_type3" checked={consent3} onClick={consent3Change}/>
                        <ConsentListText htmlFor="consent_type3">마케팅 및 프로모션 소식을 수신하고 싶습니다.</ConsentListText>
                        <ConsentListTextSub>(선택)</ConsentListTextSub>
                    </ConsentList>
                </ConsentWrap>

                <SignUpBtnWrap>
                    <SignUpBtn signUpBtnOn={next} onClick={signUpClick}>회원가입 하기</SignUpBtn>
                </SignUpBtnWrap>

            </SignUpWarp>
        </Container>
  );
}

const Container = styled.div`
    position: relative;
    width: 100%;
    z-index: 100;
    background: #fff;
`
const SignUpWarp = styled.div`
    width: 100%;
    padding: 0 calc(100vw*(12/414));
`
const SignUpSection = styled.div`
    width: 100%;
    margin-bottom: calc(100vw*(15/414));
`
const SignUpTitleWrap = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding-top: calc(100vw*(30/414));
    border-bottom: calc(100vw*(1/414)) solid #d1d1d1;
`
const SignUpTitleText = styled.p`
    font-size: calc(100vw*(15/414));
    font-weight: 900;
    padding: calc(100vw*(12/414)) calc(100vw*(30/414));
`
const SignUpListWrap = styled.div`
    width: 100%;
    padding: calc(100vw*(10/414)) calc(100vw*(12/414)) 0 calc(100vw*(30/414));
`
const SignUpList = styled.div`
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding: calc(100vw*(2/414)) 0;
    margin: calc(100vw*(5/414)) 0;
`
const SignUpPassword = styled.div`
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding-top: calc(100vw*(4/414));
`
const SignUpLabel = styled.label`
    width: calc(100vw*(72/414));
    font-size: calc(100vw*(15/414));
    font-weight: bold;
`
const SignUpInput = styled.input`
    width: calc(100% - calc(100vw*(72/414))); height: calc(100vw*(32/414));
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    text-indent: calc(100vw*(14/414));
    color: #4b4b4b;
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(3/414));
    
    ${({SignUpError})=>{
        return SignUpError ? 
        `
            border-color: #a50000;
        `
        :
        ``
    }}
`
const SignUpErrorWrap = styled.div`
    width: 100%; height: calc(100vw*(14/414));
    padding-top: calc(100vw*(2/414));
`
const SignUpErrorText = styled.p`
    font-size: calc(100vw*(10/414));
    font-weight: bold;
    color: #c32222;
    text-align: right;
`
const SignUpInputTel = styled.input`
    width: calc(100% - calc(100vw*(167/414))); height: calc(100vw*(32/414));
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    text-indent: calc(100vw*(14/414));
    color: #4b4b4b;
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(3/414));

    ${({SignUpError})=>{
        return SignUpError ? 
        `
            border-color: #a50000;
        `
        :
        ``
    }}
`
const AuthenticationBtn = styled.button`
    width: calc(100vw*(88/414)); height: calc(100vw*(32/414));
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    color: #fff;
    background: #646464;
    border: calc(100vw*(2/414)) solid #979797;
    border-radius: calc(100vw*(3/414));
    margin-left: calc(100vw*(7/414));
`
const LaterWrap = styled.div`
    position: relative;
    margin-right: calc(100vw*(14/414));
`
const ChkBox = styled.input`
    &:checked + label::after {
        content: '';
        position: absolute; top: calc(100vw*(6/414)); right: calc(100vw*(100/414));
        width: calc(100vw*(8/414)); height: calc(100vw*(8/414));
        background: #ed3342;
        border-radius: calc(100vw*(2/414));
    }
`
const ChkBoxLabel = styled.label`
    &&::before {
        content: '';
        position: absolute; top: calc(100vw*(1/414)); right: calc(100vw*(95/414));
        width: calc(100vw*(16/414)); height: calc(100vw*(16/414));
        text-align: center;
        background: #fafafa;
        border: calc(100vw*(1/414)) solid #ed3342;
        border-radius: calc(100vw*(4/414));
    }
`
const ChkBoxText = styled.span`
    font-size: calc(100vw*(13/414));
    font-weight: 900;
`
const SignUpChkBox = styled.input`
    &:checked + label::after {
        content: '';
        position: absolute; top: calc(100vw*(5/414)); right: calc(100vw*(103/414));
        width: calc(100vw*(8/414)); height: calc(100vw*(8/414));
        background: #bc343f;
        border-radius: calc(100vw*(2/414));
    }
`
const LaterText = styled.label`
    font-size: calc(100vw*(13/414));
    font-weight: 900;
    &&::before {
        content: '';
        position: absolute; top: calc(100vw*(-1/414)); right: calc(100vw*(97/414));
        width: calc(100vw*(16/414)); height: calc(100vw*(16/414));
        text-align: center;
        background: #fafafa;
        border: calc(100vw*(2/414)) solid #bc343f;
        border-radius: calc(100vw*(4/414));
    }
`
const AddressBtn = styled.button`
    width: calc(100vw*(118/414)); height: calc(100vw*(32/414));
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    color: #fff;
    background: #646464;
    border: calc(100vw*(2/414)) solid #979797;
    border-radius: calc(100vw*(3/414));
    margin-left: calc(100vw*(7/414));
`
const SignUpAddress = styled.input`
    width: calc(100% - calc(100vw*(197/414))); height: calc(100vw*(32/414));
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    text-indent: calc(100vw*(14/414));
    color: #4b4b4b;
    border: calc(100vw*(1/414)) solid #979797;
    border-radius: calc(100vw*(3/414));
`
const ConsentWrap = styled.div`
    width: 100%
`
const ConsentList = styled.div`
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    padding: calc(100vw*(12/414)) 0 calc(100vw*(12/414)) calc(100vw*(70/414));
`
const ConsentchkBox = styled.input`
    &:checked + label::after {
        content: '';
        position: absolute; top: calc(100vw*(16/414)); left: calc(100vw*(44/414));
        width: calc(100vw*(8/414)); height: calc(100vw*(8/414));
        background: #505050;
        border-radius: calc(100vw*(2/414));
    }
`
const ConsentListText = styled.label`
    font-size: calc(100vw*(14/414));
    font-weight: bold;
    color: #505050;
    &&::before {
        content: '';
        position: absolute; top: calc(100vw*(11/414)); left: calc(100vw*(39/414));
        width: calc(100vw*(16/414)); height: calc(100vw*(16/414));
        text-align: center;
        background: #fafafa;
        border: calc(100vw*(1/414)) solid #505050;
        border-radius: calc(100vw*(4/414));
    }
`
const ConsentListTextSub = styled.span`
    font-size: calc(100vw*(10/414));
    font-weight: bold;
    color: #505050;
`
const SignUpBtnWrap = styled.div`
    text-align: center;
    padding-top: calc(100vw*(45/414));
    padding-bottom: calc(100vw*(80/414));
`
const SignUpBtn = styled.button`
    width: calc(100vw*(342/414));
    background: #ed2939;
    border: calc(100vw*(3/414)) solid #bc343f;
    color: #fff;
    border-radius: calc(100vw*(6/414));
    padding: calc(100vw*(18/414)) 0;
    font-size: calc(100vw*(18/414));
    font-weight: 900;
    ${({signUpBtnOn})=>{
        return signUpBtnOn ? 
        `
            background: #ed2939;
            border-color: #bc343f;
        `
        :
        `
            background: #b4b4b4;
            border-color: #a5a5a5;
        `
    }}
`