//react
import {Link} from "react-router-dom";

//css
import styled from "styled-components"


export default function PasswordResult() {

    return (
            <Container>
                <WithdrawalWrap>
                    <WithdrawalTitle>비밀번호 변경이 완료되었습니다.</WithdrawalTitle>
                </WithdrawalWrap>
                <WithdrawalBtnWrap>
                    <Link to={"/"}>
                        <WithdrawalBtn>홈으로 돌아가기</WithdrawalBtn>
                    </Link>
                </WithdrawalBtnWrap>
            </Container>
    );
}

//css
const Container = styled.div` 
    position: relative;
    width: 100%;
    background: #fff;
    z-index: 10;
    padding-bottom: calc(100vw*(100/414));
`
const WithdrawalWrap = styled.div`
    width: 100%; min-height: calc(100vh - calc(100vw*(240/414)));
    padding-top: calc(100vw*(20/414));
    padding: calc(100vw*(140/414)) calc(100vw*(11/414)) 0;
`
const WithdrawalTitle = styled.p`
    font-size: calc(100vw*(16/414));
    font-weight: bold;
    color: #4b4b4b;
    text-align: center;
`
const WithdrawalBtnWrap = styled.div`
    width: 100%;
    text-align: center;
    padding: 0 calc(100vw*(16/414));
`
const WithdrawalBtn = styled.button`
    width: 100%;
    font-size: calc(100vw*(20/414));
    font-weight: 900;
    color: #4b4b4b;
    padding: calc(100vw*(16/414)) 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.13);
    border: calc(100vw*(2/414)) solid #efefef;
    border-radius: calc(100vw*(13/414));
`