//react
import React ,{useState,useEffect} from 'react';
import {Link} from "react-router-dom";

//css
import styled from "styled-components"

//page
import serverController from '../../server/serverController';
import Product from "../common/product/Product";

const CateData = [
    { 
      cat_id : 1,
      cat_name : "티/블라우스/셔츠"
    },
    { 
      cat_id : 2,
      cat_name : "치마/바지/핫팬츠"
    },
    { 
      cat_id : 3,
      cat_name : "원피스/점프수트"
    },
    { 
      cat_id : 4,
      cat_name : "신발/악세서리"
    },
    { 
      cat_id : 5,
      cat_name : "속옷/이너웨어"
    },
    { 
      cat_id : 6,
      cat_name : "가방/기타"
    }
  ]

export default function MallProduct() {
    const [itemList,setItemList] = useState([]);
    const [category,setCategory] = useState(CateData);
    
    useEffect(()=>{
        serverController.connectFetchController(`products/info?prd_type=2`,"GET",null,function(res){
        if(res.result == 1) {
            setItemList(res.items)
            console.log(res);
        }});
    },[]);
    console.log(itemList);


  return (
        <Container>
            <MallProductWrap>
                <MallProductTitleWrap>
                    <MallProductTitleBox>
                        <MallProductTitle>쇼핑몰 상품</MallProductTitle>
                    </MallProductTitleBox>
                    <FilterListWrap>
                        <FilterList>
                            {
                                category.map((value)=>{
                                    return <FilterListText>{value.cat_name}</FilterListText>
                                })
                            }
                        </FilterList>
                    </FilterListWrap>
                </MallProductTitleWrap>
                {/* Product */}
                <ProductBox>
                    {
                        itemList.length <= 0 ?
                        <MallNoneText>쇼핑몰 상품이 없습니다.</MallNoneText>
                        :
                        itemList.map((value,index)=>{
                            return (
                                <Link to={`/shop/product/${value.prd_type}/${value.prd_id}`}>
                                    <Product rank={false} value={value}/>
                                </Link>
                            )
                        })
                    }
                </ProductBox>
            </MallProductWrap>
        </Container>
  );
}

/* main title */
const Container = styled.div` 
    width:100%;
`
const MallProductWrap = styled.div` 
    width:100%; min-height: calc(100vh - calc(100vw*(120/414)));
    background: #fff;
    margin-bottom: calc(100vw*(24/414));
    padding-bottom: calc(100vw*(80/414));
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
`
const MallProductTitleWrap = styled.div` 
    padding: calc(100vw*(20/414)) calc(100vw*(11/414)) calc(100vw*(5/414));
`
const FilterListWrap = styled.div` 
    width:100%;
    margin-bottom: calc(100vw*(20/414));
`
const FilterList = styled.ul` 
    width:100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
`
const FilterListText = styled.li`
    max-width: calc(100vw*(120/414));
    font-size: calc(100vw*(14/414));
    font-weight: bold;
    color: #333;
    margin: calc(100vw*(12/414)) 0;
    padding: 0 calc(100vw*(12/414));
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    white-space: normal;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical; 
`
const MallFilterWrap = styled.div` 
    padding: calc(100vw*(5/414)) calc(100vw*(14/414));
    border-bottom: calc(100vw*(1/414)) solid #d1d1d1;
`
const MallProductTitleBox = styled.ul`
    display: flex;
    align-items: center;
    justify-content: center;
`
const MallProductTitle = styled.p`
    position: relative;
    display: block;
    padding: calc(100vw*(10/414)) 0 calc(100vw*(20/414));
    font-weight: bold;
    font-size: calc(100vw*(18/414));
    ${({isMarket})=>{
        return isMarket ? 
        `&::after {
            content: '';
            position: absolute; left: 0; bottom: 0;
            display: block;
            width: 100%; height: calc(100vw*(3/414));
            background: #ED2939;
            border-radius: calc(100vw*(3/414));
        }`
        :
        ``
    }}
`
/* Product */
const ProductBox = styled.div`
    margin-bottom: calc(100vw*(24/414));
`
const MallNoneText = styled.div`
    width: 100%;
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    color: #7e7e7e;
    text-align: center;
    padding: calc(100vw*(50/414)) 0;
`