//react
import React ,{useState,useEffect} from 'react';
import {Link} from "react-router-dom";

//css
import styled from "styled-components"

//page
import serverController from '../../server/serverController';
import appInfoController from '../../server/appInfoController';

import Product from "../common/product/Product";


export default function MainProduct() {
    const [itemList,setItemList] = useState([]);
    const [buttonShow,setButtonShow] = useState(true);
    let isClick = false;

    useEffect(()=>{
        serverController.connectFetchController(`products/newMartItem?mart_page=1`,"GET",null,function(res){
        if(res.result == 1) {
            setItemList(res.info)
        }});
    },[]);
    
    const itemsMore = async () => {
        if(isClick)
            return;

        isClick = true;

        let res = await serverController.connectFetchController(`products/newMartItem?mart_page=${(itemList.length / appInfoController.PageIndexLimit) + 1}`,"GET");
        if(res.result == 1){
            await setItemList(info => info.concat(res.info));
            isClick = false;
            if(res.info.length == 0)
                return setButtonShow(false);
        }
    }

  return (
        <Container>
            <Mainline></Mainline>
            <ProductWrap>
                <MainTitleWrap>
                    <MainTitle>따끈따끈 신상품</MainTitle>
                </MainTitleWrap>

                {/* Product */}
                <ProductBox>
                    {
                        itemList.map((value,index)=>{ 
                            return (
                                <Link to={`/shop/product/${value.prd_type}/${value.prd_id}`}>
                                    <Product rank={false} value={value}/>
                                </Link>
                            )
                        })
                    }
                </ProductBox>
                <ProductMoreBtnWrap>
                    {/* <ProductMoreBtn>+ 더보기</ProductMoreBtn> */}
                    {
                        buttonShow ?
                        
                        <ProductMoreBtn onClick={itemsMore}>+ 더보기</ProductMoreBtn>
                        :
                        null
                    }
                </ProductMoreBtnWrap>
            </ProductWrap>
        </Container>
  );
}

/* main prd_title */
const Container = styled.div` 
    width:100%;
    padding: 0 calc(100vw*(10/414));
`
const Mainline = styled.span`
    display: block;
    width: calc(100vw*(390/414)); height: calc(100vw*(3/414));
    background: #d9d9d9;
    margin: 0 auto;
`
const ProductWrap = styled.div` 
    width:100%;
    background: #fff;
    margin-top: calc(100vw*(12/414));
    margin-bottom: calc(100vw*(24/414));
    padding-top: calc(100vw*(26/414));
    padding-bottom: calc(100vw*(30/414));
    border-radius: calc(100vw*(15/414));
`
const MainTitleWrap = styled.div` 
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    width:100%;
    padding-left: calc(100vw*(22/414));
    margin-bottom: calc(100vw*(15/414));
`
const MainTitle = styled.h2`
    font-size: calc(100vw*(15/414));
    line-height: 1.2;
    font-weight: 900;
`

/* Product */
const ProductBox = styled.div`
    margin-bottom: calc(100vw*(24/414));
`
const ProductMoreBtnWrap = styled.div`
    text-align: center;
`
const ProductMoreBtn = styled.button`
    width: calc(100vw*(280/414));
    padding: calc(100vw*(11/414)) 0;
    font-size: calc(100vw*(19/414));
    color: #ED2939;
    font-weight: 900;
    background: #f7f7f7;
    border-radius: calc(100vw*(21/414));
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
`
