        tax_payment_date:"2020.12.30",
        txn_state: 5,
        path :"/shop/product",
        src: productImg,
        prd_title:"[농심]농심신라면",
        prd_count:"5",
        prd_detail:"멀티 5입",
        prd_details:"1봉 5개입",
        point:"120",
        prd_sale_rate:"20",
