//react
import {Link,useHistory} from "react-router-dom";
import {useState,useEffect} from "react";

//css
import styled from "styled-components"

const reviewItem = [
    {
        prd_id: 1,
        prd_title:"[농심]농심신라면",
        prd_detail1:"멀티 5입",
        checked: false
    },
    {
        prd_id: 2,
        prd_title:"[농심]농심신라면",
        prd_detail1:"멀티 5입",
        checked: false
    },
    {
        prd_id: 3,
        prd_title:"[농심]농심신라면",
        prd_detail1:"멀티 5입",
        checked: false
    },
    {
        prd_id: 4,
        prd_title:"[농심]농심신라면",
        prd_detail1:"멀티 5입",
    }
]

export default function ReviewModal({revOptionModal ,openrevOptionModal}) {

    //modal scroll
    const body = document.querySelector('body');
    const [radioChecked,setRadioChecked] = useState(0);
    const history = useHistory();

    const handleRadio = (e) =>{
        // console.log(e.target.value);
        setRadioChecked(e.target.value);
    }
    const checkRadio = () =>{
    //    console.log(radioChecked);
       history.push({
            pathname : "/review/write",
            state: radioChecked
        })
    }


    if(revOptionModal == false)
        return null;
        body.style.overflow = 'hidden';

    return(
        <Container>
            <ProductOptionWrap>
                <ReviewListWrap>
                {
                    reviewItem.map((value, index)=>{
                        return (
                            <ProductInfoWrap>
                                <ProductInfoTitleWrap>
                                    <Checks>
                                        <ChkBox id={value.prd_id} key={index} value={index} name="radio" type="radio" defaultChecked={index == 0 ? true : false} onChange={handleRadio}/>
                                        <ChkBoxLabel htmlFor={value.prd_id}></ChkBoxLabel>
                                    </Checks>
                                    <ProductName>{value.prd_title} {value.prd_detail1}</ProductName>
                                </ProductInfoTitleWrap>
                            </ProductInfoWrap>
                        )
                    })
                }
                </ReviewListWrap>
                <ShopBtnWrap>
                    <CartBtn onClick={()=>{openrevOptionModal(false); body.style.overflow = 'auto';}}>취소</CartBtn>
                    <PurchaseBtn onClick={()=>{body.style.overflow = 'auto'; checkRadio()}}>
                        리뷰적기
                    </PurchaseBtn>
                </ShopBtnWrap>
            </ProductOptionWrap>
        </Container>

    );
}

const Container = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed; bottom: 0;
    width: 100%; height: 100vh;
    z-index: 1500;
    background: rgba(1,1,1,0.4);
`
const ProductOptionWrap = styled.div`
    width: calc(100vw*(389/414));
    background: #fff;
    border-radius: 15px;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    padding: calc(100vw*(20/414)) calc(100vw*(30/414));
    overflow: hidden;
`
const ReviewListWrap = styled.div`
    max-height: calc(80vh - calc(100vw*(160/414)));
    overflow-y: scroll;
    margin-bottom: calc(100vw*(20/414));
`
const ProductInfoWrap = styled.div`
    display: flex;
    align-items: flex-end;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
    padding: calc(100vw*(15/414)) calc(100vw*(15/414));
    border-bottom: calc(100vw*(1/414)) solid #E4E4E4;
    &:last-child {border-bottom: none;}
`
const ProductInfoTitleWrap = styled.div`
    position: relative;
    display: flex;
    width: 100%;
`
const ProductName = styled.p`
    width: 100%;
    font-size: calc(100vw*(15/414));
`
const ShopBtnWrap = styled.div`
    width: 100%;
    display: flex;
    padding: calc(100vw*(6/414)) calc(100vw*(14/414));
`
const ShopBtn = styled.button`
    width: 100%;
    padding: calc(100vw*(11/414)) 0;
    background: #b4b4b4;
    border: calc(100vw*(3/414)) solid #a5a5a5;
    border-radius: calc(100vw*(6/414));
    color: #fff;
    font-size: calc(100vw*(18/414));
    font-weight: 900;
    margin: 0 calc(100vw*(4/414));
`
const CartBtn = styled(ShopBtn)``
const PurchaseBtn = styled(ShopBtn)`
    background: #ed2939;
    border-color: #bc343f;
    ${({DetailBtnOn})=>{
        return DetailBtnOn ? 
        `
            background: #ED2939;
            border-color: #BC343F;
        `
        :
        ``
    }}

`
const Checks = styled.div`
    width: calc(100vw*(18/414));
    margin-right: calc(100vw*(15/414));
`
const ChkBox = styled.input`
    position: absolute;
    width: calc(100vw*(1/414)); height: calc(100vw*(1/414));
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip:rect(0,0,0,0);
    border: 0;
    &:checked + label::after {
        content: '';
        position: absolute; top: calc(100vw*(-5/414)); left: calc(100vw*(5/414));
        width: calc(100vw*(8/414)); height: calc(100vw*(8/414));
        background: #ed3342;
        border-radius: calc(100vw*(2/414));
    }
`
const ChkBoxLabel = styled.label`
    display: inline-block;
    position: relative;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    text-indent: -9999px;
    &&::before {
        content: '';
        position: absolute; left: 0; top: calc(100vw*(-10/414));
        width: calc(100vw*(16/414)); height: calc(100vw*(16/414));
        text-align: center;
        background: #fafafa;
        border: calc(100vw*(1/414)) solid #ed3342;
        border-radius: calc(100vw*(4/414));
    }
`