//react
import React ,{useState,useEffect} from 'react';
import {Link} from "react-router-dom";

//css
import styled from "styled-components"

//page
import serverController from '../../server/serverController';


export default function Category() {
    const [catIdData,setCatIdData] = useState([]);
    useEffect(()=>{
        serverController.connectFetchController(`categories/parent`,"GET",null,function(res){
        if(res.result == 1) {
            setCatIdData(res.items)
        }});
    },[]);

    return (
        <Container>
            <CategoryWrap>
                    {
                        catIdData.map((value)=>{
                            return (
                                <CategoryTextWarp>
                                    <Link to={`/category/product/${value.cat_id}`}>
                                        <CategoryText>{value.cat_name}</CategoryText>
                                    </Link>
                                </CategoryTextWarp>
                            )
                        })
                    }
            </CategoryWrap>
        </Container>
    );
}

//css
const Container = styled.div`
    position: relative;
    width: 100%;
    background-color: #fff;
`
const CategoryWrap = styled.div`
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    width: 100%;
    padding: calc(100vw*(12/414)) calc(100vw*(11/414)) 0;
`
const CategoryTextWarp = styled.div`
    width: 50%;
    padding: calc(100vw*(8/414)) 0;
    border-bottom: calc(100vw*(1/414)) solid #d1d1d1;
`
const CategoryText = styled.p`
    font-size: calc(100vw*(19/414));
    font-weight: 900;
    color: #333;
    text-align: center;
    padding: calc(100vw*(20/414));
`