//react
import {useState, useEffect} from 'react';
import {Link, useHistory} from "react-router-dom";
import { UserAction } from '../../store/actionCreators'; // 담기

//css
import styled from "styled-components"

//page
import serverController from '../../server/serverController';

export default function Login() {
    const [next ,setNext] = useState(false);
    const [id,setId] = useState("");
    const [password,setPassword] = useState("");
    const [userError,setUserError] = useState("");
    const history = useHistory();

    const idTextChange = (e) =>{ setId(e.target.value); }
    const passwordTextChange = (e) => { setPassword(e.target.value); }

    const checkIdVaildate = () =>{ return id.length > 3; };
    const checkpasswordVaildate = () =>{ return password.length > 2; }

    useEffect(()=>{
        if(checkIdVaildate() && checkpasswordVaildate())
            setNext(true);
        else
            setNext(false);
    },[id,password])
    
    useEffect(() => {
    },[])

    const LoginClick = () =>{
        if(next == true){
            serverController.loginFetchController("user/authenticate","POST",JSON.stringify({mem_name:id,mem_password:password}),function(res){
            if(res.jwttoken != null) {
                console.log(res);
                serverController.setToken(res.jwttoken);
                console.log("로그인성공");
                //redux
                serverController.connectFetchController(`user/mypage`,"GET",null,function(res){
                    if(res.result == 1) {
                        let userData = {
                            mem_id:res.list[0].mem_id,
                            mem_name:res.list[0].mem_name,
                            mem_username:res.list[0].mem_username,
                            mem_email:res.list[0].mem_email,
                            mem_phone:res.list[0].mem_phone,
                        }
                        let adrData = {
                            adr_id:res.list[0].adr_id,
                            adr_zipcode:res.list[0].adr_zipcode,
                            adr_name:res.list[0].adr_name,
                            adr_address1:res.list[0].adr_address1,
                            adr_address2:res.list[0].adr_address2,
                            adr_phone:res.list[0].adr_phone,
                        }
                        UserAction.updateUserIdData( { userId : userData } ) // 담기
                        UserAction.updateAdrIdData( { ardId : adrData } ) // 담기
                }});


                return history.push("/");
            }
        },function(){ 
            setUserError("입력하신 아이디 또는 비밀번호가 일치하지 않습니다.");
        });
            
        }
    }

    return (
        <Container>
            <LoginWrap>
                <LoginInputWrap>
                    <div>
                        <LoginText>아이디</LoginText>
                        <LoginTextInput type="text" placeholder={"아이디를 입력하세요"} value={id} onChange={idTextChange}/>
                    </div>
                    <div>
                        <LoginText>비밀번호</LoginText>
                        <LoginTextInput type="password" placeholder={"비밀번호를 입력하세요"} value={password} onChange={passwordTextChange}/>
                    </div>
                    <AutoChkWrap>
                        <ChkBox type="checkbox" name="" id="auto_login" required/>
                        <ChkBoxLabel htmlFor="auto_login"></ChkBoxLabel>
                        <ChkBoxText>자동 로그인</ChkBoxText>
                    </AutoChkWrap>
                    <LoginErrorWrap>
                        <LoginErrorText>{userError}</LoginErrorText>
                    </LoginErrorWrap>
                    <LoginBtnWrap>
                        <LoginBtn LoginBtnOn={next} onClick={LoginClick}>로그인</LoginBtn>
                        <Link to={"/loginID"}>
                            <LoginBtnText>아이디/비밀번호 찾기</LoginBtnText>
                        </Link>
                    </LoginBtnWrap>
                </LoginInputWrap>
            </LoginWrap>
            <SignUpWrap>
                <SignUpText>아직 회원이 아니신가요?</SignUpText>
                <Link to={"/signUpStep1"}>
                    <SignUpBtn>회원가입하기</SignUpBtn>
                </Link>
            </SignUpWrap>
        </Container>
  );
}

const Container = styled.div`
    position: relative;
    width: 100%; height: calc(100vh - calc(100vw*(65/414)));
    z-index: 100;
`
const LoginWrap = styled.div`
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    width: 100%; height: calc(100vh - calc(100vw*(160/414)));
    background: #fff;
    padding: 0 calc(100vw*(36/414));
`
const LoginInputWrap = styled.div`
    width: 100%;
`
const LoginText = styled.p`
    font-size: calc(100vw*(15/414));
    font-weight: 900;
    margin-top: calc(100vw*(15/414));
    margin-bottom: calc(100vw*(8/414));
`
const LoginTextInput = styled.input`
    width: 100%; height: calc(100vw*(50/414));
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    border: calc(100vw*(1/414)) solid #707070;
    border-radius: calc(100vw*(8/414));
    text-indent: calc(100vw*(30/414));
    ${({TextError})=>{
        return TextError ? 
        `
            border-color: #a50000;
        `
        :
        ``
    }}
`
const AutoChkWrap = styled.div`
    position: relative;
    display: flex;
    align-items: baseline;
    justify-content: flex-end;
    padding: calc(100vw*(10/414)) 0 calc(100vw*(4/414));
`
const ChkBox = styled.input`
    &:checked + label::after {
        content: '';
        position: absolute; top: calc(100vw*(14/414)); right: calc(100vw*(85/414));
        width: calc(100vw*(8/414)); height: calc(100vw*(8/414));
        background: #f0505d;
        border-radius: calc(100vw*(2/414));
    }
`
const ChkBoxLabel = styled.label`
    &&::before {
        content: '';
        position: absolute; top: calc(100vw*(9/414)); right: calc(100vw*(80/414));
        width: calc(100vw*(16/414)); height: calc(100vw*(16/414));
        text-align: center;
        background: #fafafa;
        border: calc(100vw*(1/414)) solid #f0505d;
        border-radius: calc(100vw*(4/414));
    }
`
const ChkBoxText = styled.span`
    font-size: calc(100vw*(15/414));
    font-weight: 900;
`
const LoginErrorWrap = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    height: calc(100vw*(32/414));
`
const LoginErrorText = styled.p`
    text-align: center;
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    color: #ed2939;
`
const LoginBtnWrap = styled.div`
    width: 100%;
`
const LoginBtn = styled.button`
    width: 100%;
    border: calc(100vw*(3/414)) solid #bc343f;
    color: #fff;
    border-radius: calc(100vw*(6/414));
    padding: calc(100vw*(18/414)) 0;
    font-size: calc(100vw*(18/414));
    font-weight: 900;
    ${({LoginBtnOn})=>{
        return LoginBtnOn ? 
        `
            background: #ed2939;
            border-color: #bc343f;
        `
        :
        `
            background: #b4b4b4;
            border-color: #a5a5a5;
        `
    }}
`
const LoginBtnText = styled.p`
    font-size: calc(100vw*(15/414));
    font-weight: 900;
    text-align: right;
    margin-top: calc(100vw*(10/414));
`
const SignUpWrap = styled.div`
    position: absolute; bottom: 0;
    width: 100%;
    padding: calc(100vw*(32/414)) 0;
    background: #fff;
`
const SignUpText = styled.p`
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    text-align: center;
`
const SignUpBtn = styled.p`
    font-size: calc(100vw*(15/414));
    font-weight: 900;
    text-align: center;
    color: #ed2939;
    margin-top: calc(100vw*(8/414));
`
