//react
import {Link} from "react-router-dom";
import {useState, useEffect} from 'react';

//css
import styled from "styled-components"

//page
import serverController from '../../../server/serverController';


export default function ShopPayBtn({pay, usePoint, resultPrice, itemList, shipPrice, reduxZipcode}) {

    console.log("------------------");
    console.log("사용포인트 " + usePoint);
    console.log("결제금액 " + (resultPrice > 50000 ? resultPrice : resultPrice + shipPrice));
    console.log("우편번호 " + reduxZipcode);
    console.log("productlist " + JSON.stringify(itemList));
    const payClick = () =>{
        const formData = new FormData();
        formData.append("productlist" , JSON.stringify(itemList));
        formData.append("point" , usePoint == ""  || usePoint <= 0 ? 0 : usePoint);
        formData.append("totalPrice" , resultPrice > 50000 ? resultPrice : resultPrice + shipPrice);
        formData.append("zipcode" , reduxZipcode);
        
        serverController.connectFetchController('order/validate/price',"POST",formData,function(res){
        if(res.result == 1) {
            console.log("결제 API 전송 완료");
        }});
    }
    return (
        <Container>
            <ShopPayBtnWrap>
                <PayBtnTextWrap>
                    <PayBtnText>위 주문내용을 확인하였으며, 화원 본인은 결제에 동의합니다.</PayBtnText>
                </PayBtnTextWrap>
                <PayBtnWrap>
                    <PayBtn>
                        <Link onClick={payClick(), pay}>결제하기</Link>
                        {/* <Link to={"/shop/payComplete"}>결제하기</Link> */}
                    </PayBtn>
                </PayBtnWrap>
            </ShopPayBtnWrap>
        </Container>
    );
}

//css
const Container = styled.div` 
    position: relative;
    width: 100%;
    background: #fff;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    z-index: 10;
    margin-top: calc(100vw*(10/414));
`
const ShopPayBtnWrap = styled.div`
    width: 100%;
    padding: calc(100vw*(36/414)) calc(100vw*(11/414)) calc(100vw*(85/414));
`
const PayBtnTextWrap = styled.div`
    width: 100%;
    padding-bottom: calc(100vw*(20/414));
`
const PayBtnText = styled.p`
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    color: #969696;
    text-align: center;
`
const PayBtnWrap = styled.div`
    width: 100%;
    text-align: center;
`
const PayBtn = styled.button`
    width: 100%;
    font-size: calc(100vw*(18/414));
    font-weight: 900;
    color: #fff;
    padding: calc(100vw*(18/414)) 0;
    background: #ed2939;
    border: calc(100vw*(3/414)) solid #bc343f;
    border-radius: calc(100vw*(32/414));
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
`