//react
import {Link} from "react-router-dom";
import React ,{useState,useEffect} from 'react';

//css
import styled from "styled-components"

//img
import productImg from "../../../img/product_img.png"

//page
import serverController from '../../../server/serverController';
import OrderProduct from "../../common/product/OrderProduct";
import DateFilter from "../../common/product/DateFilter";


const ReturnItemList = [
    {
        prd_id: 0,
        tax_payment_date:"2020.12.30",
        txn_state: 4,
        path :"/shop/product",
        src: productImg,
        prd_title:"[농심]농심신라면",
        prd_count:"5",
        prd_detail:"멀티 5입",
        prd_details:"1봉 5개입",
        point:"120",
        prd_sale_rate:"10",
        txn_payment_amount:"3400",
        prd_qty:"1"
    }
]

export default function OrderList({openprdOptionModal}) {
    const [itemList,setItemList] = useState([]);

    useEffect(()=>{
        serverController.connectFetchController(`cart/list?cart_type=1`,"GET",null,function(res){
            console.log(res);
        if(res.result == 1) {
            setItemList(res.list);
        }});
    },[]);

    function updateItem(list){
        setItemList(list);
    }

    return(
        <Container>
            <ReturnTitleWrap>
                <ReturnTitleBox>
                    <ReturnMarketTitle>
                        <Link to={"/orderList"}>주문,배송 목록</Link>
                    </ReturnMarketTitle>
                    <ReturnShopTitle>
                        <Link to={"/returnList"}>취소,반품,교환 목록</Link>
                    </ReturnShopTitle>
                </ReturnTitleBox>
            </ReturnTitleWrap>
            <ReturnWrap>
                <DateFilter/>

                <ReturnListWrap>
                    <>
                        {
                            itemList.length > 0 ?
                            itemList.map((value,index)=>{
                                return (
                                    <OrderProduct
                                        rank={false}
                                        value={value}
                                        openprdOptionModal={openprdOptionModal}
                                    />
                                );
                            })
                            :
                            <OrderNoneWrap>
                                <OrderNoneText>취소,반품,교환 목록이 없습니다.</OrderNoneText>
                            </OrderNoneWrap>
                        }
                    </>
                </ReturnListWrap>
            </ReturnWrap>
        </Container>

    );
}

/* cart title */
const Container = styled.div`
    width: 100%;
`
const ReturnTitleWrap = styled.div`
    width: 100%;
    padding-top: calc(100vw*(10/414));
`
const ReturnTitleBox = styled.ul`
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: calc(100vw*(10/414));
`
const CartTitle = styled.li`
    position: relative;
    width: calc(100vw*(160/414));
    padding: calc(100vw*(14/414)) 0 calc(100vw*(24/414));
    margin: 0 calc(100vw*(4/414));
    background: #fff;
    font-size: calc(100vw*(15/414));
    font-weight: 900;
    color: #7e7e7e;
    text-align: center;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
`
const ReturnMarketTitle = styled(CartTitle)``
const ReturnShopTitle = styled(CartTitle)`
    color: #ED2939;
    &::after {
        content: '';
        position: absolute;
        display: block;
        width: 100%; height: calc(100vw*(20/414));
        background: #fff;
        z-index: 90;
    }
`

/* cart list */
const ReturnWrap = styled.div`
    position: relative;
    width: 100%; min-height: calc(100vh - calc(100vw*(150/414)));
    background: #fff;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    z-index: 10;
    margin-top: calc(100vw*(-10/414));
    padding-bottom: calc(100vw*(100/414));
`
const ReturnListWrap = styled.div`
    width: 100%;
    padding: calc(100vw*(5/414)) calc(100vw*(15/414));
`
const OrderNoneWrap = styled.div`
    width: 100%;
`
const OrderNoneText = styled.p`
    text-align: center;
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    color: #979697;
    padding: calc(100vw*(28/414)) 0;
`