//react
import {Link} from "react-router-dom";
import React ,{useState,useEffect} from 'react';

//css
import styled from "styled-components"

//img
import wishBtnOff from "../../../img/ico_wish_off.svg"
import wishBtnOn from "../../../img/ico_wish_on.svg"
import SelectIcon from "../../../img/ico_select.png"
import IconSoldOut from "../../../img/ico_sold_out.png"
import appInfoController from '../../../server/appInfoController'

//숫자 3자리수 콤마 코드
function numberWithCommas(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}


export default function CartProduct({updateCartItemList,handleSingleCheck, value, rank, checkProduct, selectList}) {
    const cart_items = JSON.parse(value.cart_items);
    const [count,setCount] = useState(cart_items.count);
    const [priceInfo,setPriceInfo] = useState({productCount : 0, totalPrice : 0, originalPrice: 0,  price : 0 , optionPrice : 0 , salePrice : 0});

    useEffect(()=>{
        setPriceInfo(getPrice(cart_items));
    },[checkProduct]);

    console.log(priceInfo);
    
    //장바구니에 담아진 최종 리스트에 대한 돈계산.
    function getPrice(option){
        let price = 0;
        let optionPrice = 0;
        let salePrice = 0;
        let totalPrice = 0;
        let originalPrice = 0;
        let options = option.data;

        for(let j = 0; j<options.length;j++){
        
            var p = 0;
            var ogp = 0;
            var op = 0;
            var sp = 0;
            let optionList = options[j].options.optionList;

            for(let calKey in optionList){
                for(let z =0;z<	optionList[calKey].length ;z++){
                    op += optionList[calKey][z].opt_price;
                }
            }

            if(options[j].options.isOptional == false){
                ogp += options[j].prd_price;
                p += Math.round((options[j].prd_price) - ((options[j].prd_price * options[j].prd_sale_rate) / 100));
                sp += Math.round(((options[j].prd_price * options[j].prd_sale_rate) / 100));
            }

            originalPrice += (ogp * options[j].count);
            price += (p * options[j].count);
            optionPrice += (op * options[j].count);
            salePrice += (sp * options[j].count);
            totalPrice += (p + op) *  options[j].count;
        }

        return { totalPrice : totalPrice, originalPrice: originalPrice,  price : price , optionPrice : optionPrice , salePrice : salePrice };
    }

    function getOptionTextList(){
        var optionText="";
        var value = cart_items.data;
        for(let i in value){
            for(let key in value[i].options.optionList){
                optionText += "[";
                let optionlist = value[i].options.optionList[key];     
                optionText += (value[i].options.isSetOptional==true)? value[i].prd_title+":" : "";
                for(let z =0;z < optionlist.length;z++){
                    var opt = optionlist[z];
                    optionText += opt.opt_name + ( z + 1 == optionlist.length ?"" : "/");
                }
                optionText+="] ";
            }
        }
        return optionText;
    }
    getOptionTextList();

    const updateCount = (num) =>{
        if(count + num > 0){
            setCount(count + num);
            cart_items.count = count + num;
            updateCartItemList(cart_items);
        }
    }

    return (
        <Container>
        {
            value.prd_soldout != 0 ? 
            <CartListWrap>
                <CartList>
                    <Checks>
                        <ChkBox id={value.cart_id} type="checkbox" name="checkbox" checked={checkProduct.includes(value.cart_id) ? true : false}  onChange={(e)=>{handleSingleCheck(e.target.checked,value.cart_id)}} />
                        <ChkBoxLabel htmlFor={value.cart_id}></ChkBoxLabel>
                    </Checks>
                    <CartListImg>
                        <Link to={`/shop/product/${value.prd_type}/${value.prd_id}`}>
                            <CartListImages src={appInfoController.ImagePath + cart_items.prd_img} alt="product image"/>
                        </Link>
                    </CartListImg>
                    <CartListText>
                        <Link to={`/shop/product/${value.prd_type}/${value.prd_id}`}>
                            <ProductTitle>
                                <ProductName>{cart_items.prd_title}</ProductName>
                                <ProductText>{getOptionTextList()}</ProductText>
                            </ProductTitle>
                        </Link>
                        <ProductTitleText>
                            <div>
                                <Point>적립금 {numberWithCommas(cart_items.prd_earn_price ? cart_items.prd_earn_price : "0")}원</Point>
                                <Discount>Discount</Discount>
                                <DiscountRate>{cart_items.prd_sale_rate ? cart_items.prd_sale_rate : "0"}%</DiscountRate>
                            </div>
                            <div>
                                <Price>{numberWithCommas(Math.round(priceInfo.originalPrice * count))}</Price>
                                <TotalPrice><TotalPriceText>{numberWithCommas(Math.round(priceInfo.price * count))}</TotalPriceText>원</TotalPrice>
                            </div>
                        </ProductTitleText>
                        {
                            rank ? 
                            <ProductOprion> 
                                <OptionNumberInput value={count}/>
                                <CountBtnM onClick={()=>{updateCount(-1)}}> - </CountBtnM>
                                <CountBtnP onClick={()=>{updateCount(1)}}> + </CountBtnP>
                            </ProductOprion>
                            :
                            <CartAddBtnWrap>
                                <CartAddBtn>장바구니에 담기</CartAddBtn>
                            </CartAddBtnWrap>
                        }
                    </CartListText>
                </CartList>
            </CartListWrap>
            :
            <CartListWrap>
                <CartList>
                    <Checks>
                        <ChkBox id={value.prd_id} checked={value.checked} type="checkbox" onChange={(e)=>{handleSingleCheck(false)}}/>
                        <ChkBoxLabel htmlFor={value.prd_id}></ChkBoxLabel>
                    </Checks>
                    <CartListImg>
                        <CartListImages src={appInfoController.ImagePath + value.prd_img} alt="product image"/>
                        <SoldOutImgWrap>
                            <SoldOutImgText>품절</SoldOutImgText>
                        </SoldOutImgWrap>
                    </CartListImg>
                    <CartListText>
                        <ProductTitle>
                            <SoldOutProductName>{cart_items.prd_title}</SoldOutProductName>
                            <SoldOutProductText>{getOptionTextList()}</SoldOutProductText>
                        </ProductTitle>
                        <ProductTitleText>
                            <div>
                                <SoldOutPoint>적립금 {numberWithCommas(cart_items.prd_earn_price ? cart_items.prd_earn_price : "0")}원</SoldOutPoint>
                                <SoldOutDiscount>Discount</SoldOutDiscount>
                                <SoldOutDiscountRate>{cart_items.prd_sale_rate}%</SoldOutDiscountRate>
                            </div>
                            <div>
                                <SoldOutPrice>{numberWithCommas(cart_items.prd_price)}</SoldOutPrice>
                                <SoldOutTotalPrice><SoldOutTotalPriceText>{numberWithCommas(Math.round(cart_items.prd_price - cart_items.prd_price*cart_items.prd_sale_rate/100))}</SoldOutTotalPriceText>원</SoldOutTotalPrice>
                            </div>
                        </ProductTitleText>
                        <SoldOutWrap>
                            <SoldOutText><IcoSoldOut src={IconSoldOut} alt="품절"/>품절</SoldOutText>
                        </SoldOutWrap>
                        <CartBtnWrap>
                            <WishBtn>
                                <img src={wishBtnOn} alt="wish list"/>
                            </WishBtn>
                        </CartBtnWrap>
                    </CartListText>
                </CartList>
            </CartListWrap>
            }
        </Container>
  );
}
const Container = styled.div`
    padding: 0 calc(100vw*(11/414));
    border-bottom: calc(100vw*(1/414)) solid #f7f7f7;
`
const CartListWrap = styled.div`
    width: 100%;
`
const CartList = styled.div`
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    padding: calc(100vw*(24/414)) calc(100vw*(16/414)) calc(100vw*(12/414)) calc(100vw*(16/414));
`
const Checks = styled.div`
    width: calc(100vw*(18/414));
    margin-right: calc(100vw*(15/414));
`
const ChkBox = styled.input`
    position: absolute;
    width: calc(100vw*(1/414)); height: calc(100vw*(1/414));
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip:rect(0,0,0,0);
    border: 0;
    &:checked + label::after {
        content: '';
        position: absolute; top: calc(100vw*(-3/414)); left: calc(100vw*(5/414));
        width: calc(100vw*(8/414)); height: calc(100vw*(8/414));
        background: #ed3342;
        border-radius: calc(100vw*(2/414));
    }
`
const ChkBoxLabel = styled.label`
    display: inline-block;
    position: relative;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    text-indent: -9999px;
    &&::before {
        content: '';
        position: absolute; left: 0; top: calc(100vw*(-8/414));
        width: calc(100vw*(16/414)); height: calc(100vw*(16/414));
        text-align: center;
        background: #fafafa;
        border: calc(100vw*(1/414)) solid #ed3342;
        border-radius: calc(100vw*(4/414));
    }
`
const CartListImg = styled.div`
    position: relative;
    width: calc(100vw*(80/414)); height: calc(100vw*(80/414));
`
const CartListImages = styled.img`
    width: calc(100vw*(80/414)); height: calc(100vw*(80/414));
    object-fit: contain;
`
const CartListText = styled.div`
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: calc(100vw*(255/414));
    padding-left: calc(100vw*(15/414));
`
const ProductTitle = styled.div`
    width: 100%;
    margin-bottom: calc(100vw*(5/414));
`
const ProductTitleText = styled.div`
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    width: 100%;
`
const ProductName = styled.p`
    width: 100%;
    font-size: calc(100vw*(15/414));
    margin-bottom: calc(100vw*(3/414));
`
const ProductText = styled.p`
    width: 100%;
    font-size: calc(100vw*(12/414));
`
const Point = styled.p`
    font-size: calc(100vw*(11/414));
    font-weight: bold;
    color: #292929;
`
const Discount = styled.span`
    font-size: calc(100vw*(10/414));
    margin-top: calc(100vw*(5/414));
    color: #e20000;
`
const DiscountRate = styled.span`
    font-size: calc(100vw*(12/414));
    color: #e20000;
    font-weight: bold;
    margin-top: calc(100vw*(3/414));
    margin-left: calc(100vw*(4/414));
`
const Price = styled.span`
    display: block;
    font-size: calc(100vw*(13/414));
    color: #707070;
    text-align: right;
    padding-right: calc(100vw*(8/414));
    text-decoration: line-through;
`
const TotalPrice = styled.span`
    display: flex;
    align-items: center;
    font-size: calc(100vw*(15/414));
    margin-top: calc(100vw*(3/414));
`
const TotalPriceText = styled.span`
    font-size: calc(100vw*(20/414));
    font-weight: 900;
`
const ProductOprion = styled.div`
    display: flex;
    justify-content: flex-end;
    position: relative;
    width: 100%;
    margin-top: calc(100vw*(3/414));
`
const OptionNumberInput = styled.input`
    width: calc(100vw*(135/414)); height: calc(100vw*(30/414));
    border: calc(100vw*(1/414)) solid #d1d1d1;
    border-radius: calc(100vw*(8/414));
    font-size: calc(100vw*(15/414));
    text-align: center;
    color: #707070;
`
const CountBtnM = styled.span`
    position: absolute; top: 0; right: calc(100vw*(95/414));
    font-size: calc(100vw*(15/414));
    padding: calc(100vw*(6/414)) calc(100vw*(10/414));
    color: #707070;
`
const CountBtnP = styled(CountBtnM)`
    left: auto; right: calc(100vw*(10/414));
`
const CartBtnWrap = styled.div`
    position: absolute; top: calc(100vw*(8/414)); right: calc(100vw*(25/414));
    display: flex;
    align-items: center;
`
const WishBtn = styled.button`
    display: flex;
    align-items: center;
    width: calc(100vw*(24/414));
    padding: calc(100vw*(5/414));
`
const CartAddBtnWrap = styled.div`
    width: 100%;
    text-align: right;
    margin-top: calc(100vw*(3/414));
`
const CartAddBtn = styled.button`
    text-align: right;
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    color: #ed2939;
    padding: calc(100vw*(6/414)) calc(100vw*(13/414));
    border: calc(100vw*(1/414)) solid #ed2939;
    border-radius: calc(100vw*(5/414));
`

/* sold out */
const SoldOutWrap = styled.div`
    width: 100%;
    display: flex;
    justify-content: flex-end;
`
const SoldOutText = styled.p`
    display: flex;
    align-items: center;
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    color: #292929;
`
const IcoSoldOut = styled.img`
    width: calc(100vw*(12/414));
    margin-right: calc(100vw*(8/414));
`
const SoldOutImgWrap = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute; left: 0; top: 0;
    width: 100%; height: 100%;
    background: rgba(1,1,1,0.5);
`
const SoldOutImgText = styled.p`
    color: #fff;
    text-align: center;
    font-size: calc(100vw*(16/414));
    font-weight: 900;
`
const SoldOutProductName = styled(ProductName)`
    color: #7d7d7d;
`
const SoldOutProductText = styled(ProductText)`
    color: #7d7d7d;
`
const SoldOutPoint = styled(Point)`
    color: #7d7d7d;
`
const SoldOutDiscount = styled(Discount)`
    color: #ee7d7d;
`
const SoldOutDiscountRate = styled(DiscountRate)`
    color: #ee7d7d;
`
const SoldOutPrice = styled(Price)`
    color: #7d7d7d;
`
const SoldOutTotalPrice = styled(TotalPrice)`
    color: #7d7d7d;
`
const SoldOutTotalPriceText = styled(TotalPriceText)`
    color: #7d7d7d;
`