//react
import {useState} from 'react';

//css
import styled from "styled-components"


//숫자 3자리수 콤마 코드
function numberWithCommas(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}


const btnlist = (point_state) => {

    switch(point_state){
        case 1 : return ( <PointStatus>적립 완료</PointStatus> );
        case 2 : return ( <PointStatus>적립금 사용</PointStatus> );
        case 3 : return ( <PointStatus>적립금 소멸</PointStatus> );
    }

}

export default function PointProduct({value, rank}) {

    return (
        <Container>
            <CartListWrap>
                <OrderDateTitleWrap>
                    <OrderDate>2021.01.01</OrderDate>
                </OrderDateTitleWrap>
                <CartList>
                    <CartListImg>
                        <img src={value.src} alt="product image"/>
                    </CartListImg>
                    <CartListText>
                        <ProductTitle>
                            <ProductName>{value.prd_title} 외 {value.prd_count}건</ProductName>
                            <ProductText>{value.prd_detail1}</ProductText>
                            <ProductTextDetail>{value.prd_detail2}</ProductTextDetail>
                            <TotalPrice>{numberWithCommas(value.txn_payment_amount)}원 <ProductQty>{value.prd_qty}개</ProductQty></TotalPrice>
                        </ProductTitle>
                    </CartListText>
                </CartList>
                <PointWrap>
                    <PointText>적립금: {value.prd_earn_price}원</PointText>
                    {
                        btnlist(value.point_state)
                    }
                </PointWrap>
            </CartListWrap>
        </Container>
        
  );
}
const Container = styled.div`
    width: 100%;
    padding: 0 calc(100vw*(12/414));
    border-radius: calc(100vw*(12/414));
    border: calc(100vw*(1/414)) solid #979797;
    margin-bottom: calc(100vw*(25/414));
`
const CartListWrap = styled.div`
    width: 100%;
`
const OrderDateTitleWrap = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: calc(100vw*(12/414)) calc(100vw*(15/414)) calc(100vw*(10/414));
    border-bottom: 1px solid #f7f7f7;
`
const OrderDate = styled.p`
    font-size: calc(100vw*(16/414));
    font-weight: bold;
`
const CartList = styled.div`
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    padding: calc(100vw*(20/414)) calc(100vw*(24/414));
`
const CartListImg = styled.div`
    position: relative;
    width: calc(100vw*(82/414));
`
const CartListText = styled.div`
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: calc(100vw*(255/414));
    padding-left: calc(100vw*(15/414));
`
const ProductTitle = styled.div`
    width: 100%
`
const ProductName = styled.p`
    width: 100%;
    font-size: calc(100vw*(16/414));
`
const ProductText = styled.p`
    width: 100%;
    font-size: calc(100vw*(13/414));
`
const ProductTextDetail = styled.p`
    width: 100%;
    font-size: calc(100vw*(13/414));
    color: #707070;
    margin-top: calc(100vw*(5/414));
`
const TotalPrice = styled.span`
    display: flex;
    align-items: center;
    font-size: calc(100vw*(13/414));
    margin-top: calc(100vw*(2/414));
    color: #707070;
`
const ProductQty = styled.span`
    font-size: calc(100vw*(13/414));
    color: #707070;
    margin-left: calc(100vw*(8/414));
`
const Line = styled.div`
    width: calc(100vw*(367/414)); height: calc(100vw*(1/414));
    background: #f7f7f7;
    margin: 0 auto;
`
const PointWrap = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-top: calc(100vw*(1/414)) solid #d1d1d1;
`
const PointText = styled.p`
    font-size: calc(100vw*(15/414));
    font-weight: bold;
    padding: calc(100vw*(23/414)) calc(100vw*(32/414));
`
const PointStatus = styled(PointText)`
    color: #ed2939;
`