//css
import styled from "styled-components"

//img
import IcoFilter from "../../../img/ico_filter.png"


export default function ProductTitleDetail({openFilterModal}) {
  return (
        <Container>
            <ProductTitleDetailText>
                <ProductTitleDetailNumber>30</ProductTitleDetailNumber>개의 상품
            </ProductTitleDetailText>
            <ProductFilterBtnWrap onClick={()=>{openFilterModal(true)}}>
                <ProductFilterBtn src={IcoFilter} alt="filter"/>
                <ProductFilterText>필터</ProductFilterText>
            </ProductFilterBtnWrap>
        </Container>
            
);
}

/* main title */
const Container = styled.div`
display: flex;
justify-content: space-between;
align-items: center;
width:100%;
`
const ProductTitleDetailText = styled.p`
font-size: calc(100vw*(14/414));
font-weight: bold;
`
const ProductTitleDetailNumber = styled.span`
font-size: calc(100vw*(15/414));
font-weight: 900;
margin-right: calc(100vw*(7/414)); 
`
const ProductFilterBtnWrap = styled.button`
display: flex;
align-items: center;
background: #f7f7f7;
border: calc(100vw*(1/414)) solid #979797;
padding: calc(100vw*(7/414)) calc(100vw*(18/414));
border-radius: calc(100vw*(8/414));
`
const ProductFilterBtn = styled.img`
width: calc(100vw*(13/414));
margin-right: calc(100vw*(5/414));
`
const ProductFilterText = styled.span`
font-size: calc(100vw*(13/414));
color: #707070;
font-weight: bold;
`