//react
import React ,{useState,useEffect} from 'react';
import {Link} from "react-router-dom";

//css
import styled from "styled-components"

export default function MarketTabBar({setTabBtnA, setTabBtnB, setTabBtnC, setTabBtnD, setTabBtnE}) {
    const PopupBtnOn = (e) => {
        setTabBtnA(e == 1);
        setTabBtnB(e == 2);
        setTabBtnC(e == 3);
        setTabBtnD(e == 4);
        setTabBtnE(e == 5);
    }
    
  return (
        <Container>
            <MarketTabBarWarp>
                <li onClick={() => {PopupBtnOn(1)}}>
                    <MarketTab>마켓<br/>소개</MarketTab>
                </li>
                <li onClick={() => {PopupBtnOn(2)}}>
                    <MarketTab>오마딜</MarketTab>
                </li>
                <li onClick={() => {PopupBtnOn(3)}}>
                    <MarketTab>전단<br/>행사</MarketTab>
                </li>
                <li onClick={() => {PopupBtnOn(4)}}>
                    <MarketTab>오뚜기<br/>기획</MarketTab>
                </li>
                <li onClick={() => {PopupBtnOn(5)}}>
                    <MarketTab>나들이<br/>특가</MarketTab>
                </li>
            </MarketTabBarWarp>
        </Container>
  );
}

//css
const Container = styled.div` 
    width:100%;
    padding-bottom: calc(100vw*(12/414));
`
const MarketTabBarWarp = styled.ul`
    display: flex;
    white-space: nowrap;
    overflow-y: visible;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: none;
    ::-webkit-scrollbar {
        display:none;
    }   
`
const MarketTab = styled.li`
    display: flex;
    align-items: center;
    justify-content: center;
    width: calc(100vw*(52/414)); height: calc(100vw*(52/414));
    font-size: calc(100vw*(13/414));
    text-align: center;
    color: #fff;
    background: #ED2939;
    border-radius: 50%;
    margin-left: calc(100vw*(10/414));
`