//react
import React ,{useState,useEffect} from 'react';

//css
import styled from "styled-components"


export default function ListFilterPrice({value}) {
    const [filterList,setFilterList] = useState(false);

    return (
        <Container>
            <FilterListWrap>
                <FilterList>
                    <FilterTypeTitleWrap onClick={()=>{setFilterList(e => !e)}}>
                        <FilterTypeTitle>가격대</FilterTypeTitle>
                    </FilterTypeTitleWrap>
                    {
                        filterList ?
                        <FilterTypeTextWrap>
                            <FilterTypebox>
                                <FilterTypeText>낮은 가격순</FilterTypeText>
                                <Checks>
                                    <ChkBox id="filter_typeA" type="checkbox"/>
                                    <ChkBoxLabel htmlFor="filter_typeA"></ChkBoxLabel>
                                </Checks>
                            </FilterTypebox>
                            <FilterTypebox>
                                <FilterTypeText>높은 가격순</FilterTypeText>
                                <Checks>
                                    <ChkBox id="filter_typeB" type="checkbox"/>
                                    <ChkBoxLabel htmlFor="filter_typeB"></ChkBoxLabel>
                                </Checks>
                            </FilterTypebox>
                        </FilterTypeTextWrap>
                        :
                        null
                    }
                </FilterList>
            </FilterListWrap>
        </Container>
  );
}
const Container = styled.div`
    width: 100%;
`
const FilterListWrap = styled.div`
    padding: 0 calc(100vw*(12/414));
`
const FilterList = styled.div`
    border-bottom: calc(100vw*(1/414)) solid #969696;
`
const FilterTypeTitleWrap = styled.div`
    padding: calc(100vw*(18/414)) calc(100vw*(14/414));
`
const FilterTypeTitle = styled.p`
    font-size: calc(100vw*(14/414));
    font-weight: bold;
`
const FilterTypeTextWrap = styled.div`
    width: 100%;
    padding-top: calc(100vw*(15/414));
    padding-bottom: calc(100vw*(15/414));
`
const FilterTypebox = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: calc(100vw*(6/414)) calc(100vw*(14/414));
    border-bottom: calc(100vw*(1/414)) solid #eaeaea;
    &:last-child {
        border-bottom: none;
    }
`
const FilterTypeText = styled.p`
    font-size: calc(100vw*(13/414));
`
const Checks = styled.div`
    width: calc(100vw*(18/414));
`
const ChkBox = styled.input`
    position: absolute;
    width: calc(100vw*(1/414)); height: calc(100vw*(1/414));
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip:rect(0,0,0,0);
    border: 0;
    &:checked + label::after {
        content: '';
        position: absolute; top: calc(100vw*(-3/414)); left: calc(100vw*(5/414));
        width: calc(100vw*(8/414)); height: calc(100vw*(8/414));
        background: #ed3342;
        border-radius: calc(100vw*(2/414));
    }
`
const ChkBoxLabel = styled.label`
    display: inline-block;
    position: relative;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    text-indent: -9999px;
    &&::before {
        content: '';
        position: absolute; left: 0; top: calc(100vw*(-8/414));
        width: calc(100vw*(16/414)); height: calc(100vw*(16/414));
        text-align: center;
        background: #fafafa;
        border: calc(100vw*(1/414)) solid #ed3342;
        border-radius: calc(100vw*(4/414));
    }
`