//react
import {Link} from "react-router-dom";
import {useState, useEffect} from 'react';

//css
import styled from "styled-components"

//img
import productImg from "../../../img/product_img.png"

//page
import OrderProduct from "../../common/product/OrderProduct";

const OrderItemList = [
    {
        prd_id: 0,
        tax_payment_date:"2020.12.30",
        txn_state: 1,
        path :"/shop/product",
        src: productImg,
        prd_title:"[농심]농심신라면",
        prd_count:"5",
        prd_detail:"멀티 5입",
        prd_details:"1봉 5개입",
        point:"120",
        prd_sale_rate:"10",
        txn_payment_amount:"3400",
        prd_qty:"1"
    }
]

export default function PayComplete({openprdOptionModal}) {

    return (
        <Container>
            <ShopPayInfoWrap>
                <PayProductListWrap>
                    <>
                        {
                            OrderItemList.map((value,index)=>{ return (<OrderProduct rank={true} value={value} openprdOptionModal={openprdOptionModal}/>); })
                        }
                    </>
                </PayProductListWrap>
                <MainBtnWrap>
                    <MainBtn>
                        <Link to={"/mypage"}>마이페이지</Link>
                    </MainBtn>
                </MainBtnWrap>

            </ShopPayInfoWrap>
        </Container>
    );
}

//css
const Container = styled.div`
    position: relative;
    width: 100%;
    z-index: 10;
`
const ShopPayInfoWrap = styled.div`
    width: 100%;
`
const PayProductListWrap = styled.div`
    position: relative;
    width: 100%;
    padding: calc(100vw*(12/414)) calc(100vw*(15/414)) calc(100vw*(5/414));
    background: #fff;
    z-index: 10;
`
const MainBtnWrap = styled.div`
    width: 100%;
    border-radius: 0 0 calc(100vw*(15/414)) calc(100vw*(15/414));
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    background: #fff;
    padding: calc(100vw*(42/414)) calc(100vw*(15/414)) calc(100vw*(48/414));
`
const MainBtn = styled.button`
    width: 100%;
    padding-top: calc(100vw*(20/414));
    padding: calc(100vw*(18/414)) 0;
    border-radius: calc(100vw*(13/414));
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    font-size: calc(100vw*(20/414));
    font-weight: 900;
`