//react
import {Link} from "react-router-dom";
import {useState, useEffect} from 'react';

//css
import styled from "styled-components"

//img
import IconMore from "../../../../img/ico_arrow_more.png"

//page
import serverController from '../../../../server/serverController';

export default function InquiryView({qna_id}) {
    const [qnaContent,setQnaContent] = useState([]);
    const url_qna_id = qna_id;

    useEffect(()=>{
        serverController.connectFetchController(`qna/content`,"GET",null,function(res){
        if(res.result == 1) {
            setQnaContent(res.list)
        }});
    },[]);
    
    return (
        <Container>
            <ShopLine></ShopLine>
            <InquiryListWrap>
                {
                    qnaContent.map((value)=>{
                        console.log(qnaContent);
                        console.log("!!!!!!!!!!");
                        if(url_qna_id == value.qna_id){
                            console.log(value.prd_title);
                            return (
                                <InquiryWrap>
                                    <InquiryTitleWrap>
                                        <InquiryTitle>{value.prd_title}</InquiryTitle>
                                        <Inquirydate>{value.qna_date}</Inquirydate>
                                    </InquiryTitleWrap>
                                    <InquiryTextWrap>
                                        <InquiryQTitle>Q</InquiryQTitle>
                                        <InquiryQText>{value.qna_name}</InquiryQText>
                                    </InquiryTextWrap>
                                    <InquiryTextWrap>
                                        <InquiryQDetail>{value.qna_content}</InquiryQDetail>
                                    </InquiryTextWrap>
                                    <InquiryLine></InquiryLine>
                                    <InquiryTextWrap>
                                        <InquiryATitle>A</InquiryATitle>
                                        <InquiryAText>{value.qna_answer}</InquiryAText>
                                    </InquiryTextWrap>
                                </InquiryWrap>
                            )
                        }
                    })
                }
            </InquiryListWrap>

        </Container>
  );
}

const Container = styled.div`
    width: 100%; height: 100%;
`
const ShopLine = styled.div`
    width: 100%;
    height: calc(100vw*(14/414));
    background: #f7f7f7;
`
const InquiryListWrap = styled.div`
    width: 100%; min-height: calc(100vh - calc(100vw*(175/414)));
    background: #fff;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0,0,0,0.16);
    padding-bottom: calc(100vw*(30/414));
`
const InquiryWrap = styled.div`
    width: 100%;
    padding: calc(100vw*(48/414)) calc(100vw*(35/414)) 0;
    margin-bottom: calc(100vw*(15/414));
`
const InquiryTitleWrap = styled.div`
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: calc(100vw*(18/414));
`
const InquiryTitle = styled.p`
    font-size: calc(100vw*(12/414));
    color: #969696;
`
const Inquirydate = styled.span`
    font-size: calc(100vw*(12/414));
    color: #969696;
`
const InquiryTextWrap = styled.div`
    display: flex;
`
const InquiryQTitle = styled.p`
    font-size: calc(100vw*(13/414));
    font-weight: 900;
    margin-right: calc(100vw*(8/414));
`
const InquiryQText = styled.p`
    font-size: calc(100vw*(13/414));
    font-weight: 900;
`
const InquiryQDetail = styled.p`
    font-size: calc(100vw*(13/414));
    line-height: 1.4;
    margin-top: calc(100vw*(8/414));
`
const InquiryATitle = styled.p`
    font-size: calc(100vw*(13/414));
    font-weight: 900;
    margin-right: calc(100vw*(8/414));
`
const InquiryAText = styled.p`
    font-size: calc(100vw*(13/414));
    line-height: 1.4;
`
const InquiryLine = styled.div`
    width: 100%; height: calc(100vw*(1/414));
    background: #f4f4f4;
    margin: calc(100vw*(15/414)) 0;
`