//react
import {Link , useHistory} from "react-router-dom";
import {useState, useEffect} from 'react';

//css
import styled from "styled-components"

//img
import productImg from "../../../../img/product_img.png"
import IconstarOn from "../../../../img/ico_star_on.svg"
import IconstarOff from "../../../../img/ico_star_off.svg"
import IconFile from "../../../../img/ico_file.png"

//page
import serverController from '../../../../server/serverController'

const ReviewItem = [
    {
        prd_id: 1,
        src: productImg,
        prd_title:"[농심]농심신라면",
        prd_detail:"멀티 5입",
        star: 3,
    }
]

export default function ReviewWrite() {
    const [next,setNext] = useState(false);
    const [text,setText] = useState("");
    const [star,setStar] = useState(0);
    const [imagesList,setImagesList] = useState([]);

    const TextChange = (e) => { setText(e.target.value); }
    const TextChangedate = () =>{ return text.length > 2; };
    const NoStars = () => { return star > 1 };

    const history = useHistory();

    useEffect(()=>{
        if(TextChangedate() && NoStars())
            setNext(true);
        else
            setNext(false);
    },[next,text,star])

    const getStartComponent = (index) =>{
        return  <Stars onClick={()=>{setStar(index+1)}}>
                    <img src={index < star ? IconstarOn : IconstarOff} alt=""/>
                </Stars>
    }

    const getStarImageList = () =>{
        let imageList = [];
        for(var i =1;i < 6;i++){
            imageList.push(getStartComponent(i));
        }
        return imageList;
    }
    
    const imagesChange = (e) =>{
        if(e.target.files.length > 7){
            alert("사진은 최대 7장까지 첨부하실 수 있습니다.")
            return;
        }
        
        let newImageList = [];
        for (const [key, value] of Object.entries(e.target.files)) {
            newImageList.push(value);
        }
        setImagesList(newImageList);
    }
    
    const reviewClick = () =>{
        // console.log(Object.values(imagesList));
        // var list = Object.values(imagesList).map(item => item) 
        console.log(imagesList);
        
        if(next == true){
            const prd_id = ReviewItem.map(ReviewItem =>ReviewItem.prd_id);
            const formData = new FormData();
            formData.append('rev_score',star);
            formData.append('rev_content',text);
            formData.append('rev_image', imagesList);
            formData.append('mem_id',1);
            formData.append('mem_username','test');
            formData.append('prd_id',prd_id);
            serverController.connectFetchController('review/insert',"POST",formData,function(res){
            if(res.result == 1) {
                console.log(res);
                return history.push("/reviewComplete");
            }});
        }
    }
    
  return (
        <Container>
            <ReviewDetailWrap>
                <ReviewDetailInner>
                    {
                        ReviewItem.map((value) =>{
                            return (
                            <ReviewInnerWrap>
                                <ReviewProductInfo>
                                    <ReviewProductImg src={value.src} alt="review products image"/>
                                    <ReviewProductText>[농심]농심신라면, 멀티 5입</ReviewProductText>
                                </ReviewProductInfo>
                                <StarsWrap>
                                    {
                                        getStarImageList()
                                    }
                                    <StarsText>별점을 선택해주세요</StarsText>
                                </StarsWrap>
                            </ReviewInnerWrap>
                            )
                        })
                    }
                    <ReviewFileWrap>
                        <ReviewFileBtnWrap>
                            <ReviewFileBtn type="file" id="filepic" multiple={true} onChange={imagesChange}/>
                            <ReviewFileBtnLabel for="filepic">+ 사진첨부</ReviewFileBtnLabel>
                        </ReviewFileBtnWrap>
                        {
                            imagesList && imagesList.length > 0? 
                            imagesList.map((value,index)=>{
                                var image = URL.createObjectURL(value);
                                return  <ReviewFileImgWrap><ReviewFileImg src={image}/></ReviewFileImgWrap>
                            })
                            :
                            null
                        }
                        {
                            imagesList.length < 3 ? 
                            [1,2,3].map((value,index)=>{
                                if(imagesList.length - index < 1)
                                return <ReviewFileImgWrap></ReviewFileImgWrap>
                            })
                            :
                            null
                        }
                    </ReviewFileWrap>
                    <FileGuideWrap>
                        <FileGuideText>사진은 최대 7장까지 첨부하실 수 있습니다.</FileGuideText>
                    </FileGuideWrap>
                    <ReviewTextWrap>
                        <ReviewTextarea placeholder="이 상품의 상품평을 남겨주세요." onChange={TextChange}></ReviewTextarea>
                    </ReviewTextWrap>
                </ReviewDetailInner>
                <ReviewDetailBtnWrap>
                    <ReviewDetailBtn DetailBtnOn={next} onClick={reviewClick}>리뷰 등록하기</ReviewDetailBtn>
                </ReviewDetailBtnWrap>
            </ReviewDetailWrap>
        </Container>
  );
}

//css
const Container = styled.div` 
    width: 100%; 
`
const ReviewDetailWrap = styled.div` 
    position: relative;
    width: 100%;
    background: #fff;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    z-index: 10;
    margin-top: calc(100vw*(12/414));
    padding-bottom: calc(100vw*(115/414));
`
const ReviewDetailInner = styled.div`
    width: 100%; min-height: calc(100vh - calc(100vw*(265/414)));
`
const ReviewInnerWrap = styled.div`
    padding: calc(100vw*(8/414)) calc(100vw*(24/414)) 0;
`
const ReviewProductInfo = styled.div`
    display: flex;
    align-items: center;
    padding: calc(100vw*(14/414)) calc(100vw*(14/414));
    border-bottom: calc(100vw*(1/414)) solid #eaeaea;
`
const ReviewProductImg = styled.img`
    width: calc(100vw*(52/414));
`
const ReviewProductText = styled.p`
    font-size: calc(100vw*(16/414));
    margin-left: calc(100vw*(5/414));
`
const StarsWrap = styled.div`
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: calc(100vw*(20/414)) 0 calc(100vw*(30/414));
`
const Stars = styled.span`
    display: block;
    width: calc(100vw*(30/414));
    margin: 0 calc(100vw*(2.5/414));
`
const StarsText = styled.p`
    width: 100%;
    font-size: calc(100vw*(9/414));
    font-weight: bold;
    text-align: center;
    margin-top: calc(100vw*(6/414));
`
const ReviewFileWrap = styled.div`
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding: 0 calc(100vw*(26/414));
`
const ReviewFileBtnWrap = styled.div`
    margin: 0 calc(100vw*(4/414));
`
const ReviewFileBtn = styled.input`
    display: none;
`
const ReviewFileBtnLabel = styled.label`
    display: block;
    width: calc(100vw*(82/414)); height: calc(100vw*(82/414));
    border-radius: calc(100vw*(3/414));
    border: calc(100vw*(1/414)) solid #979797;
    background: url(${IconFile}) no-repeat top calc(100vw*(18/414)) center/calc(100vw*(26/414));
    padding-top: calc(100vw*(52/414));
    font-size: calc(100vw*(9/414));
    font-weight: bold;
    text-align: center;
`
const ReviewFileImgWrap = styled.div`
    width: calc(100vw*(82/414)); height: calc(100vw*(82/414));
    border-radius: calc(100vw*(3/414));
    border: calc(100vw*(1/414)) solid #e6e6e6;
    margin: calc(100vw*(4/414));
    background: #fbfbfb;
`
const ReviewFileImg = styled.img`
    width: 100%; height: 100%;
    object-fit: cover;
`
const FileGuideWrap = styled.div`
    width: 100%;
`
const FileGuideText = styled.p`
    font-size: calc(100vw*(9/414));
    font-weight: bold;
    text-align: center;
    margin-top: calc(100vw*(3/414));
`
const ReviewTextWrap = styled.div`
    width: 100%;
    padding: calc(100vw*(15/414)) calc(100vw*(30/414));
`
const ReviewTextarea = styled.textarea`
    width: 100%; height: calc(100vw*(175/414));
    font-size: calc(100vw*(14/414));
    font-weight: bold;
    border-radius: calc(100vw*(3/414));
    border: calc(100vw*(1/414)) solid #979797;
    padding: calc(100vw*(10/414)) calc(100vw*(16/414));
`
const ReviewDetailBtnWrap = styled.div`
    width: 100%;
    text-align: center;
    padding: 0 calc(100vw*(30/414));
`
const ReviewDetailBtn = styled.button`
    width: 100%;
    font-size: calc(100vw*(18/414));
    font-weight: 900;
    border-radius: calc(100vw*(6/414));
    border: calc(100vw*(3/414)) solid #a5a5a5;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    padding: calc(100vw*(18/414)) 0;
    background: #b4b4b4;
    color: #fff;
    ${({DetailBtnOn})=>{
        return DetailBtnOn ? 
        `
            background: #ED2939;
            border-color: #BC343F;
        `
        :
        ``
    }}
`