//react
import React ,{useState,useEffect} from 'react';
import {Link} from "react-router-dom";

//css
import styled from "styled-components"

//img
import IcoFilter from "../../img/ico_filter.png"

//page
import serverController from '../../server/serverController';
import Product from "../common/product/Product";

//숫자 3자리수 콤마 코드
function numberWithCommas(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

export default function CategoryProduct({openFilterModal, cat_id}) {

    const [bscName,setbscName] = useState([]);
    const [count,setCount] = useState(0);
    const [itemList,setItemList] = useState([]);


    useEffect(()=>{

        serverController.connectFetchController(`categories/child?cat_id=${cat_id}`,"GET",null,function(res){
        if(res.result == 1) {
            setbscName(res.items)
        }});

        serverController.connectFetchController(`products/info?prd_type=1`,"GET",null,function(res){
        if(res.result == 1) {
            setItemList(res.items)
            setCount(res.items.length)
        }});
    },[]);


  return (
        <Container>
            <CategoryProductWrap>
                <CategoryTitleWrap>
                    <TitleWrap>
                        <TitleText>{bscName[0] ? bscName[0].cat_name : null}</TitleText>
                    </TitleWrap>
                    <FilterListWrap>
                        <FilterList>
                        {
                            bscName.map((value)=>{
                                return (
                                    <FilterListText>{value.bsc_name}</FilterListText>
                                )
                            })
                        }
                        </FilterList>
                    </FilterListWrap>
                </CategoryTitleWrap>
                    
                <CategoryFilterWrap>
                    <FilterText><FilterNumberText>{numberWithCommas(count)}</FilterNumberText> 개의 상품</FilterText>
                    <IconFilterText onClick={()=>{openFilterModal(true)}}><IconFilter src={IcoFilter} alt="Filter button"/> 필터</IconFilterText>
                </CategoryFilterWrap>
                <Line></Line>

                {/* Product */}
                    <ProductBox>
                        {
                            itemList.map((value,index)=>{
                                return (
                                    <Link to={`/shop/product/${value.prd_type}/${value.prd_id}`}>
                                        <Product rank={false} value={value}/>
                                    </Link>
                                )
                            })
                        }
                    </ProductBox>

            </CategoryProductWrap>
        </Container>
  );
}

/* main prd_title */
const Container = styled.div` 
    width: 100%;
`
const CategoryProductWrap = styled.div` 
    width: 100%;
    background: #fff;
    margin-top: calc(100vw*(4/414));
    padding-top: calc(100vw*(10/414));
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
`
const CategoryTitleWrap = styled.div` 
    width: 100%;
`
const TitleWrap = styled.div` 
    display: flex;
    justify-content: center;
    width: 100%;
    margin-bottom: calc(100vw*(30/414));
`
const TitleText = styled.h2`
    display: inline-block;
    position: relative;
    width: auto;
    font-size: calc(100vw*(20/414));
    font-weight: bold;
    padding: calc(100vw*(12/414)) calc(100vw*(25/414));
    margin: 0 auto;
    &::after {
        content: "";
        position: absolute; left: 0; bottom: 0;
        display: block;
        width: 100%; height: calc(100vw*(3/414));
        background: #ed2939;
    }
`
const FilterListWrap = styled.div` 
    width:100%;
    margin-bottom: calc(100vw*(30/414));
`
const FilterList = styled.ul` 
    width:100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
`
const FilterListText = styled.li`
    max-width: calc(100vw*(120/414));
    font-size: calc(100vw*(14/414));
    font-weight: bold;
    color: #333;
    margin: calc(100vw*(12/414)) 0;
    padding: 0 calc(100vw*(12/414));
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    white-space: normal;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical; 
`
const CategoryFilterWrap = styled.div` 
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    width:100%;
    padding-left: calc(100vw*(22/414));
    margin-bottom: calc(100vw*(5/414));
`
const FilterText = styled.p`
    font-size: calc(100vw*(14/414));
    line-height: 1.2;
    font-weight: bold;
`
const FilterNumberText = styled.span`
    font-size: calc(100vw*(15/414));
    font-weight: 900;
`
const IconFilterText = styled.p`
    display: flex;
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    margin-right: calc(100vw*(22/414));
    padding: calc(100vw*(7/414)) calc(100vw*(16/414));
    background: #f7f7f7;
    border-radius: calc(100vw*(8/414));
    border: calc(100vw*(1/414)) solid #979797;
    color: #707070;
`
const IconFilter = styled.img`
    width: calc(100vw*(11/414));
    margin-right: calc(100vw*(4/414));
`
const Line = styled.div`
    width: calc(100vw*(392/414)); height: calc(100vw*(1/414));
    background: #d1d1d1;
    margin: 0 auto;
`

/* Product */
const ProductBox = styled.div`
    width: 100%;
    padding-bottom: calc(100vw*(15/414));
`
